import {
  select as d3select
} from 'd3'

import { isSingleCharLabel } from '../../utils/data'

export function needsLabel (field, minLength) {
  minLength = minLength || 0
  var needsLabel = false
  var displayAs = field['display_as']
  var label = field.label || field.caption
  var labelLeft = field['label-left'] || field['unit_before'] || field['unitBefore']
  var labelRight = field['label-right'] || field['unit_after'] || field['unitAfter']

  // use this otherwise we get nullnullnull
  var s = val => (val == null) ? '' : val
  var totalLabel = s(labelLeft) + s(labelRight) + s(label)

  if (labelRight && labelRight.length >= 1) needsLabel = true
  if (displayAs && displayAs !== '%' && displayAs === labelRight) needsLabel = false
  if (labelLeft && labelLeft.length >= 1) needsLabel = true
  if (label) needsLabel = true
  if (totalLabel.length <= minLength) needsLabel = false

  return needsLabel
}

// 3px for a space on axis
const SPACE_WIDTH = 3

// no longer writing text down the side, we add a final label at the end
// if it's longer than a single character
// called after axis creation to allow background on top of chart
export default function placeVerticalAxisLabel (env, isHorizontal) {
  var field = isHorizontal ? env.x.field() : env.y.field()
  var textHeight = 15
  var paddingV = 9.5
  var boxPad = 4
  var displayAs = field['display_as']
  var labelText = ''
  var label = field.label || field.caption || ''
  var labelLeft = field['label-left'] || field['unit_before'] || ''
  var labelRight = field['label-right'] || field['unit_after'] || ''
  var labelPre = ''
  var labelPost = ''
  var anchorPos = isHorizontal ? 'middle' : 'end'
  var ticks

  env.layout.g().select('g.axis-label-box').remove()

  // when horizontal columns with labels just return
  if (env.options.labelCaps) return

  var axisGroup = env.layout.yAxis()
  var isSingleChar = isSingleCharLabel(field)
  if (isHorizontal) {
    axisGroup = env.layout.xAxis()
  }

  ticks = axisGroup.selectAll('g.tick')
  var lastTick = d3select(ticks.nodes()[ticks.nodes().length - 1])

  // now read existing text from the lastTick, read pre/post as well
  if (!lastTick.size()) return
  if (lastTick.select('text .val').size()) {
    labelText = lastTick.select('text .val').text()
    if (lastTick.select('text .pre').size()) {
      labelPre = lastTick.select('text .pre').text()
    }
    if (lastTick.select('text .post').size()) {
      labelPost = lastTick.select('text .post').text()
    }
  } else {
    labelText = lastTick.select('text').text()
  }

  // remove hide-text on lastTick to ensure we get a width/height
  lastTick.classed('hide-text', false)

  if (displayAs && displayAs !== '%' && displayAs === labelRight) {
    labelRight = ''
  }

  // make sure spaces are kept in place
  labelLeft = labelLeft.replace(/ /g, '&nbsp;')
  labelRight = labelRight.replace(/ /g, '&nbsp;')

  // weird thing about terminal font - it has a lot of spacing
  const postOffsetSpace = isWideText(env) ? 1.5 : 0

  // note post is before pre and then we move it over to the right of val
  const labelPreText = labelLeft + labelPre
  const labelRightText = label ? `&nbsp;${label}` : labelRight
  const labelHtml = `
    <tspan class="pre">${labelPreText}</tspan>
    <tspan class="val" dx="${SPACE_WIDTH * -1}">${labelText}</tspan>
    <tspan class="post" dx="${(SPACE_WIDTH * -1) - postOffsetSpace}">${labelPost}${labelRightText}</tspan>
  `

  // singleChar labels don't get a box, but we set the text directly
  if (isSingleChar) {
    const textEl = lastTick.select('text')
    textEl.html(labelHtml)

    // calculate offset to rightAlign
    const currentX = Number(textEl.attr('x'))
    const widthSpaceNeeded = labelPreText.length && !(labelPost + labelRight).length ? 0 : SPACE_WIDTH
    let postWidth = 0
    if (textEl.select('.post').node()) {
      postWidth = textEl.select('.post').node().getComputedTextLength() + widthSpaceNeeded
    }
    textEl.attr('x', currentX + postWidth)
  } else if (isHorizontal) {
    lastTick.select('text')
      .html(labelHtml)
  } else {
    const drawBounds = env.layout.draw().node().getBoundingClientRect()
    const extents = env.layout.extents().node().getBoundingClientRect()
    const axisBounds = axisGroup.node().getBoundingClientRect()
    const lastTickBounds = lastTick.node().getBoundingClientRect()
    const innerPadding = env.layout.yAxisInnerPadding()
    const innerPaddingAlt = env.layout.xAxisInnerPadding()
    const yDiff = extents.top - drawBounds.top
    let xPos = axisBounds.right - drawBounds.left - innerPadding
    let yPos = lastTickBounds.bottom - drawBounds.top - innerPaddingAlt - paddingV - yDiff

    const labelBox = env.layout.draw().selectAppend('g.axis-label-box')

    if (!labelPreText) xPos += SPACE_WIDTH

    labelBox
      .translate([xPos, yPos])

    const textItem = labelBox
      .selectAppend('text')
      .attr('text-anchor', anchorPos)
      .html(labelHtml)

    const textWidth = textItem.node().getBoundingClientRect().width
    const xOffset = isHorizontal ? (textWidth / 2) : 0

    // we have to go around d3 for insertBefore
    const rect = document.createElementNS('http://www.w3.org/2000/svg', 'rect')
    rect.setAttribute('x', 0 - textWidth - boxPad - xOffset)
    rect.setAttribute('y', 0 - textHeight - (boxPad / 2))
    rect.setAttribute('width', textWidth + (boxPad))
    rect.setAttribute('height', textHeight + boxPad)

    labelBox.node().insertBefore(rect, textItem.node())
  }
}

export function placeHorizontalAxisLabel (env) {
  placeVerticalAxisLabel(env, true)
}

function isWideText (env) {
  return ['terminal', 'coth'].indexOf(env.data.theme) !== -1
}
