import { select } from '../d3-custom'
import { range } from 'd3-array'
import { stack, stackOffsetDiverging } from 'd3-shape'
import { getFieldIndexValueParser } from '../utils/data'

import * as layout from '../utils/layout'

export default function simpleColumnStacked (options) {
  options = options || {}
  let xScale = options.xScale
  let yScale = options.yScale

  function simpleColumnStacked (selection) {
    selection.each(function (chartData) {
      const el = select(this)
        .classed('simple-column-stacked', true)

      // TODO give this more thought and make an accessor 
      const categoricalData = getFieldIndexValueParser(chartData, true, true)
      let series = chartData.fields.length > 1 ? chartData.fields.slice(1) : chartData.fields

      var dataAsObject = series[0].data.map(function (d, i) {
        var rowObj = {}
        chartData.data[i].forEach(function (val, index) {
          rowObj[index - 1] = val
        })
        return rowObj
      })

      var stackedData = stack()
        .keys(range(0, series.length))
        .offset(stackOffsetDiverging)(dataAsObject)

      series = el.selectAll('.series')
        .data(stackedData)

      series.exit().remove()

      series = series.enter()
        .append('g')
        .attr('class', function (d, i) {
          return 'series f series-' + i
        })
        .merge(series)

      var columns = series.selectAll('.column')
        .data(function (d) {
          return d
        })

      columns.exit().remove()

      // HORIZONTAL layout equals vertical bars
      if (options.orientation === layout.HORIZONTAL) {
        columns.enter()
          .append('rect')
          .attr('class', 'column')
          .merge(columns)
          .attr('x', function (d, i) {
            return xScale(categoricalData(i))
          })
          .attr('y', function (d, i) {
            return yScale(d[1])
          })
          .attr('width', Math.max(1, xScale.bandwidth()))
          .attr('height', function (d) {
            return yScale(d[0]) - yScale(d[1])
          })
      } else {
        // these are horizontal bars
        columns.enter()
          .append('rect')
          .attr('class', 'column')
          .merge(columns)
          .attr('y', function (d, i) {
            return yScale(categoricalData(i))
          })
          .attr('x', function (d, i) {
            return xScale(d[0])
          })
          .attr('height', yScale.bandwidth())
          .attr('width', function (d) {
            return xScale(d[1]) - xScale(d[0])
          })
      }
    })
  }

  simpleColumnStacked.xScale = function (_) {
    return arguments.length ? (xScale = _, simpleColumnStacked) : xScale // eslint-disable-line
  }

  simpleColumnStacked.yScale = function (_) {
    return arguments.length ? (yScale = _.copy(), simpleColumnStacked) : yScale // eslint-disable-line
  }

  return simpleColumnStacked
}
