import { select } from '../d3-custom'
import {
  bindMessageHandlers,
  bindResizeHandlers,
  screenshotUpscale,
  createResizeCallback
} from '../utils/iframe'
import { defaultScaffold } from '../utils/layout'
import { setupColors } from '../utils/color/index'
import { makeOptions } from '../utils/options'
import { makeData } from '../utils/data'
import { themeOptions, themeWrapper } from '../utils/theme'

/**
  We need to support an interface that just takes in
  the element and the data (which defines theme/type)

  In order to do this the we return a factory given a
  mapping of "type" -> chart constructor, and a theme
  remapper for translating themes like "v2business" to "business"

*/
export default function chartFactory (el, type, data, options) {
  options = makeOptions(options, data)
  makeData(data)

  // create a forceResize handler to pass into client so that it
  // can call an async resize
  let onResize = () => null
  options.forceResize = () => onResize(el, options)

  if (!type) {
    throw new Error('Chart Type not recognized')
  }

  if (!data.data || !data.data.length) {
    throw new Error('No Data supplied')
  }

  // allow type options to define scaffold
  if (type.makeOptions) {
    options = type.makeOptions(options)
  }

  let themeName = data.theme
  const typeName = data.chartType || options.typeClass
  if (options.themeMap && options.themeMap.hasOwnProperty(data.theme)) {
    themeName = options.themeMap[data.theme]
  }
  options = themeOptions(data, themeName, options)

  const themeClass = 'toaster-theme-' + themeName
  const typeClass = 'toaster-type-' + typeName

  const chart = select(el)
    .classed('toaster-chart', true)
    .classed(themeClass, true)
    .classed(typeClass, true)
    .datum(data)

  // clients can disable default scaffold or provide a custom
  // scaffold function for building legend/titles
  if (options.customScaffold) {
    chart.call(options.customScaffold(options))
  } else if (!options.disableScaffold) {
    chart.call(defaultScaffold(options))
  }

  const render = function () {
    const layerInterface = type(options)

    // before calling render we apply an upscale for screenshots etc.
    if (options.screenshotUpscale) {
      screenshotUpscale(el, options)
    }

    chart.call(themeWrapper(themeName, options))
    chart.call(layerInterface)
    setupColors(el, data, options)

    if (layerInterface && layerInterface.postRender) layerInterface.postRender()
    return layerInterface
  }
  const renderer = render() // call right away

  onResize = createResizeCallback()
  bindMessageHandlers(el, onResize, options)
  bindResizeHandlers(el, render, onResize, data, options)

  setTimeout(() => onResize(el, options), 50)

  return {
    renderer,
    render,
    onResize
  }
}
