const ANNOTATION_RADIUS = 4
const ANNOTATION_WIDTH = 180
const ANNOTATION_WIDTH_MOBILE = 120
const PAD_ANNOTATION_LINE = 8

const TYPE_LINE = 'line'
// const TYPE_TEXT_MARKER = 'text-plus-marker'
const TYPE_TEXT_ONLY = 'text-only'
const TYPE_SHORT_LINE = 'short-line'
const MOBILE_HIDE = 'hide-on-mobile'

