import {select} from '../d3-custom'

export var LINE_SEPARATOR = '///'

export function wrapText (width, options) {
  var defaults = {
    lineHeight: 16,
    margin: 10
  }
  var newLineToken = '<N>'

  options = Object.assign(defaults, options)

  return function (d) {
    var text = select(this)
    // make sure new line gets into the array
    var textVal = text.text().replace(/\n|<br>/g, ' ' + newLineToken + ' ')
    var words = textVal.split(/\s+/).reverse() // /\s+/
    var word
    var line = []
    var lineNumber = 0
    var lineHeight = options.lineHeight // ems
    var y = text.attr('y')
    var x = text.attr('x')
    var dy = options.margin
    var tspan = text.text(null).append('tspan').attr('x', x).attr('y', y).attr('dy', dy + 'px')

    var pushCount = 0
    while (word = words.pop()) { // eslint-disable-line
      line.push(word)
      pushCount++
      tspan.text(line.join(' ').trim())
      if (pushCount > 1 &&
          (tspan.node().getComputedTextLength() > width ||
          word === newLineToken)) {
        if (word === newLineToken) word = ''
        line.pop()
        tspan.text(line.join(' ').trim())
        line = [word]
        tspan = text.append('tspan').attr('x', x).attr('y', y).attr('dy', ++lineNumber * lineHeight + dy + 'px').text(word)
      }
    }
  }
}

export function toUnicode (str, separator) {
  return toUnicodeNumber(str, '\\')
}

export function toUnicodeNumber (str, separator) {
  separator = separator || ''
  return str.split('').map(function (value, index, array) {
    var temp = value.charCodeAt(0).toString(16).toUpperCase()
    if (temp.length > 2) {
      return '\\u' + temp
    }
    return temp
  }).join(separator)
}

// Place Vertically aligned text at a spot either left or right
// aligned and vertically centered to the given coords
// textObject = {
//  position: [x,y], orientation: 'right', line1: 'asdf', line2: 'asdf'
// }
export function placeMultiLineText (container, textObject, env) {
  var tg = container
    .append('g')
    .classed(textObject.orientation, true)
    .classed('text-group', true)

  if (textObject.orientation === 'right') {
    textObject.position[0] += env.options.labelLineOffset
  } else {
    textObject.position[0] -= env.options.labelLineOffset
  }

  tg.append('text')
    .classed('line1', true)
    .attr('dy', 0)
    .attr('y', 0)
    .text(textObject.line1)

  if (textObject.line2) {
    var line2 = tg.append('text')
      .classed('line2', true)
      .attr('dy', 0)
      .attr('y', env.options.labelLine2Height)
      .text(textObject.line2)

    line2.each(wrapText(
      env.options.labelTextWrapWidth,
      {margin: 0}
    ))
    line2.selectAll('tspan').attr('x', 0)
  }

  // offset to vertically center
  tg.translate(textObject.position)
}

// Given a text value of line1///line2///line3
// separate into multiple tspan lines
// support an option for horizontal labels with no wrapping
export function renderMultiLineText (options) {
  var defaults = {
    margin: 16,
    lineHeight: 18,
    isHorizontalText: false
  }
  options = Object.assign(defaults, options)

  return function (d) {
    var text = select(this)
    var textVals = text.text().split(LINE_SEPARATOR)
    var y = text.attr('y')
    var dy = options.margin

    if (options.isHorizontalText) {
      text.text(textVals.join(' '))
    } else {
      text.text(null)
      textVals.forEach((val, i) => {
        text.append('tspan')
          .attr('x', 0)
          .attr('y', y)
          .attr('dy', dy + 'px')
          .classed('line' + (i + 1), true)
          .text(val)

        dy += options.lineHeight
      })
    }
  }
}
