import {
  select as d3select,
  timeDay as d3timeDay
} from 'd3'

import {default as applyDateAxis} from './dateAxis'
import {dateToUTCString} from '../../utils/data'
import {
  hideDuplicates,
  hideOverlappingTicks,
  applyLabelDensity,
  hideLine2Duplicates,
  filterYearFormatter
} from './utils'

/**
  Each type of axis formatting will have it's own builder - defining the
  optimal number of ticks, applying to selection and then a postRender
  function to separate multiple lines and clean things up.
*/
export default function monthPlusAxis (selection, axis, env) {
  var formatters = filterYearFormatter([
    v => v.format('MMM D'),
    v => v.format('YYYY')
  ], env)
  var ticks
  var options = {
    noEndCap: false,
    endCapPriority: true
  }

  axis.model()
    .ticks(d3timeDay)
    .tickFormat(function (d, i) {
      return dateToUTCString(d, axis.field())
    })

  selection.call(axis.model())
  // selection.call(axis.model().ticks(monthOffset(15)))

  ticks = applyDateAxis(selection, axis, formatters, env, options)

  // now the tricky part hide sequential years
  // also hide anything that isn't 1st or 15th of the month

  var isFirstOr15th = function (text) {
    var tokens = text.split(' ')
    return ['1', '15'].indexOf(tokens[tokens.length - 1]) !== -1
  }

  ticks.initial.each(function () {
    var tick = d3select(this)
    if (tick.classed('hide-tick')) return

    if (!isFirstOr15th(tick.select('.line1').text())) {
      tick.classed('hide-tick', true)
    }
  })

  hideDuplicates(selection, axis)
  hideOverlappingTicks(selection, axis)
  applyLabelDensity(selection, axis)
  hideLine2Duplicates(selection, axis)

  return selection
}
