import { select } from '../d3-custom'
import { area as d3area } from 'd3-shape'
import { getSeries, isDefined, sortedDomain, getFieldIndexValueParser } from '../utils/data'

export default function simpleArea (options) {
  options = options || {}

  function simpleArea (selection, env) {
    selection.each(function (chartData, i) {
      const el = select(this)
        .classed('simple-area', true)

      const series = getSeries(chartData).slice(0) // clone
      const xData = getFieldIndexValueParser(chartData)
      const xScale = env.x.scale
      const yScale = v => {
        return Math.min(env.y.scale(v), Math.floor(env.y.rangeLength()))
      }

      // determine baseline by checking if 0 is within the domain
      // or at one of the edges
      let baseline = env.y.scale.domain()[0]
      const domain = sortedDomain(env.y.scale.domain())
      if (domain[0] === 0 || domain[1] === 0 ||
        (domain[0] < 0 && domain[1] > 0)) {
        baseline = 0
      }

      const areaGroup = el.selectAppend('g.area-group')
      const areas = areaGroup.selectAll('.line-area').data(series)

      if (options.xOffset) {
        areaGroup.translate([options.xOffset])
      }

      const area = d3area()
        .x((d, i) => xScale(xData(i)))
        .y1(yScale(baseline))
        .y0(d => yScale(d))
        .defined(isDefined)

      areas.exit().remove()

      areas.enter()
        .append('path')
        .attr('class', (d, i) => `line-area f series-${i}`)
        .merge(areas)
        .attr('d', (d) => area(d.data))
    })

    return selection
  }

  return simpleArea
}
