import { select } from '../d3-custom'
import {
  line as d3line,
  curveBasis,
  curveStep,
  curveStepAfter,
  curveStepBefore
} from 'd3-shape'

import { getSeries, getFieldIndexValueParser, isDefined } from '../utils/data'

const curveTypes = {
  step: curveStep,
  stepAfter: curveStepAfter,
  stepBefore: curveStepBefore,
  smooth: curveBasis
}

export default function simpleLine (options) {
  var xScale = options.xScale
  var yScale = options.yScale
  var seriesOffset = options.seriesOffset || 0

  function simpleLine (selection) {
    selection.each(function (chartData, i) {
      const el = select(this)
        .classed('simple-line', true)

      // bar+line will pass in options.forceCategorical
      const xData = getFieldIndexValueParser(chartData, true, options.forceCategorical)
      const series = options.seriesData || getSeries(chartData)
      const lineGroup = el.selectAppend('g.line-group')
      const lines = lineGroup.selectAll('.line').data(series)

      if (options.xOffset) {
        lineGroup.translate([options.xOffset])
      }

      const line = d3line()
        .x((d, i) => xScale(xData(i)))
        .y((d) => yScale(d))
        .defined(isDefined)

      if (options.curve && curveTypes[options.curve]) {
        line.curve(curveTypes[options.curve])
      }

      lines.exit().remove()

      lines.enter()
        .append('path')
        .attr('class', function (d, i) {
          return 'line s series-' + (i + seriesOffset)
        })
        .merge(lines)
        .attr('d', function (d) {
          return line(d.data)
        })
    })
  }

  simpleLine.xScale = function (_) {
    return arguments.length ? (xScale = _, simpleLine) : xScale  // eslint-disable-line
  }

  simpleLine.yScale = function (_) {
    return arguments.length ? (yScale = _, simpleLine) : yScale // eslint-disable-line
  }

  return simpleLine
}
