import { select } from '../d3-custom'
import { default as simpleArea } from '../layers/simpleArea'
import { default as simpleAreaStacked } from '../layers/simpleAreaStacked'
import { default as simpleLine } from '../layers/simpleLine'
import { default as dashedLine } from '../layers/dashedLine'
import { default as valueTag } from '../layers/valueTag'
import { default as makeEnv } from './env'
import { lineAnnotations } from './annotations'
import { bottomLabel, HORIZONTAL, shouldDisplayGrid, shouldDisplayDomain } from '../utils/layout'
import { makeLayerPrototype } from '../utils/layers'
import { lineTooltip } from './tooltip'

import { layoutManager, dateAxis, bandAxis, linearAxis, grid, format } from '../axis'
import {
  buildTaxonomy,
  validator,
  isSingleSeries,
  isQuadSeries,
  isTimeSeries,
  isMultipleValues,
  getInvalidSegments,
  isForceCategorical,
  FIELD_TYPE_NUMERIC
} from '../utils/data'
import debugEnv from '../utils/debug'
import {default as _merge} from 'lodash-es/merge'

export default function line (options) {
  const defaults = {
    // distance tooltip box is away from point
    tooltipOffsetX: 7,
    // distance tooltip box is away from point
    tooltipOffsetY: 7,
    // tooltip box line for height estimation
    tooltipLineHeight: 30,
    // tooltip max width
    tooltipMaxWidth: 160,
    // radius of tooltip anchor
    tooltipAnchorSize: 4,
    // radius of annotation anchor
    annotationAnchorSize: 4,
    // display value tag at end of line
    displayValueTags: false,

    // curve type stepped etc.
    curveType: null,

    // configuration options for layout manager
    layoutManager: {},

    // configuration for the chart axes
    dateAxis: {},
    bandAxis: {
      outerPadding: 0.0
    },
    linearAxis: {
      // tickStepVertical: 80,
      // tickLabelAlternate: false
    }
  }
  options = _merge(defaults, options)

  // we need a way to store env, el for each selection and
  // expose that out to any calling function
  const selections = []

  function line (selection) {
    selection.each(function (chartData, i) {
      const el = select(this)
        .classed('toaster-chart-line', true)
        .classed('bar-colors', false) // clear this on chart type change
        .classed('quad-colors', isQuadSeries(chartData))

      // ensure we have a taxonomy specified on the data
      buildTaxonomy(chartData)
      const env = makeEnv(el, chartData, options)

      // render as area chart with options provided
      const isAreaChart = env.options.renderAreaChart
      const isAreaChartStacked = (isAreaChart && !isSingleSeries(chartData))

      // depending on the data we either render dateAxis or
      // bandAxis (for Quarters)
      const field = chartData.fields[0]
      const emptySegments = getInvalidSegments(chartData)
      let xOffset = 0
      let isBandAxis = (isForceCategorical(field) ||
        (field.taxonomy && !field.taxonomy.isContinuous))
      if (field.type === FIELD_TYPE_NUMERIC) isBandAxis = true

      const layoutConfig = {
        orientation: HORIZONTAL,
        yAxisInnerPadding: format.axisPadding(chartData)
      }
      Object.assign(layoutConfig, env.options.layoutManager)

      const linearAxisConfig = {
        isPrimaryAxis: false,
        domainFromZero: isAreaChart,
        stackedDomain: isAreaChartStacked
      }
      Object.assign(linearAxisConfig, env.options.linearAxis)
      layoutConfig.secondaryAxis = linearAxis(linearAxisConfig)

      if (isBandAxis) {
        const bandAxisConfig = { isPrimaryAxis: true }
        Object.assign(bandAxisConfig, env.options.bandAxis)

        layoutConfig.primaryAxis = bandAxis(bandAxisConfig)
      } else {
        const dateAxisConfig = { isPrimaryAxis: true }
        Object.assign(dateAxisConfig, env.options.dateAxis)

        layoutConfig.primaryAxis = dateAxis(dateAxisConfig)
      }

      const chartLayout = layoutManager(layoutConfig)

      const chart = el.selectAppend('div.chart')
      chart.selectAll('*').remove()
      chart.call(chartLayout, env)

      const line = chartLayout.draw().classed('line-container', true)
      const curveType = options.curveType || chartData.config.curve_type

      // grid is now essentially a render layer
      // secondaryAxis.gridlines(line.selectAppend('g.grid'), width)
      el.classed('no-grid', !shouldDisplayGrid(env))
        .classed('no-domain', !shouldDisplayDomain(env))
        .classed('no-axis', env.options.disableGrid)
      line.selectAppend('g.grid')
        .call(grid.gridlines({ length: chartLayout.outerWidth() }), env)

      if (isBandAxis) xOffset = env.x.scale.bandwidth() / 2

      if (isAreaChart) {
        el.classed('toaster-chart-area', true)
        el.classed('toaster-chart-area-stacked', isAreaChartStacked)
        el.classed('area-colors', true)
        if (isAreaChartStacked) {
          line.call(simpleAreaStacked({ xOffset: xOffset }), env)
        } else {
          line.call(simpleArea({ xOffset: xOffset }), env)
        }
      }

      if (!isAreaChart && !isAreaChartStacked) {
        line.call(simpleLine({
          xScale: env.x.scale,
          yScale: env.y.scale,
          drawArea: env.options.renderSingleSeriesAreaChart,
          xOffset: xOffset,
          curve: curveType
        }), env)

        // draw dashed line to fill in gaps
        if (emptySegments) {
          line.call(dashedLine({
            xScale: env.x.scale,
            yScale: env.y.scale,
            drawArea: env.options.renderSingleSeriesAreaChart,
            xOffset: xOffset,
            segments: emptySegments,
            curve: curveType
          }), env)
        }

        // TODO :: valueTags
        if (options.displayValueTags) {
          line.call(valueTag({
            env: env,
            xScale: env.x.scale,
            yScale: env.y.scale,
            drawArea: env.options.renderSingleSeriesAreaChart,
            xOffset: xOffset
          }), env)
        }
      }

      // after drawing we place the y axis label
      // on top of everything
      format.placeVerticalAxisLabel(env)

      lineAnnotations(line, chartData.annotations, env)

      // create bottom label if needed
      chart.call(bottomLabel(), env)

      // overflow layout call
      chartLayout.overflowScale()

      // build out the default tooltip renderer
      if (!env.options.disableTooltip) {
        lineTooltip({
          chart: chart,
          area: line,
          env: env
        })
      }

      // add line class to legend swatches
      if (!env.options.renderAreaChart) {
        el.selectAll('.simple-legend .swatch')
          .classed('line', true)
      }

      debugEnv(env)

      selections.push({ el, env })
    })
  }

  return makeLayerPrototype(line, selections, options)
}

// Validation is a static method to check if the class
// supports the chartData before building the chart
line.validateData = function (chartData, options) {
  const validate = validator(chartData, options)

  validate('Line chart must have more than one value.', isMultipleValues)
  validate('Line should be a time series', isTimeSeries)

  return validate
}
