import { select } from '../d3-custom'
import dialSVG from '../../assets/svg/dial-orange-to-blue.svg'

const Y_TOP = 293
const X_MID = 310
const INNER_RADIUS = 200
const OUTER_RADIUS = 256

export default function dial (options) {
  var defaults = {
  }
  options = Object.assign(defaults, options)

  function dial (selection) {
    selection.each(function (chartData) {
      if (chartData.chartOptions && chartData.chartOptions.pie) {
        Object.assign(options, chartData.chartOptions.pie)
      }

      const el = select(this)
      el.html(dialSVG)

      // width 620
      const rangle = (i) => (7 - i) * (Math.PI / 7) + (Math.PI / 14)
      const start = Number(chartData.fields[1].data[0])
      const end = Number(chartData.fields[1].data[1])
      const halfway = (end + start) / 2
      const noChange = (start === end)
      const arrow0 = [
        X_MID + Math.cos(rangle(start)) * OUTER_RADIUS,
        Y_TOP - (Math.sin(rangle(start)) * OUTER_RADIUS)
      ]
      const arrow1 = [
        X_MID + Math.cos(rangle(halfway)) * OUTER_RADIUS,
        Y_TOP - (Math.sin(rangle(halfway)) * OUTER_RADIUS)
      ]
      const arrow2 = [
        X_MID + Math.cos(rangle(end)) * OUTER_RADIUS,
        Y_TOP - (Math.sin(rangle(end)) * OUTER_RADIUS)
      ]

      el.select(`#val-${end}`)
        .classed('active', true)

      // end line
      el.select('#end-line')
        .attr('x2', X_MID + Math.cos(rangle(end)) * INNER_RADIUS)
        .attr('y2', Y_TOP - (Math.sin(rangle(end)) * INNER_RADIUS))

      el.select('#start-line')
        .attr('x2', X_MID + Math.cos(rangle(start)) * INNER_RADIUS)
        .attr('y2', Y_TOP - (Math.sin(rangle(start)) * INNER_RADIUS))

      // set
      el.select('#transition-path')
        .attr('d', calcCirclePath(arrow0, arrow2, arrow1))
        .style('display', noChange ? 'none' : 'block')

      el.select('#start-mark')
        .attr('transform', `translate(${arrow0[0]}, ${arrow0[1]}) rotate(${rangle(start) * (180 / Math.PI) * -1})`)
        .attr('d', `M${arrow0[0]},${arrow0[1]}l.41.28`)
        .style('display', noChange ? 'none' : 'block')
    })
  }

  return dial
}

function dist (a, b) {
  return Math.sqrt(
    Math.pow(a[0] - b[0], 2) +
    Math.pow(a[1] - b[1], 2))
}

// used in the arrow function, pass in three points and it will return an
// arc path going through all three
function calcCirclePath (a, b, c) {
  const A = dist(b, c)
  const B = dist(c, a)
  const C = dist(a, b)

  const angle = Math.acos((A * A + B * B - C * C) / (2 * A * B))

  // calc radius of circle
  const K = 0.5 * A * B * Math.sin(angle)
  let r = A * B * C / 4 / K
  r = Math.round(r * 1000) / 1000

  // large arc flag
  const laf = +(Math.PI / 2 > angle)

  // sweep flag
  const saf = +((b[0] - a[0]) * (c[1] - a[1]) - (b[1] - a[1]) * (c[0] - a[0]) < 0)

  return ['M', a, 'A', r, r, 0, laf, saf, b].join(' ')
}
