import { select } from '../d3-custom'
import { default as simpleColumn } from '../layers/simpleColumn'
import { default as makeEnv } from './env'
import { columnTooltip } from './tooltip'
import { columnAnnotations } from './annotations'
import { drawColumnLabels, drawColumnTableLabels } from './labels'
import { makeLayerPrototype } from '../utils/layers'
import {
  layoutManager,
  bandAxis,
  linearAxis,
  grid,
  format
} from '../axis'
import {
  buildTaxonomy,
  getSeries,
  sortAllFields,
  validator,
  isMultipleValues,
  isTimeSeries,
  isContinuousTimeSeries,
  isQuadSeries,
  isForceCategorical,
  isNegativeDomain,
  isMixedDomain,
  isPositiveDomain,
  DURATION_QUARTERS,
  OUTPUT_FORMAT_YEARS,
  OUTPUT_FORMAT_QUARTERS,
  OUTPUT_FORMAT_MONTHS
} from '../utils/data'
import * as layout from '../utils/layout'
import debugEnv from '../utils/debug'
import {default as _merge} from 'lodash-es/merge'

const CATEGORICAL_COLUMN_MIN = 5
const CATEGORICAL_COLUMN_MAX = 12
const APPLIED_ASPECT = 2.3
const MIN_ASPECT = 0.6
const MAX_ASPECT = 3.0

export default function column (options) {
  const defaults = {
    // this is set from the outside to drive JS sizing relative
    // to a CSS font size.  1.0 == 14px
    fontSizeRatio: 1.0,
    // some settings for horizontal layout switching
    // min width for column before horizontal layout
    columnWidthBreakpointMin: 30,
    // max width for column (basically disabled at 1000+)
    columnWidthBreakpointMax: 1250,
    // how many characters triggers long text breakpoints
    textLengthBreakpoint: 20,
    // if long text min width of column
    columnWidthLongTextBreakpoint: 80,
    // everything under this width is horizontal
    smallWidthBreakpoint: 500,
    // everything under this width is horizontal table layout
    mobileWidthBreakpoint: 380,
    // amount to offset label for mobile view
    mobileColumnOffset: 6,
    // distance tooltip box is away from point
    tooltipOffsetX: 7,
    // distance tooltip box is away from point
    tooltipOffsetY: 7,
    // tooltip box line for height estimation
    tooltipLineHeight: 30,
    // tooltip max width
    tooltipMaxWidth: 160,
    // radius of tooltip anchor
    tooltipAnchorSize: 4,
    // replaces axis inner padding for date axes
    dateAxisInnerPadding: 15,
    // Max lines before condensed mode
    maxLinesBeforeCondense: 15,
    // Max lines before single series condense
    maxLinesBeforeCondenseSingle: 15,
    // space between horizontal column and label
    columnLabelOffset: 8,

    // TODO :: REMOVE
    // forceVerticalColumns: true,

    // configuration options for layout manager
    layoutManager: {
      yAxisInnerPadding: 0,
      xAxisExtendRight: 7,
      margin: {
        top: 10,
        right: 0,
        bottom: 0,
        left: 0
      }
    },

    // configuration for the chart axes
    bandAxis: {
      horizontalBarHeight: 32,
      horizontalBarHeightCondensed: 24,
      horizontalBarPadding: 0.2,
      horizontalBarPaddingCondensed: 0.12,
      horizontalBarHeightMin: 10,
      axisTextLineHeight: 18,
      axisTextLineHeightHorizontal: 18
    },
    dateAxis: {},
    linearAxis: {}
  }
  options = _merge(defaults, options)

  const selections = []

  function column (selection) {
    selection.each(function (chartData, i) {
      const el = select(this)
        .classed('toaster-chart-column', true)
        .classed('bar-colors', true)
        .classed('quad-colors', isQuadSeries(chartData))

      columnTaxonomy(chartData)
      const env = makeEnv(el, chartData, options)

      // confusing but stackedHbar is vertical
      let orientation = layout.HORIZONTAL
      let yAxisPosition = layout.RIGHT
      let isHorizontal = isChartHorizontal(el, chartData, env.options)
      let isDateAxis = false
      if (!isHorizontal) {
        isDateAxis = renderChartAsTimeSeries(el, chartData, env.options)
      }

      if (isHorizontal) {
        // if horizontal and sort enabled
        if (chartData.config &&
            chartData.config.sort_by_value &&
            !isTimeSeries(chartData)) {
          if (isNegativeDomain(chartData)) {
            sortAllFields(chartData, {direction: 'ascending'})
          } else {
            sortAllFields(chartData)
          }
        }

        orientation = layout.VERTICAL
        yAxisPosition = layout.LEFT

        // endcaps by default on single series
        env.options.labelCaps = true
        env.options.tableLabels = false
        env.options.disableGrid = true

        env.options.layoutManager.margin.top = 0
        env.options.layoutManager.margin.bottom = 20
        env.options.layoutManager.margin.right = 30
        env.options.layoutManager.margin.left = 0
        env.options.layoutManager.yAxisInnerPadding = 0
        env.options.layoutManager.verticalAxisShift = 0

        const series = getSeries(chartData)
        const vRatio = getHeightRatio(env)

        const {minHeight} = env.options.layoutManager
        let minBarHeight = 0
        if (minHeight) {
          minBarHeight = minHeight / getNumberOfColumns(env.data)
        }

        if (env.options.mobileColumnChart) {
          env.options.bandAxis.horizontalBarHeight = 30 + (25 / series.length)
          env.options.bandAxis.horizontalBarPadding = 0.2 + (0.35 / series.length)
          env.options.layoutManager.yAxisInnerPadding = 0
          env.options.layoutManager.yAxisForceZeroWidth = true
          env.options.layoutManager.verticalAxisShift = 8
        } else {
          var w = el.node().offsetWidth
          // make sure labels don't go over half screen
          env.options.bandAxis.verticalAxisWrapWidth = w / 2

          env.options.bandAxis.horizontalBarHeight *= vRatio
          // env.options.bandAxis.horizontalBarPadding *= 0.8

          // if screenshot mode calculate horizontalBarHeight
          if (env.options.screenshotUpscale || env.options.screenshotMode) {
            env.options.bandAxis.horizontalBarHeight = Math.max(minBarHeight, env.options.bandAxis.horizontalBarHeight)
          }

          if (w < 600) {
            env.options.bandAxis.axisTextLineHeightHorizontal = 12
          }
        }

        // go to a condensed view when too many values
        // condensed view has no labels
        if (!env.options.mobileColumnChart && ((series.length > 1 &&
          chartData.data.length * series.length >
          env.options.maxLinesBeforeCondense) ||
          chartData.data.length > env.options.maxLinesBeforeCondenseSingle)
        ) {
          env.options.condensedView = true
          env.options.labelCaps = false
          env.options.disableGrid = false

          env.options.bandAxis.horizontalBarHeight =
            Math.max(minBarHeight, env.options.bandAxis.horizontalBarHeightMin,
                env.options.bandAxis.horizontalBarHeightCondensed * vRatio / (series.length))
          env.options.bandAxis.horizontalBarPadding =
            env.options.bandAxis.horizontalBarPaddingCondensed
        }

        if (env.options.labelCaps && !env.options.mobileColumnChart) {
          env.options.layoutManager.yAxisInnerPadding = 8
        } else if (!env.options.labelCaps && !env.options.mobileColumnChart) {
          env.options.layoutManager.verticalAxisShift = -5
        }

        // if horizontal chart with mixed domain (positive and negative)
        // we render tableLabels (all values at the end)
        if (env.options.labelCaps && isMixedDomain(chartData)) {
          env.options.labelCaps = false
          env.options.tableLabels = true
          env.options.layoutManager.margin.right = 60
          env.options.bandAxis.horizontalBarPadding = 0.4
          env.options.bandAxis.horizontalBarHeight *= 0.8
        }

        // if negative domain we draw label caps to the right of
        // negative chart values rather than the left
        if (env.options.labelCaps && isNegativeDomain(chartData)) {
          yAxisPosition = layout.RIGHT
        }
      }

      el.classed('vertical-flow', isHorizontal)

      // cleanup horizontal/vertical bits on layout change
      if (el.attr('toaster-flow') &&
        el.attr('toaster-flow') !== orientation) {
        el.select('.chart').selectAll('*').remove()
      }
      el.attr('toaster-flow', orientation)

      // allow every component of the layout to be
      // overriden by external options
      const layoutConfig = {
        orientation: orientation,
        yAxisPosition: yAxisPosition
      }
      Object.assign(layoutConfig, env.options.layoutManager)

      const bandAxisConfig = { isPrimaryAxis: true }
      if (isDateAxis) {
        layoutConfig.yAxisInnerPadding = env.options.dateAxisInnerPadding
        bandAxisConfig.isDateAxis = true
      }
      Object.assign(bandAxisConfig, env.options.bandAxis)
      layoutConfig.primaryAxis = bandAxis(bandAxisConfig)

      const linearAxisConfig = {
        isPrimaryAxis: false,
        domainFromZero: true
      }
      Object.assign(linearAxisConfig, env.options.linearAxis)

      layoutConfig.secondaryAxis = linearAxis(linearAxisConfig)
      const chartLayout = layoutManager(layoutConfig)
      const chart = el.selectAppend('div.chart')
      chart.selectAll('*').remove()
      chart.call(chartLayout, env)

      const column = chartLayout.draw().classed('column', true)
      let gridLength = env.x.rangeLength() +
        chartLayout.xAxisExtendRight() +
        (2 * chartLayout.yAxisInnerPadding())

      // grid is now a render layer
      const showGrid = (isHorizontal && !env.options.disableGrid) || layout.shouldDisplayGrid(env)
      el.classed('no-grid', !showGrid)
        .classed('no-domain', !layout.shouldDisplayDomain(env))
        .classed('no-axis', env.options.disableGrid)
      if (!isHorizontal) {
        column.selectAppend('g.grid')
          .call(grid.gridlines({ length: gridLength }), env)
      } else {
        gridLength = env.y.rangeLength() +
          (2 * chartLayout.xAxisInnerPadding())
        column.selectAppend('g.grid')
          .call(grid.gridlines({
            length: gridLength,
            orientation: layout.Y
          }), env)
      }

      column.call(simpleColumn({
        xScale: env.x.scale,
        yScale: env.y.scale,
        orientation: orientation
      }), env)

      if (env.options.labelCaps) {
        drawColumnLabels(column, env)
      } else if (env.options.tableLabels) {
        drawColumnTableLabels(column, env)
      }

      format.placeVerticalAxisLabel(env, isHorizontal)

      columnAnnotations(column, chartData.annotations, env)

      // create bottom label if needed
      chart.call(layout.bottomLabel(), env)

      // overflow layout call
      chartLayout.overflowScale()

      if (!env.options.disableTooltip) {
        columnTooltip({
          chart: chart,
          area: column,
          env: env
        })
      }

      selections.push({ el, env })
      debugEnv(env)
    })
  }

  return makeLayerPrototype(column, selections, options)
}

// Validation is a static method to check if the class
// supports the chartData before building the chart
column.validateData = function (chartData, options) {
  const validate = validator(chartData, options)

  validate('Bar chart must have more than one value.', isMultipleValues)

  validate(
    'Bar charts cannot have more than 50 colummns',
    chartData => (chartData.data.length <= 50)
  )

  validate(
    'Multi-series bar charts that have no rows in common should be stacked bar charts',
    notStackedDomain
  )

  validate(
    'Bar charts with time series data should not have more than 20 columns (use a line).',
    isTimeSeriesSmallEnough
  )

  validate(
    'Bar charts with time series data should be years or quarters.',
    isTimeSeriesYearQuarterMonth
  )

  return validate
}

// some validators that are shared between bar and stacked bar
export function isTimeSeriesYearQuarterMonth (chartData) {
  if (isTimeSeries(chartData)) {
    const field = chartData.fields[0]
    if (field.taxonomy.duration === DURATION_QUARTERS) return true
    if (field.taxonomy.isYears) return true
    if (field.dateFormat === 'YYYY' || field.customFormatter === 'YYYY' || field.type === 'year') {
      return true
    }
    if (chartData.chartOptions) {
      if (chartData.chartOptions.dateOutputFormat === OUTPUT_FORMAT_YEARS) {
        return true
      } else if (chartData.chartOptions.dateOutputFormat === OUTPUT_FORMAT_QUARTERS) {
        return true
      } else if (chartData.chartOptions.dateOutputFormat === OUTPUT_FORMAT_MONTHS) {
        return true
      }
    }
    return false
  }
  return true
}

// some validators that are shared between bar and stacked bar
export function isTimeSeriesSmallEnough (chartData) {
  if (isTimeSeries(chartData)) {
    return (chartData.data.length <= 12)
  }
  return true
}

export function notStackedDomain (chartData) {
  if (chartData.data.length < 50 && chartData.data[0].length > 2) {
    let sharedRowCount = 0
    chartData.data.forEach(row => {
      const nonNulls = row.slice(1).filter(v => v != null)
      if (nonNulls.length > 1) sharedRowCount += 1
    })

    return (sharedRowCount > 0)
  }

  return true
}

export function getColumnWidth (el, chartData, options) {
  const isStacked = options.stackedBand
  let columnCount = chartData.data.length * (chartData.fields.length - 1)
  if (isStacked) columnCount = chartData.data.length
  const width = el.node().offsetWidth
  return width / columnCount
}

export function renderChartAsTimeSeries (el, chartData, options) {
  const columnWidth = getColumnWidth(el, chartData, options)
  const isDate = isTimeSeries(chartData)
  const yearOrQuarter = isTimeSeriesYearQuarterMonth(chartData)

  if (chartData.chartOptions && chartData.chartOptions.dateOutputFormat && columnWidth > 40) {
    return false // bar chart using output format for years etc.
  }

  if (isForceCategorical(chartData.fields[0])) return false

  if (isDate) {
    if (yearOrQuarter) return false
    if (columnWidth > 50) return false
    return true
  }
  return false
}

// we need to apply a height ratio based on the aspectRatio
// provided - but applied for vertical height
export function getHeightRatio (env, numBars) {
  let ratio = '5:2'

  if (env.data.chartOptions && env.data.chartOptions.aspectRatio) {
    ratio = env.data.chartOptions.aspectRatio
  }
  if ((typeof ratio) === 'string') {
    ratio = ratio.split(':')[0] / ratio.split(':')[1]
  }

  // specific sizing to COTH
  if (env.options.ratioHeightOffset) {
    ratio += 1.0
  }

  return Math.max(
    MIN_ASPECT,
    Math.min(
      MAX_ASPECT,
      APPLIED_ASPECT / ratio
    )
  )
}

// This is for columns and stacked columns to determine by data
// and element width alone if the chart should be vertical or
// horizontal
// NOTE: this has side-effect of setting options.mobileColumnChart
export function isChartHorizontal (el, chartData, options) {
  let isHorizontal = false
  isHorizontal = isHorizontal || (chartData.chartType === 'hbar')
  isHorizontal = isHorizontal || (chartData.chartType === 'stackedHbar')
  isHorizontal = isHorizontal || options.forceHorizontalColumns

  if (options.forceVerticalColumns) return false
  if (chartData.config.force_vertical_bars) return false
  if (isHorizontal) return isHorizontal

  const isStacked = options.stackedBand
  let columnCount = chartData.data.length * (chartData.fields.length - 1)
  if (isStacked) columnCount = chartData.data.length
  const width = el.node().offsetWidth
  const columnWidth = width / columnCount

  if (columnCount <= 2) return true

  // when time series is cast to categorical maintain V Bars
  if (chartData.chartOptions.isCategoricalTime) return false
  if (isTimeSeries(chartData)) return false

  if (columnWidth < options.columnWidthBreakpointMin ||
    columnWidth > options.columnWidthBreakpointMax) {
    return true
  }

  // if the primary field is a date field (years or quarters)
  // keep the chart as a vertical bar
  const field = chartData.fields[0]
  if (field.taxonomy && field.taxonomy.isDate) {
    return false
  }

  // now perform a check for long text
  let longestText = 0
  chartData.fields[0].data.forEach(val => {
    if (val == null || val.length == null) return
    longestText = Math.max(longestText, val.length)
  })

  if (longestText > options.textLengthBreakpoint &&
    columnWidth < options.columnWidthLongTextBreakpoint) {
    isHorizontal = true
  }

  if (width < options.smallWidthBreakpoint) {
    isHorizontal = true

    // set horizontal chart style to mobile
    if ((width < options.mobileWidthBreakpoint ||
        longestText > options.textLengthBreakpoint) &&
        !isStacked && isPositiveDomain(chartData)) {
      options.mobileColumnChart = true
    }
  }

  return isHorizontal
}

// We need to keep core taxonomy functions agnostic to the
// chart type, but sometimes we want to refine
// the taxonomy (ie, categorical axes for bar charts vs line)
export function columnTaxonomy (data) {
  data.chartOptions = data.chartOptions || {}
  data.chartOptions.isCategorical = true

  // because categorical will override isTimeSeries
  if (data.chartOptions.isCategoricalTime == null) {
    data.chartOptions.isCategoricalTime = isTimeSeries(data)
  }

  if ((isContinuousTimeSeries(data) && data.data.length >= CATEGORICAL_COLUMN_MIN) ||
      (isTimeSeries(data) && data.data.length >= CATEGORICAL_COLUMN_MAX)) {
    data.chartOptions.isCategorical = false
  }

  buildTaxonomy(data, true) // force rebuild taxonomy
}

// number of columns from the data
export function getNumberOfColumns (chartData, isStacked) {
  if (isStacked) {
    return chartData.data.length
  } else {
    return chartData.data.length * getSeries(chartData).length
  }
}
