import {select} from '../d3-custom'
import {event as d3event} from 'd3'

/**
 *  provide the necessary javelin iframe hooks for resizing etc.
 */

// create callback closure for postResize
export function createResizeCallback () {
  let windowId = window._toasterWindowId || ''
  let useString = window._toasterIframeUseString || false
  const noNan = (v, defaultVal) => {
    if (isNaN(Number(v))) return defaultVal
    return Number(v)
  }

  const callback = (el, options) => {
    let height = noNan(el.offsetHeight, 0) + noNan(el.getBoundingClientRect().top, 0)
    const padding = 0

    if (options.screenshotUpscale) {
      const canvasSize = options.canvasSize || 740
      const exportSize = options.exportSize || 1200

      height *= noNan(exportSize / canvasSize, 1)
      setBodyScale(exportSize / canvasSize)
    }

    if (!options.noResizePropagation && window.parent && withinIframe()) {
      window.parent.postMessage({
        sentinel: 'amp',
        type: 'embed-size',
        height: height + padding
      }, '*')

      // if we've recieved a windowId from javelin, pass through height
      if (windowId !== '') {
        const message = {
          method: 'resize',
          windowId: windowId,
          height: height + padding
        }
        window.parent.postMessage(useString ? JSON.stringify(message) : message, '*')
      }
    }

    // this triggers the phantomjs resizer
    el.setAttribute('phantomheight', (height + padding) + 'px')
    return windowId
  }

  callback.setWindowId = id => {
    windowId = id
    window._toasterWindowId = id  // for multiple renders
  }

  callback.setUseString = val => {
    useString = val
    window._toasterIframeUseString = val // for multiple renders
  }

  return callback
}

/**
*  This is necessary for compatability with the javelin iframe setup
*  BUMP
*/
export function bindMessageHandlers (el, postResize, options) {
  select(window)
    .on('message.toaster', null)
    .on('message.toaster', function () {
      var data = d3event.data

      if (data.substring) {
        postResize.setUseString(true)
        try {
          data = JSON.parse(data)
        } catch (e) {
          return
        }
      }

      if (data.method === 'register') {
        postResize.setWindowId(data.windowId)
        postResize(el, options)
      }
    })
}

/**
*  We need a method to prevent an infinite loop with a resize sending message
*  to parent container, triggering further resize, etc. 
*/
export function bindResizeHandlers (el, render, postResize, data, options) {
  let lastWidth = null
  const { id } = data
  select(window)
    .on(`resize.toaster-${id}`, function () {
      if (!window.forceResize && lastWidth === window.innerWidth) return
      render()
      postResize(el, options)

      console.log("TOASTER PREVENT MOBILE :: ", id, options.preventMobileFont)
      select(el)
        .classed('large', (el.offsetWidth > 880))
        .classed('phablet', (el.offsetWidth <= 880 && (el.offsetWidth > 600 || options.preventMobileFont)))
        .classed('mobile', (el.offsetWidth <= 600 && !options.preventMobileFont))

      lastWidth = window.innerWidth
    })

  // modern browsers support the ability to check if fonts are ready
  // but we have to have a fallback for older browsers
  try {
    if (document.fonts && document.fonts.ready) {
      document.fonts.ready.then(() => select(window).dispatch('resize'))
    } else {
      setTimeout(() => select(window).dispatch('resize'), 1000)
    }
  } catch (err) {
    setTimeout(() => select(window).dispatch('resize'), 1000)
  }
}

/**
*  Upscale for screenshot (ie. we want a 740px rendered at 1200px)
*/
export function screenshotUpscale (el, options) {
  var canvasSize = options.canvasSize || 740

  var chart = select(el)
    .classed('large', (canvasSize > 880))
    .classed('phablet', (canvasSize <= 880 && canvasSize > 600))
    .classed('mobile', (canvasSize <= 600))

  if (options.screenshotMinHeight) {
    chart.style('min-height', options.screenshotMinHeight + 'px')
  }

  // read padding off of element style
  var style = window.getComputedStyle(el)
  var padding = Number(style.getPropertyValue('padding-left').replace('px', ''))
  padding += Number(style.getPropertyValue('padding-right').replace('px', ''))

  chart.style('width', (canvasSize - padding) + 'px')

  // we set our body scale here and then via the postResize
  // handler we apply the ratio scale (to make sure our )
  setBodyScale(1.0) // reset before render
}

function setBodyScale (scale) {
  select('body')
    .style('-webkit-transform-origin', 'top left')
    .style('-webkit-transform', 'scale(' + scale + ')')
}

export function withinIframe () {
  try {
    return window.self !== window.top
  } catch (e) {
    return true
  }
}
