/**
* Providing methods to expose information and data back to the toaster editor.
* This includes type validation, as well as custom options to be presented to the
* user within toaster.
*/
import { withinIframe } from './iframe'
import {
  typeReport,
  typeCheck
} from './data'

import { default as _isEqual } from 'lodash-es/isEqual'
import { default as _throttle } from 'lodash-es/throttle'

// these are for reporting to the editor
const throttledTypeCheck = _throttle(typeCheck, 5000)
const throttledTypeReport = _throttle(typeReport, 5000)

function postTypeCheck (messages) {
  if (messages && withinIframe()) {
    console.warn('TYPE CHECK FAIL', messages)
    window.parent.postMessage({
      type: 'type-check',
      messages: messages
    }, '*')
  }
}

function postTypeReport (report) {
  if (report && withinIframe()) {
    window.parent.postMessage({
      type: 'type-report',
      report: report
    }, '*')
  }
}

// instead of throttling we store current color series
// on the window and then check if it's different
// before sending to parent
window.currentColorSeries = null
function postEditorColorSeries (newSeries, active) {
  if (!window.currentColorSeries || !_isEqual(window.currentColorSeries, newSeries)) {
    window.currentColorSeries = newSeries
    if (newSeries && withinIframe()) {
      window.parent.postMessage({
        type: 'color-series',
        colorSeries: {
          active,
          colors: newSeries
        }
      }, '*')
    }
  }
}

export function typeChecks (chartType, types, data, options) {
  throttledTypeCheck(chartType, data, options, postTypeCheck)
  throttledTypeReport(types, data, options, postTypeReport)
}

export function editorHooks (chartType, data, options) {
  // allow exposing customColors to the editor
  options.editorHooks = {
    colorSeries: postEditorColorSeries
  }
}
