import { dateToUTCString } from '../../utils/data'
import {
  hideDuplicates,
  labelDensity,
  hideOverlappingTicks,
  hideLine2Duplicates
} from './utils'
import { default as applyDateAxis } from './dateAxis'

export default function dayAxis (selection, axis, env, options) {
  var numTicks = Math.max(2, Math.floor(axis.rangeLength() / axis.tickStepHorizontal()))
  var formatters = [
    v => v.format('ddd'),
    v => v.format('MMM D')
  ]

  numTicks /= labelDensity(axis)

  axis.model()
    .ticks(numTicks)
    .tickFormat(function (d, i) {
      return dateToUTCString(d, axis.field())
    })

  selection.call(axis.model())

  applyDateAxis(selection, axis, formatters, env, { noEndCap: false })

  hideDuplicates(selection, axis)
  hideOverlappingTicks(selection, axis)
  hideLine2Duplicates(selection, axis)

  return selection
}
