// Allow each theme to have chart options that get passed
// through to the renderer. These are just for the default
// themes, and other type-specific or theme-specific types
// would be set in any implemnting factory
import {default as _merge} from 'lodash-es/merge'
import { select } from '../d3-custom'

window._merge = _merge

const themeDefaults = {
  'gadfly': {
    renderAreaChart: true
  },
  'bizweek': {
    linearAxis: {
      tickLabelAlternate: false
    }
  },
  'coth': {
    columnWidthBreakpointMin: 20,
    columnWidthLongTextBreakpoint: 50,
    screenshotMinHeight: 340,
    ratioHeightOffset: true
  },
  'bi': {
    screenshotMinHeight: 385,
    layoutManager: {
      aspectDrivenHeight: false,
      fixedHeightSizing: true,
      fixedHeight: 385 // 781 x 385 images are expected
    }
  },
  'green': {
    linearAxis: {
      tickStepVertical: 180
    },
    layoutManager: {
      aspectRatio: '4:3'
    }
  },
  'green-dark': {
    linearAxis: {
      tickStepVertical: 180
    },
    layoutManager: {
      aspectRatio: '4:3'
    }
  }
  // 'terminal': {
  //   bandAxis: {
  //     axisTextLineHeightHorizontal: 15
  //   }
  // },
  // 'coth': {
  //   bandAxis: {
  //     axisTextLineHeightHorizontal: 15
  //   }
  // }
}

export function themeOptions (data, theme, options) {
  options = options || {}
  const defaults = themeDefaults[theme] || {}

  // to avoid having COTH's ratioHeightOffset mess up
  // other themes via webTheme= we need to read the original theme
  // and apply it based off that
  if (data.originalTheme === 'coth' ||
      (data.config && data.config.original_theme === 'coth')) {
    defaults.ratioHeightOffset = true
  }

  return _merge(options, defaults)
}

const defaultWrapper = selection => null
const themeWrappers = {
  gadfly: function (selection) {
    selection.each(function (chartData, i) {
      // using +3 to match rails implementation color
      const colorSetId = (parseInt('0x' + chartData.id) + 3) % 5
      select(this)
        .classed('color-set-0', false)
        .classed('color-set-1', false)
        .classed('color-set-2', false)
        .classed('color-set-3', false)
        .classed('color-set-4', false)
        .classed(`color-set-${colorSetId}`, true)
    })
  }
}

export function themeWrapper (theme, options) {
  if (themeWrappers[theme]) {
    return themeWrappers[theme]
  }

  return defaultWrapper
}
