import {
  mouse as d3mouse,
  touches as d3touches
} from 'd3'
import {
  getSeries,
  valueFormatter,
  interpolateBetweenValues,
  isSingleSeries,
  isSingleSeriesStackedDomain,
  valueOrNull
} from '../utils/data'
import * as layout from '../utils/layout'
import { applyCustomColors } from '../utils/color/index'

export default function tooltip (selection, tooltipRender) {
  // TODO: provide timeout to check whether mouse is offscreen

  var isEnabled = false

  function enable () {
    if (isEnabled) return
    selection.selectAll('.tooltip').classed('tooltip-hidden', false)
    isEnabled = true
  }

  function disable () {
    if (!isEnabled) return
    selection.selectAll('.tooltip').classed('tooltip-hidden', true)
    isEnabled = false
  }

  function mouseMove (context) {
    tooltipRender(d3mouse(this), enable, disable)
  }

  function touchMove (context) {
    const touchPos = d3touches(this)[0]
    if (touchPos) {
      tooltipRender(touchPos, enable, disable)
    }
  }

  selection.style('pointer-events', 'all')
    .on('mouseover.tooltip', null) // clear exisiting handlers
    .on('mouseout.tooltip', null) // clear exisiting handlers
    .on('mousemove.tooltip', null) // clear exisiting handlers
    .on('mouseover.tooltip', enable)
    .on('mouseout.tooltip', disable)
    .on('mousemove.tooltip', mouseMove)
    .on('touchstart.tooltip', null) // clear exisiting handlers
    .on('touchend.tooltip', null) // clear exisiting handlers
    .on('touchmove.tooltip', null) // clear exisiting handlers
    .on('touchstart.tooltip', enable)
    .on('touchend.tooltip', disable)
    .on('touchmove.tooltip', touchMove)
}

export function lineTooltip (opts) {
  // ---- NOTE : going to cleanup and move all the tooltip stuff
  // just here for convenience factor
  const { env, chart, area } = opts
  const xAxis = env.x
  const yAxis = env.y

  // append tooltip to our selection
  chart.selectAll('.tooltip').remove()

  const series = getSeries(env.data)
  const singleSeries = isSingleSeries(env.data)
  const tContainer = chart.selectAppend('div.tooltip-container')
  const tLabelBox = tContainer.append('div')
    .attr('class', 'tooltip tooltip-hidden tooltip-label-box')
  const tLabel = tLabelBox.append('div').attr('class', 'tooltip-label')
  const tValues = tLabelBox.selectAppend('div.tooltip-values')
  const margin = env.layout.margin()
  const isStacked = env.y.stackedDomain()

  const gTooltip = area.selectAppend('g.tooltip-container')
  const tGridline = gTooltip.append('line')
    .attr('class', 'tooltip tooltip-hidden tooltip-gridline')

  const tAnchors = []
  tAnchors.push(gTooltip.append('circle')
    .attr('class', 'tooltip tooltip-hidden tooltip-anchor series-0-stroke'))
  tAnchors.push(gTooltip.append('circle')
    .attr('class', 'tooltip tooltip-hidden tooltip-anchor series-1-stroke'))
  tAnchors.push(gTooltip.append('circle')
    .attr('class', 'tooltip tooltip-hidden tooltip-anchor series-2-stroke'))
  tAnchors.push(gTooltip.append('circle')
    .attr('class', 'tooltip tooltip-hidden tooltip-anchor series-3-stroke'))
  tAnchors.push(gTooltip.append('circle')
    .attr('class', 'tooltip tooltip-hidden tooltip-anchor series-4-stroke'))
  tAnchors.push(gTooltip.append('circle')
    .attr('class', 'tooltip tooltip-hidden tooltip-anchor series-5-stroke'))

  const formatOptions = {
    defaultDecimals: 1,
    isTooltip: true
  }
  const formatPrimary = valueFormatter(env.x, env, formatOptions)
  const formatSecondary = valueFormatter(env.y, env, formatOptions)

  applyCustomColors(tContainer.node(), env.data, env.options)

  function tooltipRender (pos, enableTooltip, disableTooltip) {
    var boundingBox = area.node().getBoundingClientRect()
    var padLeft = env.layout.yAxisInnerPadding()

    var posNormalized = [
      (pos[0] - padLeft) / xAxis.scale.range()[1],
      1.0 - pos[1] / yAxis.scale.range()[0]
    ]

    var primaryAxisData = xAxis.getDataAtPosition(posNormalized[0])
    enableTooltip() // won't do anything if already enabled
    if (!primaryAxisData) return

    // set 0 as min seriesIndex for cases when no hit registered
    if (primaryAxisData.seriesIndex == null) {
      primaryAxisData.seriesIndex = 0
    }

    var anchorPositionX = xAxis.getPositionFromIndex(
      primaryAxisData.dataIndex, 'ignoreSeries')

    if (!env.data.data[primaryAxisData.dataIndex]) return
    var labelValue = env.data.data[primaryAxisData.dataIndex][0]
    var dataValue = primaryAxisData.data.map((val, i) => {
      if (valueOrNull(val) == null) {
        return interpolateBetweenValues(env, primaryAxisData.dataIndex, i)
      }
      return val
    })

    // do nothing if no active items
    if (!dataValue.filter(v => v != null).length) return

    var xPos = anchorPositionX * xAxis.scale.range()[1]
    var yPos = dataValue.map(val => yAxis.scale(val))
    if (isStacked) {
      var yPosTotal = 0
      var yNegTotal = 0
      yPos = yPos.map((v, i) => {
        if (dataValue[i] < 0) {
          yNegTotal += v
          return yNegTotal
        } else {
          yPosTotal += yAxis.rangeLength() - v
          return yAxis.rangeLength() - yPosTotal
        }
      })
    }
    var lowestPos = yPos.reduce((a, b) => Math.min(a, b))

    tGridline
      .attr('x1', xPos)
      .attr('x2', xPos)
      .attr('y1', yAxis.scale.range()[0])
      .attr('y2', yAxis.scale.range()[1])

    tAnchors.forEach(function (tAnchor, i) {
      const showAnchor = (xAxis.series()[i] && dataValue[i] != null)
      tAnchor
        .attr('r', env.options.tooltipAnchorSize)
        .attr('cx', xPos || 0)
        .attr('cy', yPos[i] || 0)
        .style('display', showAnchor ? 'block' : 'none')
    })

    // to speed things up, calculate out of bounds
    // before placing the label box - determine what
    // if we need to flip to the other side etc.
    var pad = env.layout.yAxisInnerPadding()
    var labelX = xPos + margin.left + pad + env.options.tooltipOffsetX
    var labelY = lowestPos + margin.top + env.options.tooltipOffsetY
    var adjustClass = null
    var heightEst = env.options.tooltipLineHeight * (series.length + 1)
    var widthEst = env.options.tooltipMaxWidth
    var flipHorizontal = false
    var flipVertical = false

    if (labelX + widthEst > boundingBox.width) {
      labelX = Math.max(
        xPos + pad + margin.left - env.options.tooltipOffsetX,
        widthEst)
      adjustClass = 'flipHorizontal'
      flipHorizontal = true
    }

    if (labelY + heightEst > boundingBox.height) {
      labelY = Math.max(
        lowestPos + margin.top - env.options.tooltipOffsetY,
        heightEst)
      adjustClass = 'flipVertical'
      flipVertical = true
    }

    if (flipVertical && flipHorizontal) {
      adjustClass = 'flipVH'
    }

    tLabelBox
      .style('left', labelX + 'px')
      .style('top', labelY + 'px')
      .classed('flipHorizontal', (adjustClass === 'flipHorizontal'))
      .classed('flipVertical', (adjustClass === 'flipVertical'))
      .classed('flipVH', (adjustClass === 'flipVH'))
      .classed('single-series', singleSeries)
    tLabel
      .text(formatPrimary(labelValue))

    var valueDivs = tValues.selectAll('.tooltip-value').data(dataValue)
    valueDivs.exit().remove()
    valueDivs.enter()
      .append('div')
      .classed('tooltip-value', true)
      .merge(valueDivs)
      .style('display', (d) => d == null ? 'none' : 'block')
      .html(function (d, i) {
        return `
            <span class="swatch bg series-${i}"></span>
            <span class="val">${formatSecondary(d)}</span>
          `
      })

    applyCustomColors(tLabelBox.node(), env.data, env.options)

    if (env.options.tooltipPostRender) {
      env.options.tooltipPostRender(env, tLabelBox, dataValue, primaryAxisData.dataIndex)
    }
  }

  var chartTooltip = tooltip(chart, tooltipRender, env)

  return chartTooltip
}

//
// Tooltip implementation specific to columns
// we also share it for the stacked column
export function columnTooltip (opts) { // eslint-disable-line no-unused-vars
  // append tooltip to our selection
  opts.chart.selectAll('.tooltip').remove()

  const env = opts.env
  if (env.options.labelCaps) return // no tooltips with labels

  const series = getSeries(opts.env.data)
  const singleSeries = isSingleSeries(opts.env.data)
  const tContainer = opts.chart.selectAppend('div.tooltip-container')
  const tLabelBox = tContainer.append('div')
    .attr('class', 'tooltip tooltip-hidden tooltip-label-box')
  const tLabel = tLabelBox.append('div').attr('class', 'tooltip-label')
  const tValues = tLabelBox.selectAppend('div.tooltip-values')

  const margin = env.layout.margin()
  const isStacked = (env.options.stackedBand || env.y.stackedDomain())
  const ignoreSeries = isStacked || opts.barline

  const gTooltip = opts.area.selectAppend('g.tooltip-container')
  const tGridline = gTooltip.append('line')
    .attr('class', 'tooltip tooltip-hidden tooltip-gridline')
  const tAnchor = gTooltip.append('circle')
    .attr('class', 'tooltip tooltip-hidden tooltip-anchor')

  applyCustomColors(tContainer.node(), env.data, env.options)

  let singleSeriesStacked = false

  // called on mousemove to position tooltip
  function tooltipRender (pos, enableTooltip, disableTooltip) {
    var boundingBox = opts.area.node().getBoundingClientRect()
    var posNormalized = [
      (pos[0] - margin.left) / env.x.rangeLength(),
      1.0 - (pos[1] - margin.top) / env.y.rangeLength()
    ]

    // wait until tooltip active to save initial render time
    // if stacked domain check if singleseriesstacked
    if (isStacked && env.options.singleSeriesStacked == null) {
      singleSeriesStacked = isSingleSeriesStackedDomain(env.data)
      env.options.singleSeriesStacked = singleSeriesStacked
    }

    // HorizontalBar is equal to a vertical primary axis
    var isHorizontal = (env.layout.orientation() === layout.VERTICAL)
    var primaryAxis = isHorizontal ? env.y : env.x
    var secondaryAxis = isHorizontal ? env.x : env.y
    var formatOptions = {
      defaultDecimals: 1,
      isTooltip: true
    }
    var formatPrimary = valueFormatter(primaryAxis, env, formatOptions)
    var formatSecondary = valueFormatter(secondaryAxis, env, formatOptions)

    var primaryAxisData, anchorPositionX
    if (isHorizontal) {
      posNormalized[1] = 1.0 - posNormalized[1]
      primaryAxisData = env.y.getDataAtPosition(posNormalized[1])
    } else {
      primaryAxisData = env.x.getDataAtPosition(posNormalized[0])
    }

    if (!primaryAxisData ||
      env.data.data[primaryAxisData.dataIndex] === undefined) {
      disableTooltip()
      return
    }
    enableTooltip() // won't do anything if already enabled

    if (isHorizontal) {
      anchorPositionX = env.y.getPositionFromIndex(
        [primaryAxisData.dataIndex, primaryAxisData.seriesIndex], ignoreSeries)
    } else {
      anchorPositionX = env.x.getPositionFromIndex(
        [primaryAxisData.dataIndex, primaryAxisData.seriesIndex], ignoreSeries)
    }

    // for barline we only use the bar series
    if (opts.barline) primaryAxisData.seriesIndex = 0

    var labelValue = env.data.data[primaryAxisData.dataIndex][0]
    var dataValue = primaryAxisData.data[primaryAxisData.seriesIndex]
    if (isStacked) {
      dataValue = primaryAxisData.data.reduce((a, b) => {
        return Math.max(a, 0) + Math.max(b, 0)
      }, 0)
      primaryAxisData.seriesIndex = 'X' // nullify this value
    }

    var xPos = anchorPositionX * env.x.rangeLength()
    var yPos = env.y.scale(dataValue)
    if (isHorizontal) {
      yPos = anchorPositionX * env.y.rangeLength()
      xPos = env.x.scale(dataValue)
    }

    var values = series.map(function (s) {
      return s.data[primaryAxisData.dataIndex]
    })

    if (isNaN(yPos) || isNaN(xPos)) return

    if (isHorizontal) {
      tGridline
        .attr('x1', xPos)
        .attr('x2', xPos)
        .attr('y1', env.y.scale.range()[0])
        .attr('y2', env.y.scale.range()[1])
    } else {
      tGridline
        .attr('x1', env.x.scale.range()[0])
        .attr('x2', env.x.scale.range()[1])
        .attr('y1', yPos)
        .attr('y2', yPos)
    }

    tAnchor
      .attr('r', env.options.tooltipAnchorSize)
      .attr('cx', xPos || 0)
      .attr('cy', yPos || 0)

    // to speed things up, calculate out of bounds
    // before placing the label box - determine what
    // if we need to flip to the other side etc.
    var pad = env.layout.yAxisInnerPadding()
    var labelX = xPos + pad + margin.left + env.options.tooltipOffsetX
    var labelY = yPos + margin.top + env.options.tooltipOffsetY
    var adjustClass = null
    var heightEst = env.options.tooltipLineHeight * (series.length + 1)
    var widthEst = env.options.tooltipMaxWidth
    var flipHorizontal = false
    var flipVertical = false

    if (isStacked) {
      heightEst = env.options.tooltipLineHeight * (series.length + 2)
    }

    if (labelX + widthEst > boundingBox.width) {
      labelX = Math.max(
        xPos + pad + margin.left - env.options.tooltipOffsetX,
        widthEst)
      adjustClass = 'flipHorizontal'
      flipHorizontal = true
    }

    if (labelY + heightEst > boundingBox.height) {
      labelY = Math.max(
        yPos + margin.top - env.options.tooltipOffsetY,
        heightEst)
      adjustClass = 'flipVertical'
      flipVertical = true
    }

    if (flipVertical && flipHorizontal) {
      adjustClass = 'flipVH'
    }

    tLabelBox
      .style('left', labelX + 'px')
      .style('top', labelY + 'px')
      .classed('flipHorizontal', (adjustClass === 'flipHorizontal'))
      .classed('flipVertical', (adjustClass === 'flipVertical'))
      .classed('flipVH', (adjustClass === 'flipVH'))
      .classed('single-series', (singleSeries || singleSeriesStacked))
    tLabel
      .text(formatPrimary(labelValue))

    var valueDivs = tValues.selectAll('.tooltip-value').data(values)
    valueDivs.exit().remove()
    valueDivs.enter()
      .append('div')
      .classed('tooltip-value', true)
      .merge(valueDivs)
      .classed('active', function (d, i) {
        return (i === primaryAxisData.seriesIndex)
      })
      .html(function (d, i) {
        return `
            <span class="swatch bg series-${i}"></span>
            <span class="val">${formatSecondary(d)}</span>
          `
      })
      .style('display', function (d) {
        if (!singleSeriesStacked) return 'flex'
        return d ? 'flex' : 'none'
      })

    if (isStacked && !singleSeriesStacked) {
      tLabelBox.classed('stacked', true)
      tValues.append('div')
        .classed('tooltip-value', true)
        .classed('total', true)
        .html(function () {
          return `
            <span class="left">Total</span>
            <span class="val">${formatSecondary(dataValue)}</span>
          `
        })
    }

    applyCustomColors(tLabelBox.node(), env.data, env.options)
  }

  tooltip(opts.chart, tooltipRender)
}
