// standardize the creation of the default env
// TODO :: standardize this into stub axes and layout manager
export default function makeEnv (el, data, options) {
  return {
    el, // required
    data, // required
    x: null, // simpleAxis({orientation: 'x'}),
    y: null, // simpleAxis({orientation: 'y'}),
    z: null, // simpleAxis({orientation: 'y'}),
    r: null,
    layout: null, // simpleLayout(),
    layers: [],
    options: Object.assign({}, options) // shallow copy
  }
}
