/**
 *  Gridline renderers
 */
import { select } from '../d3-custom'
import * as layout from '../utils/layout'

export function gridlines (options) {
  const defaults = {
    orientation: layout.X,
    verticalGridLength: 8
  }
  options = Object.assign(defaults, options)

  function render (selection, env) {
    const horizontalGrid = options.orientation === layout.X
    const axis = horizontalGrid ? env.y : env.x
    const grid = layout.AXIS_MAP[axis.position()](axis.scale)
    let length = options.length

    if (axis.position() === layout.LEFT) {
      length *= -1
    }

    if (axis.model().tickValues()) {
      grid.tickValues(axis.model().tickValues())
    } else {
      grid.ticks(axis.numTicks())
    }

    grid
      .tickSize(length)
      .tickFormat('')

    selection.call(grid)
      .classed('vertical-grid', !horizontalGrid)
      .translate([env.layout.yAxisInnerPadding() * -1, env.layout.xAxisInnerPadding() * -1])

    // set class for zero line
    selection.selectAll('g.tick')
      .filter(function (d) { return d === 0 })
      .select('line')
      .attr('class', 'zero-line')

    // hide lines at the bottom
    selection.selectAll('g.tick')
      .filter(function (d) {
        return Math.abs(axis.range()[0] - axis.scale(d)) < 5
      })
      .select('line')
      .attr('class', 'hide-line')

    // make sure vertical ticks are only small ticks
    if (!horizontalGrid) {
      selection.selectAll('g.tick')
        .select('line')
        .attr('y1', function () {
          const y2 = Number(select(this).attr('y2'))
          return y2 - options.verticalGridLength
        })
    }

    return selection
  }

  return render
}
