import {select} from '../d3-custom'

import {default as simpleTable} from '../layers/simpleTable'

export default function table (options) {
  options = options || {}

  function table (selection) {
    selection.each(function (chartData) {
      var el = select(this)

      el.selectAppend('div.chart')
        .selectAppend('div.toaster-chart-table')
        .call(simpleTable(options))
    })
  }

  return table
}
