import { select } from '../d3-custom'

import { getSeries, getFieldIndexValueParser, valueFormatter } from '../utils/data'

export default function valueTag (options) {
  const xScale = options.xScale
  const yScale = options.yScale
  const seriesOffset = options.seriesOffset || 0

  function valueTag (selection) {
    selection.each(function (chartData, i) {
      const el = select(this)
        .classed('simple-line', true)

      const formatOptions = {
        defaultDecimals: 1,
        bareValue: true,
        isTooltip: true
      }
      const format = valueFormatter(options.env.y, options.env, formatOptions)
      const xData = getFieldIndexValueParser(chartData)
      const series = options.seriesData || getSeries(chartData)
      const tagGroup = el.selectAppend('g.value-tag-group')
      const tags = tagGroup.selectAll('.tags').data(series)

      if (options.xOffset) {
        tagGroup.translate([options.xOffset])
      }

      const pathD = (w) => {
        return `M${w - 1},0.8c0,7.5,0,15,0,22.5c-22,0-${w / 2 + 4},0-${w - 14},0c-4-3.8-8-7.7-12-11.5c4-3.7,8-7.3,12-11C35,0.8,${w - 23},0.8,${w - 1},0.8z`
      }

      tags.exit().remove()

      const tagEntries = tags.enter()
        .append('g')
        .attr('class', 'tag')
        .merge(tags)
        .style('display', (d) => d.data[d.data.length - 1] ? 'block' : 'none')
        .translate((d) => {
          const lastIndex = d.data.length - 1
          const lastPoint = d.data[lastIndex]
          return [xScale(xData(lastIndex)), yScale(lastPoint)]
        })

      // const paths = tags.selectAll('path').data(series)
      // tags.enter().selectAll('g.tag')
      tagEntries
        .append('path')
        .attr('class', (d, i) => 'f series-' + (i + seriesOffset))
        .style('transform', 'translate(-2px, -10px) scale(0.85)')
        .merge(tagEntries)
        .attr('d', pathD(64))

      // const texts = tags.selectAll('text').data(series)
      // tags.enter().selectAll('g.tag')
      tagEntries
        .append('text')
        .merge(tagEntries)
        .style('fill', 'white')
        .attr('x', 13)
        .attr('y', 5)
        .text(d => {
          return format(d.data[d.data.length - 1])
        })

      // after text is in place use text length to crop the tag
      tagGroup.selectAll('g.tag').each(function () {
        const width = select(this).select('text').node().getComputedTextLength()
        select(this).select('path').attr('d', pathD(width + 30))
      })
    })
  }

  return valueTag
}
