import { select } from '../d3-custom'
import { getFieldIndexValueParser } from '../utils/data'
import { range as d3range } from 'd3-array'
import {
  area as d3area,
  stack as d3stack
} from 'd3-shape'

export default function simpleAreaStacked (options) {
  options = options || {}

  function simpleAreaStacked (selection, env) {
    selection.each(function (chartData, i) {
      const el = select(this)
        .classed('simple-area', true)

      const xScale = env.x.scale
      const xData = getFieldIndexValueParser(chartData)
      const yScale = v => {
        return Math.min(env.y.scale(v), Math.floor(env.y.rangeLength()))
      }

      const stackedKeys = d3range(1, chartData.data[0].length)
      const stackedData = d3stack()
        .keys(stackedKeys)(chartData.data)

      const areaGroup = el.selectAppend('g.area-group')
      const areas = areaGroup.selectAll('.line-area').data(stackedData)

      if (options.xOffset) {
        areaGroup.translate([options.xOffset])
      }

      const area = d3area()
        .x((d, i) => xScale(xData(i)))
        .y0(d => yScale(d[0]))
        .y1(d => yScale(d[1]))
        .defined(d => {
          let isDefined = true
          stackedKeys.forEach(k => {
            if (isNaN(d[k])) return false
            if (d[k] === null || d[k] === undefined) return false
          })

          return isDefined
        })

      areas.exit().remove()

      areas.enter()
        .append('path')
        .attr('class', (d, i) => `line-area f series-${i}`)
        .merge(areas)
        .attr('d', d => area(d))
    })

    return selection
  }

  return simpleAreaStacked
}
