import { select } from '../d3-custom'
import {objectAccessor} from '../utils/object'
import {default as linearAxis} from './linear'

import {
  BOTTOM,
  RIGHT,
  LEFT,
  HORIZONTAL,
  VERTICAL,
  getAspectRatio,
  getPaddingHeight,
  getMarginHeight
} from '../utils/layout'

/**
  Provide layout options and return an interface to chart elements

  A goal here is to determine when to swap orientation between
  horizontal and vertical and keep each axis independent

  Added InnerPadding to support a range that is inside the drawing area,
  this allows a drawing area and axis length that is wider than the range()

  I had to change the name from chartLayout for an odd naming collision
*/
export default function layoutManager (options) {
  var state = {
    primaryAxis: linearAxis(),
    secondaryAxis: linearAxis(),
    orientation: HORIZONTAL,
    secondaryOrientation: VERTICAL,
    xAxisPosition: BOTTOM,
    yAxisPosition: RIGHT,
    yAxisPadding: 8,
    yAxisInnerPadding: 10,
    yAxisTickWidth: 0, // used to set width from 'mini-ticks'
    xAxisInnerPadding: 0,
    xAxisMinHeight: 30,
    yAxisForceZeroWidth: false,
    xAxisForceZeroWidth: false,
    xAxisExtendRight: 0,
    width: 0,
    outerWidth: 0,
    height: 0,
    minHeight: null,
    minHeightHorizontal: 240,
    minHeightVertical: 0,
    outerHeight: 0,
    legend: null,
    xAxis: null,
    yAxis: null,
    container: null,
    svg: null,
    g: null, // g.margin
    draw: null, // draw area
    extents: null, // draw area extents
    aspectDrivenHeight: true, // disable to fill to space (flex)
    aspectRatio: '5:2', // target aspect ratio
    isMobile: false, // mobile
    isMedium: false, // phablet
    isLarge: true, // large size
    chartFrameMarginWidth: 30, // 15px margins on either side
    verticalAxisShift: 5, // amount to shift vertical axis towards bars
    padding: 0,
    verticalBandHeight: 80,
    expectedFrameHeight: 160,
    margin: {
      top: 10,
      right: 0,
      bottom: 0,
      left: 0
    },
    padBottom: 8,
    fixedHeightSizing: false, // set a specific height to fill
    fixedHeight: 385 // set a height to matches
  }

  state = Object.assign(state, options)

  const postLayoutHandlers = []

  const layout = function (selection, env) {
    var chart = selection
    var margin = layout.margin()
    var width = chart.node().offsetWidth
    var outerWidth = width
    var outerWidthMargins = outerWidth + state.chartFrameMarginWidth
    var height = chart.node().offsetHeight
    var outerHeight = height
    var xAxisProvider = layout.primaryAxis()
    var yAxisProvider = layout.secondaryAxis()

    // apply self at top to allow axis functions to reference layout
    env.layout = layout

    // use aspectRatio to determine ideal height
    if (layout.aspectDrivenHeight()) {
      var ratio = getAspectRatio(env.data, layout.aspectRatio())

      // height offset is used specifically for COTH
      var heightOffset = 0
      if (env.options.ratioHeightOffset) {
        heightOffset = state.expectedFrameHeight
      }

      height = Math.round(layout.heightOrMin((width / ratio) - heightOffset))
    }

    // use aspectRatio to determine ideal height, we read the CSS properties
    // to determine specific height for chart
    if (layout.fixedHeightSizing()) {
      var parentNode = select(chart.node().parentNode)
      var totalMargins = getPaddingHeight(parentNode.node())
      var header = parentNode.select('.header').node()
      var legend = parentNode.select('.simple-legend').node()
      var footer = parentNode.select('.footer').node()

      if (header.offsetHeight) {
        totalMargins += header.offsetHeight + getMarginHeight(header)
      }

      // legend margin is there even when hidden
      totalMargins += legend.offsetHeight + getMarginHeight(legend)

      if (footer.offsetHeight) {
        totalMargins += footer.offsetHeight + getMarginHeight(footer)
      }

      height = Math.round(state.fixedHeight - totalMargins)
      chart.style('height', height + 'px')
    }

    // swap primary / secondary axis
    if (layout.orientation() === VERTICAL) {
      xAxisProvider = layout.secondaryAxis()
      yAxisProvider = layout.primaryAxis()

      // calculate height by number of bands
      height = layout.verticalBandHeight() * env.data.data.length
      height += margin.top + margin.bottom
      height = Math.round(height)
    }

    // classify as mobile / medium / or large
    if (outerWidthMargins <= 880 && outerWidthMargins > 600) {
      layout.isLarge(false).isMedium(true)
    } else if (outerWidthMargins <= 600) {
      layout.isLarge(false).isMobile(true)
    }

    // instead of setting width / height explicitly, if
    // we use viewBox="0 0 w h" we can have the svg scale to fit it's
    // container just like an image. This will allow an SVG to be
    // embedded directly without JS
    // NOTE :: viewBox disabled because phantomjs doesn't support it yet
    height = layout.heightOrMin(height)
    var svg = chart.selectAppend('svg')
      .style('width', width + 'px')
      .style('height', height + 'px')

    var gMargin = svg.selectAppend('g.margin')

    height -= margin.top + margin.bottom + layout.padBottom()

    if (layout.orientation() === HORIZONTAL) {
      height -= layout.xAxisMinHeight()
    }

    yAxisProvider
      .orientation(VERTICAL)
      .position(layout.yAxisPosition())
      .range([height, 0])
      .data(env.data)

    var yAxis = gMargin.selectAppend('g.axis.axis-y')
      .call(yAxisProvider, env)
    var yAxisBounds = yAxis.node().getBoundingClientRect()
    var yAxisBoundsWidth = yAxisBounds.width // bounds object is read only

    if (layout.yAxisForceZeroWidth()) yAxisBoundsWidth = 0

    if (layout.yAxisPosition() === RIGHT) {
      margin.right = Math.max(margin.right,
        Math.ceil(yAxisBoundsWidth + layout.yAxisPadding()))

      outerWidth -= margin.right + margin.left

      yAxis.attr('transform', 'translate(' + (outerWidth) + ', 0)')
    } else {
      margin.left = Math.max(margin.left,
        Math.ceil(yAxisBoundsWidth))

      outerWidth -= margin.right + margin.left
    }

    // adjust height to height of chart
    if (layout.orientation() === VERTICAL) {
      height = layout.heightOrMin(yAxisProvider.rangeLength())
      outerHeight = height
      svg.style('width', width + 'px')
        .style('height', Math.round(height + margin.top + margin.bottom + layout.padBottom()) + 'px')
      if (layout.yAxisPosition() === LEFT) {
        yAxis.translate([state.verticalAxisShift, 0])
      }
    }

    gMargin
      .attr('transform', 'translate(' + [margin.left, margin.top] + ')')
      .attr('data-transform-x', margin.left)
      .attr('data-transform-y', margin.top)

    width = outerWidth - (layout.yAxisInnerPadding() * 2)

    xAxisProvider
      .orientation(HORIZONTAL)
      .position(layout.xAxisPosition())
      .range([0, width - layout.xAxisExtendRight()])
      .data(env.data)

    height = Math.round(height)
    var xAxis = gMargin.selectAppend('g.axis.axis-x')
      .translate([layout.yAxisInnerPadding(), height])
      .call(xAxisProvider, env)

    // READ THE HEIGHT of xAxis, adjust and redraw if needed
    var xAxisHeight = xAxis.node().getBoundingClientRect().height
    if (layout.orientation() === HORIZONTAL &&
        xAxisHeight > layout.xAxisMinHeight()) {
      height = Math.round(height - (xAxisHeight - layout.xAxisMinHeight()))
      xAxis.translate([layout.yAxisInnerPadding(), height])

      yAxisProvider.range([height, 0]).data(env.data)
      yAxis.call(yAxisProvider, env) // call again
    }

    // if vertical we have a different style for x-axis
    if (layout.orientation() === VERTICAL) {
      yAxisProvider.range([0, height])
      xAxis
        .translate([0, height])
        .classed('axis-secondary', true)
    }

    var draw = gMargin.selectAppend('g.draw-container')
      .translate([layout.yAxisInnerPadding(), 0])

    // draw extends onto area to avoid bounds lookup not showing
    // the true draw region bounds
    var extents = draw.selectAppend('rect.extents')
      .attr('x', 0)
      .attr('y', 0)
      .attr('width', xAxisProvider.rangeLength())
      .attr('height', yAxisProvider.rangeLength())

    layout.g(gMargin)
    layout.draw(draw)
    layout.extents(extents)
    layout.container(chart)
    layout.xAxis(xAxis)
    layout.yAxis(yAxis)
    layout.svg(svg)
    layout.margin(margin)
    layout.width(width)
    layout.outerWidth(outerWidth)
    layout.height(height)
    layout.outerHeight(outerHeight)

    env.y = yAxisProvider
    env.x = xAxisProvider
    env.g = gMargin

    layout.emitPostLayout(selection, env)
    return selection
  }

  // read width / height of components and update margins
  layout.update = function () {

  }

  // if our gMargin is larger than the svg it will cut off text and
  // whatnot so we use transform scale to bring it back to normal
  // OLD WEBKIT (in phantomjs) only supports scale origin top left
  // so we have to do manual shifting
  // TODO :: resolve 10px min for translateY
  layout.overflowScale = function () {
    const svgBounds = layout.svg().node().getBoundingClientRect()
    const gBounds = layout.g().node().getBBox()
    const margin = layout.margin()
    const minHeight = Math.max(svgBounds.height, gBounds.height)

    let translateY = Math.max(margin.top, gBounds.y * -1)
    let translateX = 0
    if (gBounds.x < 0) {
      translateX += (gBounds.x * -1)
    }

    // .attr('transform', 'translate(' + [margin.left, margin.top] + ')')
    let transform = `translate(${translateX}, ${translateY})`
    if (gBounds.width > svgBounds.width) {
      const ratio = (svgBounds.width / gBounds.width)
      transform = `translate(${translateX}, ${translateY}) scale(${ratio})`
    }

    layout.svg().style('height', `${minHeight}px`)
    layout.g()
      .style('transform-origin', 'top left')
      .style('-webkit-transform-origin', 'top left')
      .attr('transform', transform)
      .style('transform', transform)
      .style('-webkit-transform', transform)
  }

  layout.getMinHeight = () => {
    if (layout.minHeight() !== null) {
      return layout.minHeight()
    } else {
      if (layout.orientation() === HORIZONTAL) {
        return layout.minHeightHorizontal()
      } else {
        return layout.minHeightVertical()
      }
    }
  }

  layout.heightOrMin = h => {
    return Math.round(Math.max(layout.getMinHeight(), h))
  }

  // there are some things that need to be done
  // after all axes have been drawn. so that is done by adding a handler
  // to the layout manager here
  layout.addPostLayoutHandler = (callback) => {
    postLayoutHandlers.push(callback)
  }

  layout.emitPostLayout = (el, env) => {
    postLayoutHandlers.forEach(cb => {
      cb(el, env, layout)
    })
  }

  objectAccessor(layout, state)

  return layout
}
