import { select } from '../../d3-custom'
import { seriesColors, defs } from './values'
import { getOption } from '../options'

const COLOR_KEYS = [
  'color0',
  'color1',
  'color2',
  'color3',
  'color4',
  'color5',
  'color6',
  'color7',
  'color8',
  'color9',
  'colorbg',
  'colorfg',
  'colorh',
  'stroke0',
  'stroke1',
  'stroke2',
  'stroke3',
  'stroke4',
  'stroke5',
  'stroke6',
  'stroke7',
  'stroke8',
  'stroke9',
  'strokeh'
]

export const black = '#000000'
export const white = '#ffffff'
export const cyan = '#00aeff'
export const honey = '#ffc400'
export const magenta = '#ff008c'
export const mint = '#3ef794'
export const mintD1 = '#32ce7b'
export const gray2 = '#cccccc'

export const cyanD3 = '#00577f'
export const cyanD2 = '#0079b2'
export const cyanD1 = '#0094d9'
export const cyanL1 = '#44c4ff'
export const cyanL2 = '#89d9fe'
export const cyanL3 = '#cdeffe'

// temporary function to take in data length and type (bar/column)
export function createColorSchemeOld (el, data, options) {
  // TODO MOVE into lib
  const columnTypes = ['bar', 'hbar', 'stackedVbar', 'stackedHbar', 'barline', 'area']
  const noStroke = !(data.chartType === 'area')
  let styleIndex = 0
  if (data.config.series_color_style) styleIndex = Number(data.config.series_color_style)
  if (options.colorSeries) styleIndex = Number(options.colorSeries)
  let styleSet = false

  if (data.theme === 'business' && columnTypes.indexOf(data.chartType) !== -1 && !options.colorVariableSet) {
    if (data.data[0].length === 3) {
      styleSet = true
      if (styleIndex === 0) {
        options.color1 = cyan
      } else if (styleIndex === 1 && noStroke) {
        options.color1 = white // black + white
        options.stroke1 = black
      } else {
        options.color1 = honey
      }
    } else if (data.data[0].length === 2 && data.config.highlight_values) {
      styleSet = true
      if (styleIndex === 0) {
        options.colorh = cyan
      } else if (styleIndex === 1 && noStroke) {
        options.color0 = white // black + white
        options.stroke0 = black
        options.colorh = black
      } else {
        options.colorh = honey
      }
    } else if (data.data[0].length >= 4) {
      styleSet = true
      if (styleIndex === 0) {
        if (data.data[0].length === 4) {
          applySeriesColors(options, [magenta, honey])
        } else if (data.data[0].length === 5 || data.data[0].length === 6) {
          applySeriesColors(options, [magenta, cyan, honey, mint])
        }
      } else {
        // GRADIENT COLORS
        if (data.data[0].length === 4) {
          applySeriesColors(options, [cyanD2, cyan])
        } else if (data.data[0].length === 5) {
          applySeriesColors(options, [cyanD3, cyanD1, cyanL1])
        } else if (data.data[0].length > 5) {
          applySeriesColors(options, [cyanD3, cyanD1, cyan, cyanL1, cyanL2, cyanL3])
        }
      }
    }
  } else if (data.theme === 'business' && !options.colorVariableSet) {
    styleSet = true
    // lines and the rest
    if (data.data[0].length === 3) {
      if (styleIndex === 0) {
        options.color1 = cyan
      } else {
        options.color1 = honey
      }
    } else if (data.data[0].length === 4) {
      applySeriesColors(options, [magenta, honey])
    } else if (data.data[0].length === 5 || data.data[0].length === 6) {
      applySeriesColors(options, [magenta, cyan, honey, mint])
    }
  }

  // the editor will check for these and propogate up to define
  // the selection options
  options.seriesColorStyleSet = styleSet
  options.seriesColorStyles = []
}

export function createColorScheme (el, data, options) {
  const colors = seriesColors(data.theme)
  const isHighlighted = data.config.highlight_values
  let styleSet = false
  let activeColors = null
  let styleIndex = 0
  if (data.config.series_color_style) styleIndex = Number(data.config.series_color_style)
  if (options.colorSeries != null) styleIndex = Number(options.colorSeries)

  if (!options.colorVariableSet && colors && data.data[0]) {
    if (colors[data.chartType]) {
      styleSet = true
      const colorSets = colors[data.chartType]
      const length = isHighlighted ? data.data[0].length : data.data[0].length - 1
      const key = length > 6 ? 'all' : length
      activeColors = colorSets[key]
      applySeriesColors(options, activeColors[styleIndex], 0, isHighlighted)
    }
  }

  // the editor will check for these and propogate up to define
  // the selection options
  options.seriesColorStyleSet = styleSet
  options.seriesColorStyles = activeColors
  if (options.editorHooks && options.editorHooks.colorSeries) {
    options.editorHooks.colorSeries(activeColors, styleIndex)
  }
}

function applySeriesColors (options, colors, offset = 1, isHighlighted = false) {
  if (!colors) return

  const setC = (key, value) => {
    if (!options[key]) {
      options[key] = value
    }
  }

  colors.forEach((c, i) => {
    if (typeof c === 'string') {
      setC(`color${i + offset}`, c)
    } else {
      setC(`color${i + offset}`, c.color)
      setC(`stroke${i + offset}`, c.stroke)
    }

    if (isHighlighted && (i + offset) === 1) {
      setC('colorh', options[`color1`])
      if (c.stroke) {
        setC('strokeh', c.stroke)
      }
    }
  })
}

