// initial setup involves creating color scheme based on data
// and then applying it to the dom via css
export function setupColors (el, data, options) {
  createColorScheme(el, data, options)

  const colorVariant = getOption('variant_color', data, options)
  if (!colorVariant) {
    applyCustomColors(el, data, options)
  }
}

function ensureDefsInSVG (el, key) {
  if (defs[key]) {
    const id = key.replace('url(', '').replace(')', '')
    if (el.select(id).size()) return
    el.select('svg')
      .append('defs')
      .html(defs[key])
  }
}

/**
  Allow query parameters to customize the color of a chart via:
  ?color0=#fff&color1=#f0n23&colorbg=#888&colorfg=#aaa
*/
export function applyCustomColors (el, data, options) {
  el = select(el)

  COLOR_KEYS.forEach(key => {
    if (options[key]) {
      const color = options[key]
      const theme = options.theme || data.theme

      if (color.substring(0, 5) === 'url(#') {
        ensureDefsInSVG(el, color)
      }

      if (key === 'colorbg') {
        select('body.toaster-iframe, body.framed-viewer').style('background', color)
        el.style('background', color)
        el.selectAll(`.toaster-theme-${theme}`)
          .style('background', color)
        el.selectAll('.pie-container.highlighted path')
          .style('stroke', color)
        el.selectAll('.tooltip-label-box, .toaster-iframe, .logo')
          .style('background-color', color)
        el.selectAll('.tooltip-valueline')
          .style('color', color)
        el.selectAll('.tooltip-anchor, .axis-label-box > rect')
          .style('fill', color)
        el.selectAll('.annotations text')
          .style('text-shadow', `-1px -1px 0px ${color}, 
            -1px 1px 0px ${color}, 
            1px -1px 0px ${color},
            1px 1px 0px ${color},
            -2px -2px 0px ${color},
            -2px 2px 0px ${color},
            2px -2px 0px ${color},
            2px 2px 0px ${color}`)
      } else if (key === 'colorfg') {
        el.selectAll('h1, h2, a, th, .source, .logo, label, .tooltip-value, .title, .footnote, .label-bottom, .annotations-footer')
          .style('color', color)
        el.selectAll('.tooltip-gridline, .tooltip-anchor, .grid line, rect.column')
          .style('stroke', color)
        el.selectAll('text, text.column-label')
          .style('fill', color)
        el.selectAll('.axis path.domain, .axis .tick > line, .zero-line, .label line, .annotations line')
          .style('stroke', color)
        el.selectAll('.tooltip-label-box')
          .style('border-color', color)
          .style('color', color)
        el.selectAll('.swatch')
          .style('border-color', color)
      } else if (key === 'colorh') {
        el.selectAll('.highlight, .f.highlight')
          .style('fill', color)
        el.selectAll('.highlight-legend')
          .style('background-color', color)
      } else if (key === 'strokeh') {
        el.selectAll('.highlight, .f.highlight')
          .style('stroke', color)
        el.selectAll('.highlight-legend')
          .style('border-color', color)
      } else if (key.indexOf('stroke') !== -1) {
        const i = Number(key.replace('stroke', ''))
        el.selectAll(`.bg.series-${i}`)
          .style('border-style', 'solid')
          .style('border-width', '1px')
          .style('border-color', color)
        el.selectAll(`.f.series-${i}, .bg.series-${i}, .s.series-${i}, .series-${i}-stroke`)
          .style('stroke', color)
      } else {
        const i = Number(key.replace('color', ''))

        el.selectAll(`.bg.series-${i}`)
          .style('background-color', color)
        el.selectAll(`.c.series-${i}`)
          .style('color', color)
        el.selectAll(`.s.series-${i}, .series-${i}-stroke`)
          .style('stroke', color)
        el.selectAll(`.f.series-${i}, .line-area.series-${i}`)
          .style('fill', color)
      }
    }
  })
}
