import {select} from '../d3-custom'
import {
  getSeries,
  valueHighlighter,
  highlightSupported
} from '../utils/data'
import {
  arc as d3arc,
  pie as d3pie
} from 'd3-shape'

export default function simplePie (options) {
  var defaults = {
    pieInnerRadius: 0,
    pieOuterRadius: 120,
    piePadAngle: 0,
    pieSort: (a, b) => b - a
  }
  options = Object.assign(defaults, options)

  var arc = d3arc()
  var pie = d3pie()

  function simplePie (selection) {
    selection.each(function (chartData) {
      if (chartData.chartOptions && chartData.chartOptions.pie) {
        Object.assign(options, chartData.chartOptions.pie)
      }

      // TODO give this more thought and make an accessor
      var series = getSeries(chartData)[0]

      pie.padAngle(options.piePadAngle)

      if (options.sortPieSeries) {
        pie.sort(options.pieSort)
      } else {
        pie.sort(null)
      }

      arc.innerRadius(options.pieInnerRadius)
        .outerRadius(options.pieOuterRadius)

      // function to highlight a value by index
      var isHighlighted = valueHighlighter(chartData)

      var el = select(this)
        .classed('not-highlighted', !highlightSupported(chartData))
        .classed('highlighted', highlightSupported(chartData))
      var sectors = el.selectAll('path.sector')
      var data1 = pie(series.data)

      sectors = sectors.data(data1)

      sectors.exit()
        .remove()

      sectors.enter().append('path')
        .attr('d', arc)
        .merge(sectors)
        .attr('class', d => 'sector f series-' + d.index)
        .classed('highlight', d => isHighlighted(d, d.index))
    })
  }

  return simplePie
}
