import {
  select as d3select
} from 'd3'

import {
  hideOverlappingTicks,
  quarterLabelDensity,
  hideLine2Duplicates,
  drawDomain
} from './utils'

/**
  Each type of axis formatting will have it's own builder - defining the
  optimal number of ticks, applying to selection and then a postRender
  function to separate multiple lines and clean things up.
*/
export default function quarterAxis (selection, axis, env, options) {
  const field = axis.field()

  axis.model()
    .tickFormat((d) => d)

  selection.call(axis.model())

  drawDomain(selection, axis, env)

  var textItems = selection.selectAll('g.tick text')
  var firstText = d3select(textItems.nodes()[0]).text()
  var firstQuarter = field.taxonomy.valueParser(firstText)[1]
  var qOffset = firstQuarter % 2 === 0 ? 0 : 1

  textItems
    .each(function (d, i) {
      var textElement = d3select(this)
      var textVal = textElement.text()
      var yText = textElement.attr('y')
      var quarter
      var year
      var yearText
      var quarterText

      [year, quarter] = field.taxonomy.valueParser(textVal)

      quarterText = 'Q' + quarter
      if (i !== 0 && axis.axisScale.bandwidth() <= 25 &&
        axis.axisScale.bandwidth() > 15) {
        quarterText = i % 2 === qOffset ? '' : quarterText
      } else if (i !== 0 && axis.axisScale.bandwidth() <= 15) {
        quarterText = quarter === 1 ? quarterText : ''
      }

      d3select(this.parentNode).attr('quarter', quarter)
      textElement.text(null)
      textElement.append('tspan')
        .attr('x', 0)
        .attr('y', yText)
        .attr('dy', axis.axisTextMarginHorizontal() + 'px')
        .classed('line1', true)
        .text(quarterText)

      yearText = year

      textElement.append('tspan')
        .attr('x', 0)
        .attr('y', yText)
        .attr('dy', axis.axisTextMarginHorizontal() + axis.axisTextLineHeight() + 'px')
        .classed('line2', true)
        .text(yearText)

      if (quarterText === '') {
        d3select(textElement.node().parentNode).classed('hide-tick', true)
      }
    })

  hideOverlappingTicks(selection, axis)
  quarterLabelDensity(selection, axis)
  hideLine2Duplicates(selection, axis)

  return selection
}
