import { select } from '../d3-custom'
import {
  line as d3line,
  curveBasis,
  curveStep,
  curveStepAfter,
  curveStepBefore
} from 'd3-shape'
import { getFieldIndexValueParser } from '../utils/data'

const curveTypes = {
  step: curveStep,
  stepAfter: curveStepAfter,
  stepBefore: curveStepBefore,
  smooth: curveBasis
}

// this renderer is specifically for filling in the gaps of a
// chart that contains gaps
export default function dashedLine (options) {
  const xScale = options.xScale
  const yScale = options.yScale
  const seriesOffset = options.seriesOffset || 0
  const segments = options.segments

  function dashedLine (selection) {
    selection.each(function (chartData, i) {
      const el = select(this)
        .classed('simple-line', true)
        .classed('dashed-line', true)

      const xData = getFieldIndexValueParser(chartData)
      const lineGroup = el.selectAppend('g.line-group')
      const lines = lineGroup.selectAll('.dashed-line').data(segments)

      const line = d3line()
        .x((d) => xScale(xData(d.index)))
        .y((d) => yScale(d.value))

      if (options.curve && curveTypes[options.curve]) {
        line.curve(curveTypes[options.curve])
      }

      lines.exit().remove()

      lines.enter()
        .append('path')
        .attr('class', (d, i) => `dashed-line s series-${i + seriesOffset}`)
        .merge(lines)
        .attr('d', function (d) {
          let lineString = ''
          d.forEach(function (segment) {
            lineString += line(segment.data)
          })
          return lineString
        })
    })
  }

  return dashedLine
}
