// these are the "Show Date As values"
export const OUTPUT_FORMAT_YEARS = 'Yearly'
export const OUTPUT_FORMAT_QUARTERS = 'Quarterly'
export const OUTPUT_FORMAT_MONTHS = 'Monthly'
export const OUTPUT_FORMAT_DAYS = 'Daily'
export const OUTPUT_FORMAT_HOURS = 'Intraday'
export const OUTPUT_FORMAT_CATEGORICAL = 'Categorical'

export const DISPLAY_AS_PERCENT = '%'
export const DISPLAY_AS_THOUSANDS = 'K'
export const DISPLAY_AS_MILLIONS = 'M'
export const DISPLAY_AS_BILLIONS = 'B'
export const DISPLAY_AS_TRILLIONS = 'T'
export const DISPLAY_AS_MULTIPLIERS = {
  [DISPLAY_AS_PERCENT]: 1e2,
  [DISPLAY_AS_THOUSANDS]: 1e3,
  [DISPLAY_AS_MILLIONS]: 1e6,
  [DISPLAY_AS_BILLIONS]: 1e9,
  [DISPLAY_AS_TRILLIONS]: 1e12
}

export const DIVISOR_MAP = {
  100: 'Hundreds',
  1000: 'Thousands',
  1000000: 'Millions',
  1000000000: 'Billions',
  1000000000000: 'Trillions'
}

// used to determine if numeric values falling within this
// range can be treated as year values
export const NUMERIC_YEAR_MIN = 1800
export const NUMERIC_YEAR_MAX = 2200

export function isString (val) {
  return typeof val === 'string' || val instanceof String
}

export function isDefined (val) {
  if (isNaN(val)) return false
  if (val === null || val === undefined) return false
  return true
}

var regexQuarters = {
  'quarter_year': /Q[1234]\s\d/,
  'quarter_year_alt': /[1234]Q\s\d/,
  'year_quarter': /\d\sQ[1234]/,
  'year_quarter_nospace': /\dQ[1234]/,
  'quarter_year_nospace': /Q[1234]\d/,
  'quarter_year_nospace2': /[1234]Q\d/,
  'quarter_dash_year': /Q[1234]-\d/,
  'year_dash_nospace': /\dQ[1234]/
}

// FY 2017 matches new Date() so we use this to
// exclude these dates
var regexExclude = {
  'fy_year': /FY\s\d/
}

export function isExcluded (val) {
  var exclude = false
  Object.keys(regexExclude).forEach(function (key) {
    if (exclude) return
    exclude = regexExclude[key].test(val)
  })

  return exclude
}

function getQuarterRegex (val) {
  var reg = null
  Object.keys(regexQuarters).forEach(function (key) {
    if (reg) return
    if (regexQuarters[key].test(val)) reg = regexQuarters[key]
  })

  return reg
}

function isQuarter (val) {
  return (getQuarterRegex(val) !== null)
}

function testQuarter (field) {
  return !isForceCategorical(field) &&
    isQuarter(getFirstValue(field.data)) &&
    isQuarter(getLastValue(field.data))
}

// when forceUnitDisplay is set to "Trillions" we take values like
// 600B and convert to display as 0.6T
function forceUnitDisplay (val, precision, options) {
  if (options.forceUnitDisplay === 'Thousands') {
    val = val / 1000
    return d3format('.' + precision + 'f')(val) + 'K'
  } else if (options.forceUnitDisplay === 'Millions') {
    val = val / 1000000
    return d3format('.' + precision + 'f')(val) + 'M'
  } else if (options.forceUnitDisplay === 'Billions') {
    val = val / 1000000000
    return d3format('.' + precision + 'f')(val) + 'B'
  } else if (options.forceUnitDisplay === 'Trillions') {
    val = val / 1000000000000
    return d3format('.' + precision + 'f')(val) + 'T'
  }
}

// generic formatting for a number
function smallNumber (precision) {
  if (precision == null) precision = 2
  return val => Number(numberOrNull(val)).toFixed(precision)
}

function prettyNumber (precision, options) {
  if (precision == null) precision = 0
  return function (val) {
    val = Number(trimNumber(val))
    if (isNaN(val)) return ''
    if (val === 0) return 0

    // precision
    var currentPrecision = precision
    if (options.displayAsMultiplier &&
        Math.abs(val) > 100 &&
        Math.abs(val) < options.displayAsMultiplier) {
      currentPrecision = Math.max(0, precision - 1)
    }

    if (options.forceCommaDisplay) {
      return d3format(',.' + currentPrecision + 'f')(val)
    }

    if (Math.abs(val) < 10) {
      return d3format('.' + currentPrecision + 'f')(val)
    }

    // replace scientific "G" (Giga) with "B"
    if (options.forceUnitDisplay) {
      val = forceUnitDisplay(val, currentPrecision, options)
    } else {
      val = d3format('s')(val).replace('G', 'B')
    }

    if (options.lowerCaseUnits) {
      val = val.toLowerCase()
    } else {
      // treat k as a special case of uppercase units
      val = val.toUpperCase()
    }

    // strip out non character part, and apply precisionf float
    var numericVal = val.replace(/[^\d.-]/g, '')
    var numericFormatted = d3format('.' + currentPrecision + 'f')(Number(numericVal))
    return val.replace(numericVal, numericFormatted)
  }
}

// this is meant for tick text elements so we return strings
// wrapped in tspans
export function multilineDateFormatter (formatters, parser) {
  parser = parser || parseDate
  return val => {
    return formatters
      .map(f => f(parser(val)))
      .join(LINE_SEPARATOR)
  }
}

export function dateFormatter (formatter, parser) {
  parser = parser || parseDate
  return val => {
    return parser(val).format(formatter)
  }
}

function percentageShift (formatter) {
  return val => {
    return formatter(val * 100.0)
  }
}

function valueMultiplier (formatter, multiplier) {
  return val => {
    return formatter(val * multiplier)
  }
}

// returns Q1, Q2 etc. for any date
export function formatDateAsQuarter (val) {
  var d = parseDate(val)
  if (d.valueOf()) {
    return 'Q' + (Math.floor((d.format('M') - 1) / 3) + 1)
  } else {
    return ''
  }
}

export function formatDateAsQuarterYear (val) {
  var d = parseDate(val)
  if (d.valueOf()) {
    var q = (Math.floor((d.format('M') - 1) / 3) + 1)
    if (q === 1) {
      return 'Q' + q + ' ' + d.format('YYYY')
    }
    return 'Q' + q
  } else {
    return ''
  }
}

// Default value formatter
export function valueFormatter (axis, env, options) {
  options = options || {}
  var field = axis.field()
  var parser = fieldDateParser(field)
  var tx = field.taxonomy
  var precision = null
  var noformat = val => {
    return val
  }
  var formatter = noformat
  var pre = field['unit_before'] || field['label-left'] || ''
  var post = field['unit_after'] || field['label-right'] || ''
  var prepostLength = pre.length + post.length
  let customFormatter

  // passed in with axis prepare options
  if (options.hasOwnProperty('defaultDecimals')) {
    precision = options.defaultDecimals
  }

  if (!options.hasOwnProperty('forceCommaDisplay')) {
    options.forceCommaDisplay = env.options.forceCommaDisplay || env.data.config.forceCommaDisplay
  }

  if (!options.voidDecimals && field.hasOwnProperty('defaultDecimals') &&
      field.defaultDecimals >= precision) { // this is so field.defaultDecimals overrides options.defaultDecimals
    precision = field.defaultDecimals
  }

  if (env.data.chartOptions && env.data.chartOptions.decimalPlaces != null) {
    precision = env.data.chartOptions.decimalPlaces
  }

  if (options.isTooltip && env.data.chartOptions &&
      env.data.chartOptions.tooltipDecimals != null) {
    precision = env.data.chartOptions.tooltipDecimals
  } else if (options.isTooltip) {
    // special case for small decimal ranges
    const d = axis.scale.domain()
    if (Math.abs(d[1] - d[0]) < 0.05) {
      precision = 3
    } else if (Math.abs(d[1] - d[0]) < 0.1) {
      precision = 2
    }
  }

  // on tooltip drop post if too long {
  if (options.isTooltip) {
    if (post.length > 4) post = ''
    if (pre.length > 4) pre = ''
  }

  // allow for a custom formatter
  if (options.isTooltip && options.customTooltipFormatter != null) {
    customFormatter = options.customTooltipFormatter
  }

  if (options.lowerCaseUnits == null) {
    options.lowerCaseUnits = env.data.config.lower_case_units
  }

  if (tx && tx.isContinuous) {
    if (tx.isDate) {
      if (tx.duration && DURATION_FORMATTERS[tx.duration]) {
        let durationFormatter = DURATION_FORMATTERS[tx.duration]
        if (env.data.config && env.data.config.hide_year_display &&
            DURATION_FORMATTERS_WITH_YEAR.indexOf(tx.duration) !== -1) {
          durationFormatter = DURATION_FORMATTER_MD
        }
        formatter = dateFormatter(durationFormatter, parser)
        pre = '' // delete extra label -- was causing a bug
        post = ''
        prepostLength = 0

        if (tx.isYears) {
          formatter = dateFormatter(DURATION_FORMATTER_YEAR, parser)
        }
      }
      if (field.type === 'year') formatter = noformat
    } else {
      if (appliedDomainRange(field, axis) < 1) {
        if (field.display_as) {
          if (precision == null) precision = 1
          formatter = prettyNumber(precision, options)
        } else {
          formatter = smallNumber(precision)
        }
      } else if (appliedDomainRange(field, axis) < 10) {
        if (options.forAxis || field.display_as) {
          if (precision == null) precision = 0
          formatter = prettyNumber(precision, options)
        } else {
          formatter = smallNumber(precision)
        }
      } else {
        if (appliedDomainRange(field, axis) < 1000 && !options.preventCommaDisplay) {
          options.forceCommaDisplay = true
        }
        formatter = prettyNumber(precision, options)
      }
    }
  }

  formatter = customFormatter || formatter || noformat

  // wrap formatter in a function that multiplies value by 100
  if (field.display_as === '%') {
    formatter = percentageShift(formatter)
  } else if (field.display_as) {
    if (post.toUpperCase() === field.display_as) post = ''
    if (!options.ignoreDisplayAsMultiplier) {
      options.displayAsMultiplier = displayAsMultiplier(field)
      formatter = valueMultiplier(formatter, options.displayAsMultiplier)
    }
  }

  return function (val) {
    var fVal = formatter(val)
    if (options.bareValueSingleChar && prepostLength === 1) {
      return pre + fVal + post
    }
    if (options.bareValue || fVal === '') {
      return fVal
    }
    return pre + fVal + post
  }
}

// NOTE -- this is actually used for less than 2 chars
// TODO :: change to count only post label
export function isSingleCharLabel (field) {
  var label = field.label || field.caption || ''
  var pre = field['unit_before'] || field['label-left'] || ''
  var post = field['unit_after'] || field['label-right'] || ''
  var prepostLength = label.length + pre.length + post.length

  return (prepostLength <= 3)
}

// one of the major problems that arises is tick values looking off
// because of different unit displays: for example with one dataset
// we get:  2T 2T 1T 1T 600B 200B  as axis values
// we want to format all values to Trillion (so 0.6T instead of 600B)
// as well as provide a default decimal place with repeating vals
//
// special case for thousands: keep 1,000 when values below 1000
export function prepareFormatterOptions (ticks, options, axis, env) {
  options = options || {
    defaultDecimals: 1
  }
  options.forAxis = true

  // var valMultiplier = displayAsMultiplier(axis.field())
  ticks = ticks.filter(v => v !== 0)

  const reduced = reduceNumericValues(ticks)
  if (reduced) {
    const multipliers = ticks.map(v => v / reduced.divisor)
    // if any multipliers are over 1000 that means there is a split
    if (reduced.divisor === 1000 ||
      multipliers.filter(v => Math.abs(v) >= 1000).length) {
      // special case for 1000s force 1,000 etc.
      // only when multiple 100s and less than 10k
      if (reduced.divisor === 1000 &&
          Math.abs(multipliers[1]) < 1) {
        options.forceCommaDisplay = true
      } else {
        options.preventCommaDisplay = true
      }
    }

    // this prevents 1M and 500k in the same axis
    let multiplier = 1
    if (multipliers.filter(v => Math.abs(v) >= 1000).length) {
      multiplier = 1000
    } else if (multipliers.filter(v => Math.abs(v) >= 100).length) {
      multiplier = 100
    } else if (multipliers.filter(v => Math.abs(v) >= 10).length) {
      multiplier = 10
    }
    options.forceUnitDisplay = DIVISOR_MAP[reduced.divisor * multiplier]
  } else {
    // special case for small decimal ranges
    const d = axis.scale.domain()
    if (Math.abs(d[1] - d[0]) < 0.05) {
      options.defaultDecimals = 3
    } else if (Math.abs(d[1] - d[0]) < 0.1) {
      options.defaultDecimals = 2
    }
  }

  // now that we've defined the options pass them through the
  // formatter and decide if we need to bump up decimals
  let foundDecimals = false
  let count = 0
  options.ignoreDisplayAsMultiplier = true
  while (!foundDecimals && count < 3) {
    const formatter = valueFormatter(axis, env, options)
    const vals = ticks.map(v => formatter(v))
    if (!hasDuplicates(vals)) {
      foundDecimals = true
    } else {
      options.defaultDecimals = options.defaultDecimals || 0
      options.defaultDecimals += 1
    }
    count += 1
  }

  // reset because we only need it for formatting calculations
  options.ignoreDisplayAsMultiplier = false
  return options
}

// provide a wrapper for a common use case where only the first
// value in a series should have the full unit labeling
export function singleUseFormatter (formatter, altFormatter) {
  let hasPrintedValue = false
  return val => {
    if (hasPrintedValue) return altFormatter(val)
    val = formatter(val)
    if (val) hasPrintedValue = true
    return val
  }
}

// keep nulls in tact but
export function numberOrNull (val) {
  if (val === null) return null
  let v = Number(val)
  if (isNaN(v)) return null
  return v
}

// trim whitespace from a number
export function trimNumber (val) {
  if (val === null) return null
  return val.toString()
    .replace(/\s/g, '')
    .replace(/,/g, '')
}

// convert vals like 400.0M to 400000000
export function convertDisplayToValue (val) {
  if (typeof val === 'string') {
    val = val.toUpperCase()
      .replace('T', 'x' + 1e12)
      .replace('B', 'x' + 1e9)
      .replace('M', 'x' + 1e6)
      .replace('K', 'x' + 1e3)

    return val.split('x').reduce((a, b) => a * b)
  }

  return val
}

export function hasDuplicates (array) {
  var valuesSoFar = Object.create(null)
  for (var i = 0; i < array.length; ++i) {
    var value = array[i]
    if (value in valuesSoFar) {
      return true
    }
    valuesSoFar[value] = true
  }
  return false
}

// applied domain includes the field "display numerical value as"
// multiplied by the domain
export function appliedDomain (field, axis) {
  var domain = [field.min, field.max]
  if (axis && axis.scale) domain = axis.scale.domain()

  return domain.map(v => v * displayAsMultiplier(field))
}

export function appliedDomainRange (field, axis) {
  var domain = appliedDomain(field, axis)
  return Math.abs(domain[1] - domain[0])
}

// ensure [min, max] value
export function sortedDomain (domain) {
  if (domain[0] <= domain[1]) return domain
  return [domain[1], domain[0]]
}

export function displayAsMultiplier (field) {
  if (field['display_as'] && DISPLAY_AS_MULTIPLIERS[field['display_as']]) {
    return DISPLAY_AS_MULTIPLIERS[field['display_as']]
  }
  return 1.0
}

export function hasCustomDomain (chartData) {
  if (chartData.chartOptions && (
    chartData.chartOptions.scaleStart != null ||
    chartData.chartOptions.scaleEnd != null ||
    chartData.chartOptions.scaleStep != null)) {
    return true
  }
  return false
}

export function hasCustomDomainStep (chartData) {
  if (chartData.chartOptions &&
    chartData.chartOptions.scaleStep != null) {
    return true
  }
  return false
}

// return a function that parses [year, quarter]
function buildValueParser (initialValue) {
  var reg = getQuarterRegex(initialValue)

  return function (val) {
    if (val === null) return val
    if (!val.split) return val  // make sure it's a string

    var year, quarter

    if (reg === regexQuarters['quarter_year'] ||
      reg === regexQuarters['quarter_year_alt']) {
      [quarter, year] = val.split(' ')
    } else if (reg === regexQuarters['quarter_dash_year']) {
      [quarter, year] = val.split('-')
    } else if (reg === regexQuarters['quarter_year_nospace']) {
      quarter = val.slice(1, 2)
      year = val.slice(2)
    } else if (reg === regexQuarters['quarter_year_nospace2']) {
      [quarter, year] = val.replace(/\s|-|'/g, '').split('Q')
    } else if (reg === regexQuarters['year_quarter'] ||
      reg === regexQuarters['year_quarter_nospace'] ||
      reg === regexQuarters['year_dash_quarter']) {
      [year, quarter] = val.replace(/\s|-|'/g, '').split('Q')
    }

    // filter out Q and '
    year = year || ''
    quarter = quarter || ''
    year = year.replace('\'', '')
    quarter = quarter.replace('Q', '')

    // NOTE:: be sure to fix this naive method if still running in 2050
    if (year.length === 2) {
      year = Number(year) < 50 ? ('20' + year) : ('19' + year)
    }

    return [Number(year), Number(quarter)]
  }
}

// try to determine if a value is of type date
export function isDate (val) {
  return (parseDate(val, null, true) || getCustomDateFormat(val) !== null) && !isExcluded(val)
}

export function testDate (field) {
  return !isForceCategorical(field) &&
    !isFieldStringYear(field) &&
    !isFieldStringMonth(field) &&
    isDate(getFirstValue(field.data)) &&
    isDate(getLastValue(field.data))
}

export function isForceCategorical (field) {
  if (field.taxonomy && field.taxonomy.forceCategorical) return true
  return false
}

export function applyCategoricalAxis (field, chartData) {
  if (chartData.chartOptions &&
      (chartData.chartOptions.dateOutputFormat === OUTPUT_FORMAT_CATEGORICAL ||
      chartData.chartOptions.isCategorical)) {
    return true
  }

  return false
}

// returns a function that reads the chartOptions hightlightValue
// field to hightlight bars, values etc.
export function valueHighlighter (chartData, options) {
  options = options || {}
  var highlight = []
  if (chartData.config && chartData.config.highlight_values) {
    highlight = String(chartData.config.highlight_values)
      .split(',')
      .map(v => Number(v) - 1)
  }

  if (!highlight.length && options.zeroHighlight) {
    highlight = [0]
  }

  return (d, i) => {
    return (highlight.indexOf(i) !== -1)
  }
}

// returns whether any values are highlighted
export function highlightSupported (chartData) {
  return chartData.config && chartData.config.highlight_values
}

// this format preserves the value as a UTC string as long as it is
// consumed by moment and _NOT_ new Date()
export function dateToUTCString (d, field) {
  const val = fieldDateParser(field)(d)
  if (val.isValid()) return val.format()
  return ''
}

// find obscure date formats that do not parse as dates
// normally ie "Dec '17"
export function getCustomDateFormat (str, formats) {
  let formatter = null
  formats = formats || DATE_INPUT_PARSERS
  formats.forEach(f => {
    const d = moment.utc(str, f)
    if (d.isValid() && str.toUpperCase() === d.format(f).toUpperCase()) {
      formatter = f
    }
  })

  return formatter
}

// return a function that parses a value based on the field taxonomy
// for now its only date values we need to parse
export function fieldValueParser (field, forceCategorical) {
  if (field && field.taxonomy && field.taxonomy.isContinuous && field.taxonomy.isDate && !forceCategorical) {
    return fieldDateParser(field)
  } else {
    return v => v
  }
}

// bind field to parseDate method
export function fieldDateParser (field) {
  if (field && field.taxonomy && field.taxonomy.dateParser) {
    return field.taxonomy.dateParser
  } else {
    return val => parseDate(val, field)
  }
}

export function parseDate (val, field, testDate) {
  let timeZoneOffset = 0
  if (field && field.taxonomy) {
    timeZoneOffset = field.taxonomy.timeZoneOffset || 0
  }

  // if customFormatter is supplied on the field we use that
  if (field && field.taxonomy && field.taxonomy.customFormatter) {
    const customDate = moment
      .utc(val, field.taxonomy.customFormatter)
      .utcOffset(timeZoneOffset)
    if (customDate.isValid()) return customDate
  }

  // first pass is through moment directly - this will keep UTC strings as
  // is rather than passing them through Date constructor that offsets by
  // local timezone
  let momentDate = moment.utc(val).utcOffset(timeZoneOffset)
  if (momentDate.isValid()) return momentDate

  // special case for years because utc("2050") fails
  // using Jan 2nd because future years have offset more than 1 day
  if (val && val.length && val.length === 4) {
    momentDate = moment.utc('1/2/' + val).utcOffset(timeZoneOffset)
    if (momentDate.isValid()) return momentDate
  }

  // second pass casts through Date() we have to manually offset timezone
  // here, only for positiveOffsets (negative timezone shift like NYC)
  let dateVal = new Date(val)
  if (dateVal.getTimezoneOffset() > 0) {
    dateVal = new Date(dateVal.getTime() + dateVal.getTimezoneOffset() * MS_IN_MINUTE)
  }
  if (!isNaN(dateVal.valueOf())) return moment.utc(dateVal).utcOffset(timeZoneOffset)

  // charts shouldn't break if a single date breaks
  if (testDate) return null
  return moment.utc(new Date('1/1/1900'))
}

// given a value parser return a function that returns date objects
// instead of [year, quarter] tuples
export function quarterlyDateParser (valueParser) {
  return val => {
    var [year, quarter] = valueParser(val)
    return parseDate(Date.UTC(year, 1 + (3 * (quarter - 1)), 1))
  }
}

export function formatNumber () { }

// does d3 have an inverse interpolate function? can't find one
export function normalizeValue (value, domain) {
  return (value - domain[0]) / (domain[1] - domain[0])
}

// get series from chartData
// TODO:: allow chartData to specify series breakdown other than default
export function getSeries (chartData) {
  return chartData.fields.length > 1 ? chartData.fields.slice(1) : chartData.fields
}

// get field from chartData
// currently only grabs x or y axis
export function getField (chartData, isPrimaryAxis = true) {
  if (isPrimaryAxis) {
    return chartData.fields[0]
  } else {
    return chartData.fields[1]
  }
}

// get field from chartData
// currently only grabs x or y axis
export function getFieldValueParser (chartData, isPrimaryAxis = true, forceCategorical = false) {
  if (isPrimaryAxis) {
    return fieldValueParser(chartData.fields[0], forceCategorical)
  }

  return fieldValueParser(chartData.fields[1], forceCategorical)
}

// convenience function for mapping an index to a parsed value
export function getFieldIndexValueParser (chartData, isPrimaryAxis = true, forceCategorical = false) {
  const field = isPrimaryAxis ? chartData.fields[0] : chartData.fields[1]
  const parser = fieldValueParser(field, forceCategorical)

  return (index) => {
    return parser(field.data[index])
  }
}

// in case the array is sparse we can't just grab the first index
// include index will return the index number as well
export function getFirstValue (values, from, includeIndex) {
  let firstValue
  let foundIndex
  const fromIndex = from || 0

  for (var i = fromIndex; i < values.length; i++) {
    if (valueOrNull(values[i]) != null) {
      firstValue = values[i]
      foundIndex = i
      break
    }
  }

  if (includeIndex) return [foundIndex, firstValue]
  return firstValue
}

export function getLastValue (values, from, includeIndex) {
  let lastValue
  let foundIndex
  const fromIndex = from || values.length - 1

  for (var i = fromIndex; i >= 0; i--) {
    if (valueOrNull(values[i]) != null) {
      lastValue = values[i]
      foundIndex = i
      break
    }
  }

  if (includeIndex) return [foundIndex, lastValue]
  return lastValue
}

// run through a list of dates to determine the timespan
// used for determining the best axis formatting
export function dateRange (field, fieldIndex) {
  // we could loop through all values, but for now just use first / last
  var parser = fieldDateParser(field)
  var start = parser(String(getFirstValue(field.data))).toDate()
  var end = parser(String(getLastValue(field.data))).toDate()
  var numDays = Math.abs(end - start) / MS_IN_DAY

  field.taxonomy.dateStart = start
  field.taxonomy.dateEnd = end

  if (numDays < 2) {
    return DURATION_HOURS
  } else if (numDays >= 2 && numDays < 16) {
    return DURATION_DAYS
  } else if (numDays >= 14 && numDays < 120) {
    return DURATION_WEEKS
  } else if (numDays >= 120 && numDays < 365 * 2) {
    return DURATION_MONTHS
  } else if (numDays >= 365 * 2 && numDays < 365 * 5) {
    return DURATION_MULTIYEAR
  } else if (numDays >= 365 * 5 && numDays < 365 * 15) {
    return DURATION_DECADE
  } else if (numDays >= 365 * 15 && numDays < 365 * 100) {
    return DURATION_MULTIDECADE
  } else {
    return DURATION_CENTURY
  }
}

export function quarterDateRange (field, valueParser) {
  var start = valueParser(String(getFirstValue(field.data)))
  var end = valueParser(String(getLastValue(field.data)))
  var numDays = Math.abs(end - start) / MS_IN_DAY

  // if it's more than 5 years just show it as years not quarters
  if (numDays >= 365 * 5) return DURATION_DECADE

  return DURATION_QUARTERS
}

// Data Taxonomy
// Function to read chartData and try to determine the
// field types, possible chart types, etc.
//
// save a taxonomy config object back to chartData so we
// can allow an editor to specify fields directly.
// TODO:: merge with makeDataGreatAgain
export function buildTaxonomy (chartData, forceRebuild) {
  if (chartData.taxonomy && !forceRebuild) return

  chartData.config = chartData.config || {}
  chartData.chartOptions = chartData.chartOptions || {}
  const tzOffset = (chartData.config.time_zone || 0) * 60

  var taxonomy = {
    version: '0.1' // taxonomy schema
  }

  // because the IDs differ between the axes and the fields, we need to stash them...
  const ids = chartData.fields.map(field => field.id)

  if (chartData.primaryAxis && chartData.fields[0]) {
    Object.assign(chartData.fields[0], chartData.primaryAxis)
  }

  if (chartData.secondaryAxis && chartData.fields[1]) {
    Object.assign(chartData.fields[1], chartData.secondaryAxis)
  }

  // .. and restore the field ids later
  chartData.fields.forEach((field, i) => { field.id = ids[i] })

  chartData.fields.forEach(function (field, i) {
    var fieldTaxonomy = {
      isContinuous: true
    }
    field.taxonomy = field.taxonomy || fieldTaxonomy

    // handle the case of year values being exported as numbers from
    // excel by converting to strings locally
    if (i === 0 && field.type === FIELD_TYPE_NUMERIC && isFieldNumericYear(field)) {
      field.type = FIELD_TYPE_STRING
      fieldTaxonomy.isYears = true
      castNumericToString(field, chartData)
    }

    // i === 0 because we only want this test on Categorical axis
    if (i === 0 && field.type === FIELD_TYPE_STRING) {
      // if a date format is specified, we convert to time stamps
      if (chartData.chartOptions.dateInputFormat) {
        field.taxonomy.customFormatter = chartData.chartOptions.dateInputFormat
      }

      fieldTaxonomy.forceCategorical = applyCategoricalAxis(field, chartData)

      if (testDate(field)) {
        fieldTaxonomy.isDate = true
        fieldTaxonomy.customFormatter = getCustomDateFormat(getFirstValue(field.data))
        fieldTaxonomy.isContinuous = true
        fieldTaxonomy.duration = dateRange(field)
        fieldTaxonomy.timeZoneOffset = tzOffset
      } else if (testQuarter(field)) {
        fieldTaxonomy.valueParser = buildValueParser(getFirstValue(field.data))
        fieldTaxonomy.dateParser = quarterlyDateParser(fieldTaxonomy.valueParser)
        fieldTaxonomy.isQuarters = true
        fieldTaxonomy.isDate = true
        fieldTaxonomy.isContinuous = false
        fieldTaxonomy.duration = quarterDateRange(field, fieldTaxonomy.valueParser)
        fieldTaxonomy.timeZoneOffset = tzOffset
      } else {
        fieldTaxonomy.isContinuous = false
        if (isFieldStringYear(field)) {
          fieldTaxonomy.isDate = fieldTaxonomy.isYears = true
          fieldTaxonomy.customFormatter = 'YYYY'
          fieldTaxonomy.duration = dateRange(field)
          fieldTaxonomy.timeZoneOffset = tzOffset
        } else if (isFieldStringMonth(field)) {
          fieldTaxonomy.isContinuous = true
          fieldTaxonomy.isDate = fieldTaxonomy.isMonths = true
          fieldTaxonomy.noYears = true // months have no Year val attaached
          fieldTaxonomy.customFormatter = getCustomDateFormat(
            getFirstValue(field.data),
            DATE_INPUT_MONTH
          )
          fieldTaxonomy.duration = dateRange(field)
          fieldTaxonomy.timeZoneOffset = tzOffset
        }
      }
    }

    // attempt to read a default decimal place from the data
    if (field.type === FIELD_TYPE_NUMERIC) {
      field.defaultDecimals = readDecimalsFromData(field)
    }

    if (field.type === FIELD_TYPE_JUMBLE) {
      fieldTaxonomy.isContinuous = false
    }
  })

  // if primary axis is date and reversed, sort it
  if (!chartData.config.disable_date_sorting &&
    chartData.fields[0] &&
    chartData.fields[0].taxonomy &&
    chartData.fields[0].taxonomy.dateStart &&
    chartData.fields[0].taxonomy.dateEnd) {
    if (chartData.fields[0].taxonomy.dateEnd <
      chartData.fields[0].taxonomy.dateStart) {
      sortAllFields(chartData, { sortPrimaryAxisTime: true, reverse: true })
      var swap = chartData.fields[0].taxonomy.dateEnd
      chartData.fields[0].taxonomy.dateEnd =
        chartData.fields[0].taxonomy.dateStart
      chartData.fields[0].taxonomy.dateStart = swap
    }
  }

  chartData['taxonomy'] = taxonomy
}

// get number of years between two dates (as array)
// first check field for min / max convenience method for a duratoin()
export function getDurationYears (field, dateArray) {
  if (field.min && field.max) {
    return Math.abs(field.max - field.min)
  }
  return Math.abs(dateArray[1].getUTCFullYear() - dateArray[0].getUTCFullYear())
}

// given [100000 200000] return {divisor: 1000, label: "k"}
export function reduceNumericValues (values) {
  // first strip out any 0 values
  values = values.filter(function (v) { return v !== 0 })

  if (values.filter(function (v) { return v % 1e12 === 0 }).length === values.length) {
    return {divisor: 1e12, short: 'T', long: 'Trillions'}
  } else if (values.filter(function (v) { return v % 1e9 === 0 }).length === values.length) {
    return {divisor: 1e9, short: 'B', long: 'Billions'}
  } else if (values.filter(function (v) { return v % 1e6 === 0 }).length === values.length) {
    return {divisor: 1e6, short: 'M', long: 'Millions'}
  } else if (values.filter(function (v) { return v % 1e3 === 0 }).length === values.length) {
    return {divisor: 1e3, short: 'k', long: 'Thousands'}
  } else if (values.filter(function (v) { return v % 1e2 === 0 }).length === values.length) {
    return {divisor: 1e3, short: 'k', long: 'Thousands'}
  }
  return null
}

export function seriesDomain (series, domainFromZero) {
  var domain = extent(merge(series.map(function (d) {
    // used cached min/max if available to speed things up
    if (typeof d.min !== 'undefined' &&
      typeof d.max !== 'undefined') {
      return [d.min, d.max]
    } else {
      var tmpExtent = extent(d.data)
      d.min = tmpExtent[0]
      d.max = tmpExtent[1]
      return tmpExtent
    }
  })))

  // needed for column charts
  if (domainFromZero) {
    domain[0] = Math.min(0, domain[0])
    domain[1] = Math.max(0, domain[1])
  }

  // cast strings to numbers
  domain[0] = Number(domain[0])
  domain[1] = Number(domain[1])

  return domain
}

export function stackedDomain (chartData) {
  var minMax = [0, 0]

  chartData.data.forEach((d, i) => {
    var numbers = d.slice(1)
      .map(v => Number(v))

    var positiveSum = numbers
      .filter(v => v >= 0)
      .reduce((a, b) => a + b, 0)

    var negativeSum = numbers
      .filter(v => v < 0)
      .reduce((a, b) => a + b, 0)

    minMax[0] = Math.min(negativeSum, minMax[0])
    minMax[1] = Math.max(positiveSum, minMax[1])
  })

  return minMax
}

// given a list of data representing a single data point
// provide the sum
export function stackedSum (data) {
  let total = data.reduce((sum, val) => {
    if (val == null) return sum
    return val > 0 ? sum + val : sum
  }, 0)

  // only read negative stack if sum is zero
  if (total === 0) {
    total = data.reduce((sum, val) => {
      if (val == null) return sum
      return val > 0 ? sum : sum + val
    }, 0)
  }

  return total
}

// need a way to read the index of a value in a field
// while casting to type "string" / "number" / "date"
// get rid of newline and ↵ characters
export function fieldIndexOf (field, value) {
  const parser = fieldDateParser(field)
  const firstValue = getFirstValue(field.data)
  const firstValueType = typeof firstValue
  let index = -1

  if (firstValueType === 'string') {
    index = field.data.indexOf(String(value))
  } else if (firstValueType === 'number') {
    index = field.data.indexOf(Number(value))
  } else if (firstValueType === 'object' && firstValue.getDate) {
    index = field.data.indexOf(parser(value))
  } else {
    index = field.data.indexOf(value)
  }

  if (index === -1) {
    // if we haven't found the date, it might be alredy cast to Date()
    if (field.taxonomy.isDate) {
      const compareDate = parser(value)

      field.data.forEach((d, i) => {
        if (parser(d).diff(compareDate) === 0) {
          index = i
        }
      })
    } else {
      // if field is categorical try and do string compare
      // to get rid of whitespace
      const compareVal = value.replace(/\s|↵|\n/g, '')
      field.data.forEach((d, i) => {
        if (d.replace(/\s|↵|\n/g, '') === compareVal) {
          index = i
        }
      })
    }
  }

  return index
}

// does the numeric field fall within the expected range for years
export function isFieldNumericYear (field) {
  return (field.min > NUMERIC_YEAR_MIN && field.max < NUMERIC_YEAR_MAX)
}

// can we convert first and last values to numbers and do they
// fall within the year bounds
export function isFieldStringYear (field) {
  const testString = (str) => {
    return str.length === 4 &&
      !isNaN(Number(str)) &&
      Number(str) > NUMERIC_YEAR_MIN &&
      Number(str) < NUMERIC_YEAR_MAX
  }

  return testString(getFirstValue(field.data)) &&
    testString(getLastValue(field.data))
}

// check if the values are "months" like "Jan" "January" etc.
export function isFieldStringMonth (field) {
  const testString = (str) => {
    return getCustomDateFormat(str, DATE_INPUT_MONTH) !== null
  }

  return testString(getFirstValue(field.data)) &&
    testString(getLastValue(field.data))
}

export function castNumericToString (field, chartData) {
  field.data = field.data.map(n => {
    if (n == null) return null
    return String(n)
  })

  chartData.data.forEach((d, i) => {
    d[0] = field.data[i]
  })
}

// changing this to use the minimum decimal display
export function readDecimalsFromData (field) {
  const data = field.data
  let decimalPlaces = null
  var places = num => {
    if (!num || !num.split) num = String(num)
    return (num.split('.')[1] || []).length
  }

  for (var i = 0; i < data.length; i++) {
    if (data[i] === null) continue
    var currentPlaces = Math.min(places(data[i]), 3) // max 3

    if (decimalPlaces === null) {
      decimalPlaces = currentPlaces
    } else {
      if (decimalPlaces !== currentPlaces) {
        if (currentPlaces < decimalPlaces && currentPlaces !== 0) {
          decimalPlaces = currentPlaces
        }
      }
    }
  }

  if (appliedDomainRange(field) > 5) {
    decimalPlaces = Math.min(decimalPlaces, 2)
  }

  if (appliedDomainRange(field) > 10) {
    decimalPlaces = Math.min(decimalPlaces, 1)
  }

  return decimalPlaces
}

function camelCase (inStr) {
  return inStr.replace(/_([a-z])/g, function (g) {
    return g[1].toUpperCase()
  })
}

export function makeData (chartData) {
  if (chartData['_makeDataExecuted']) {
    return true // prevent multiple execution
  }

  if (!chartData.data || !chartData.data.length) {
    console.warn(chartData.id, 'has no data')
    return false
  }

  if (!chartData.fields || !chartData.fields.length) {
    console.warn(chartData.id, 'has no fields')
    return false
  }

  if (chartData.fields.length < chartData.data[0].length) {
    console.warn(chartData.id, 'Fields Not properly defined, fill with stubs')

    for (var n = 0; n <= chartData.data[0].length - chartData.fields.length; n++) {
      chartData.fields.push({name: ''}) // empty field
    }
  }

  // normalize the fields properties by changing the underscores to camelCase
  var i, j
  for (i = 0; i < chartData.fields.length; ++i) {
    var props = Object.keys(chartData.fields[i])
    for (j = 0; j < props.length; ++j) {
      var prop = props[j]
      if (prop.indexOf('_') >= 0) {
        chartData.fields[i][camelCase(prop)] = chartData.fields[i][prop]
        // delete chartData.fields[i][prop];
      }
    }
  }

  chartData.fields.forEach(function (field, i) {
    field.data = chartData.data.map(function (d) {
      return d[i]
    })

    guessFieldType(field)

    if (field.type !== FIELD_TYPE_STRING) {
      // strip out any NaN values
      var domainData = field.data.filter(v => !isNaN(Number(v)) && v !== null)
      field.min = Math.min.apply(null, domainData)
      field.max = Math.max.apply(null, domainData)
    }
  })

  chartData['_makeDataExecuted'] = true

  return true
}

// in the makeData operation remove the empty rows
function nullFilter (v) {
  if (v == null || v === 'na') {
    return false
  }
  return true
}

function guessFieldType (field) {
  // we'll trust the client assuming it's a valid type
  if (field.type && FIELD_TYPES_DEFINED.indexOf(field.type) >= 0) {
    return field
  }

  // Can we figure out the type of the displayFormat
  if (field.displayFormat && DISPLAY_FORMAT_TO_TYPE.hasOwnProperty(field.displayFormat)) {
    field.type = DISPLAY_FORMAT_TO_TYPE[field.displayFormat]
    return field
  }

  var types = {}
  field.data.filter(nullFilter).forEach(function (d) {
    types[guessType(d)] = null
  })
  types = Object.keys(types).sort()

  if (types.length === 2) {
    if (types.indexOf(FIELD_TYPE_NUMERIC) !== -1) {
      if (types.indexOf(FIELD_TYPE_YEAR) !== -1) {
        field.type = FIELD_TYPE_NUMERIC
        return field
      } else if (types.indexOf(FIELD_TYPE_CHANGE) !== -1) {
        field.type = FIELD_TYPE_NUMERIC
        field.displayChange = true
        return field
      }
    }
  }

  field.type = types.length > 1 ? FIELD_TYPE_JUMBLE : types[0]
  return field
}

// var dateLike = /^\d\d[/-]\d\d[/-]\d\d\d\d$/
var changeLike = /^\s*\+\d*\.?\d+\s*$/
var numberLike = /^\s*[+-]?\d*\.?\d+\s*$/

function guessType (d) {
  // disabling date-like as our taxonomy is based on strings
  // if (dateLike.exec(d)) return 'date'
  if (changeLike.exec(d)) return FIELD_TYPE_CHANGE
  if (numberLike.exec(d)) return FIELD_TYPE_NUMERIC
  return FIELD_TYPE_STRING
}

// simple closure to record responses
export function validator (data) {
  var res = {
    valid: true,
    messages: []
  }

  var validate = function (message, condition) {
    if (message && condition) {
      if (!condition(data)) {
        res.valid = false
        res.messages.push(message)
      }
    } else {
      // when called with no arguments return the valid state
      return res.valid
    }
  }

  validate.messages = function () { return res.messages }
  return validate
}

// some common validator functions
export function isMultipleValues (chartData) {
  return (chartData.data.length > 1)
}

export function isSingleSeries (chartData) {
  return (chartData.fields.length === 2)
}

export function isDoubleSeries (chartData) {
  return (chartData.fields.length === 3)
}

export function isQuadSeries (chartData) {
  return (chartData.fields.length <= 5)
}

export function isManySeries (chartData) {
  return (chartData.fields.length > 2)
}

export function isTimeSeries (chartData) {
  return (chartData.fields[0] &&
    chartData.fields[0].taxonomy &&
    chartData.fields[0].taxonomy.isDate)
}

export function isContinuousTimeSeries (chartData) {
  return (chartData.fields[0] &&
    chartData.fields[0].taxonomy &&
    chartData.fields[0].taxonomy.isContinuous &&
    chartData.fields[0].taxonomy.isDate)
}

export function isPositiveDomain (chartData) {
  var domain = seriesDomain(getSeries(chartData))
  return (domain[0] >= 0 && domain[1] >= 0)
}

// only true if domain min and max are negative
export function isNegativeDomain (chartData) {
  var domain = seriesDomain(getSeries(chartData))
  return (domain[0] < 0 && domain[1] < 0)
}

// only true if domain has positive and negative values
export function isMixedDomain (chartData) {
  return (!isPositiveDomain(chartData) && !isNegativeDomain(chartData))
}

// because d3.ticks is awful sometimes
export function greatestCommonDivisor (a, b) {
  a = Math.abs(a)
  b = Math.abs(b)
  while (b) {
    var t = b
    b = a % b
    a = t
  }
  return a
}

// this is a special case to check if a stacked domain is being used to mimic
// a single series chart - in this case for every column there will be only one
// non-zero value
export function isSingleSeriesStackedDomain (chartData) {
  if (chartData.fields.length === 2) return true
  let isSingleSeries = true

  for (let r = 0; r < chartData.data.length; r++) {
    if (!isSingleSeries) break
    const row = chartData.data[r]
    let total = 0
    let lastVal = 0
    for (let i = 1; i < row.length; i++) {
      if (row[i]) {
        total += row[i]
        lastVal = row[i]
      }
    }

    if (total !== lastVal) isSingleSeries = false
  }

  return isSingleSeries
}

// A function called when there is no chartType defined
// use the list of types to iterate over and select a format
// that succeeds
export function determineType (types, data, options) {
  var typeMatch = null
  Object.keys(types).forEach(function (key) {
    var type = types[key]
    if (!typeMatch) {
      if (typeCheck(type, data, options, () => null)) {
        typeMatch = type
      }
    }
  })

  return typeMatch
}

// Apply type check will test the data against a single
// type validator, and respond with warning messages back
// to the editor
export function typeCheck (type, data, options, postMessage) {
  if (type.validateData) {
    var valid = type.validateData(data)
    if (!valid()) {
      postMessage(valid.messages())
    }
    return valid()
  } else {
    return true // if no validator then we assume it's valid
  }
}

// Used for the editor to run validation over the available types
// and provide a report of what types apply
export function typeReport (types, data, options, postMessage) {
  var typeReport = {}
  Object.keys(types).forEach(function (typeName) {
    var type = types[typeName]
    if (type.validateData) {
      var valid = type.validateData(data)
      typeReport[typeName] = {
        valid: valid(),
        messages: valid.messages()
      }
    } else {
      typeReport[typeName] = 'no validation method provided'
    }
  })

  postMessage(typeReport)
}

// provide a method for sorting values on a field while keeping
// a map of original indices to newly sorted values
export function sortAllFields (chartData, options) {
  var defaults = {
    direction: 'descending',
    sortPrimaryAxisTime: false,
    stacked: false
  }
  options = Object.assign(defaults, options)

  // default sort is on the primary field
  var sort = (a, b) => b[1] - a[1]
  if (options.direction === 'ascending') {
    sort = (a, b) => a[1] - b[1]
  }

  // if stacked sort on total value of all fields
  if (options.stacked) {
    var add = (a, b) => Math.abs(a) + Math.abs(b)
    sort = (a, b) => {
      return b.slice(1).reduce(add, 0) - a.slice(1).reduce(add, 0)
    }

    if (options.direction === 'ascending') {
      sort = (a, b) => {
        return a.slice(1).reduce(add, 0) - b.slice(1).reduce(add, 0)
      }
    }
  }

  if (options.sortPrimaryAxisTime) {
    if (options.reverse) {
      chartData.data.reverse()
      sort = null
    } else {
      var parse = fieldDateParser(chartData.fields[0])
      sort = (a, b) => parse(a[0]).toDate().getTime() - parse(b[1]).toDate().getTime()
    }
  }

  if (options.customSort) sort = options.customSort

  if (sort) chartData.data.sort(sort)

  chartData.fields.forEach(function (field, i) {
    field.data = chartData.data.map(function (d) {
      return d[i]
    })
  })
}

// for dashed line segments return a list of invalid
// segments for rendering
export function getInvalidSegments (chartData) {
  var filterFunction = seg => seg.is_valid === false
  return getSegments(chartData, filterFunction)
}

export function getValidSegments (chartData) {
  var filterFunction = seg => seg.is_valid === true
  return getSegments(chartData, filterFunction)
}

export function getSegments (chartData, filter) {
  var series = getSeries(chartData)
  var seriesSegments = []

  series.forEach(function (field, i) {
    var length = field.data.length
    var segments = []
    var segment = []
    var segValid = true
    var segIndex = 0

    while (segIndex < length) {
      var dataValid = (field.data[segIndex] != null)
      if (dataValid && segValid) {
        segment.push({ value: field.data[segIndex], index: segIndex })
      } else if (!dataValid && segValid) {
        // If the first segment is invalid, it will not be drawn
        if (segment.length > 0) {
          segments.push({ data: segment, is_valid: segValid })
          segment = [segment[segment.length - 1]]
          segValid = false
        }
      } else if (dataValid && !segValid) {
        segment.push({ value: field.data[segIndex], index: segIndex })
        segments.push({ data: segment, is_valid: segValid })
        segment = [{ value: field.data[segIndex], index: segIndex }]
        segValid = true
      }
      segIndex++
    }
    // If the last segment is invalid, it will not be drawn
    if (segment.length >= 1 && segValid) {
      segments.push({ data: segment, is_valid: segValid })
    }

    if (filter) {
      segments = segments.filter(filter)
    }

    seriesSegments.push(segments)
  })

  var l = seriesSegments.reduce((sum, arr) => sum + arr.length, 0)
  if (l === 0) seriesSegments = null

  return seriesSegments
}

// this function is called for tooltips that are in the dashed
// zone between values. basically find the nearest neighbors and
// interpolate between them
export function interpolateBetweenValues (env, dataIndex, seriesIndex) {
  const series = getSeries(env.data)

  if (series[seriesIndex] && series[seriesIndex].data) {
    // seek forward for the nextValue
    const values = series[seriesIndex].data
    const [nextIndex, nextVal] = getFirstValue(values, dataIndex, true)
    const [prevIndex, prevVal] = getLastValue(values, dataIndex, true)

    if (valueOrNull(nextVal) == null) return null
    if (valueOrNull(prevVal) == null) return null
    // if (nextVal && prevVal == null) return nextVal
    // if (prevVal && nextVal == null) return prevVal

    const interpAmount = (dataIndex - prevIndex) / (nextIndex - prevIndex)
    return prevVal + ((nextVal - prevVal) * interpAmount)
  }

  return null
}

export function valueOrNull (val) {
  if (val == null) return null
  if (val === 'null' || val === 'na' || val === 'n/a' || val === '') return null
  return val
}
