import { default as applyDateAxis } from './dateAxis'
import {
  dateToUTCString
} from '../../utils/data'
import {
  labelDensity,
  hideOverlappingTicks,
  hideLine2Duplicates
} from './utils'

export default function hourlyAxis (selection, axis, env, options) {
  var numTicks = Math.max(2, Math.floor(axis.range()[1] / axis.tickStepHorizontal()))
  var formatters = [
    v => v.format('HH:mm'),
    v => v.format('MMM D')
  ]

  if (axis.range()[1] < 600) {
    numTicks *= 1.5
  }

  numTicks /= labelDensity(axis)

  axis.model()
    .ticks(numTicks)
    .tickFormat(function (d, i) {
      return dateToUTCString(d, axis.field())
    })

  selection.call(axis.model())

  applyDateAxis(selection, axis, formatters, env)

  hideOverlappingTicks(selection, axis)
  hideLine2Duplicates(selection, axis)

  return selection
}
