import { select } from '../d3-custom'
import { getOption } from './options'
import { isMixedDomain, isNegativeDomain } from './data'
import { renderChartAsTimeSeries, getColumnWidth } from '../toaster/column'
import simpleLegend from '../layers/simpleLegend'
import {
  axisTop,
  axisLeft,
  axisBottom,
  axisRight
} from 'd3-axis'

export var TOP = 'top'
export var BOTTOM = 'bottom'
export var LEFT = 'left'
export var RIGHT = 'right'
export var HORIZONTAL = 'horizontal'
export var VERTICAL = 'vertical'
export var X = 'x'
export var Y = 'y'

export var MOBILE_AXIS_WIDTH = 375
export var MOBILE_BREAKPOINT = 600
export var PHABLET_BREAKPOINT = 880
export var SCREENSHOT_MIN_HEIGHT = 240

export var AXIS_MAP = {}
AXIS_MAP[TOP] = axisTop
AXIS_MAP[BOTTOM] = axisBottom
AXIS_MAP[LEFT] = axisLeft
AXIS_MAP[RIGHT] = axisRight

export function placeRectangleUnderneath (origin, textItem, options) {
  options = options || {paddingH: 4, paddingV: 4, fill: 'white'}

  var bounds = textItem.node().getBoundingClientRect()

  // we have to go around d3 for insertBefore
  var rect = document.createElementNS('http://www.w3.org/2000/svg', 'rect')
  rect.setAttribute('x', (bounds.left - origin[0]) - (options.paddingH * 2))
  rect.setAttribute('y', (bounds.top - origin[1]) - options.paddingV)
  rect.setAttribute('width', bounds.width + (options.paddingH * 4))
  rect.setAttribute('height', bounds.height + (options.paddingV * 2))
  rect.setAttribute('fill', options.fill)

  textItem.node().parentNode.insertBefore(rect, textItem.node())
}

export function bottomLabel (options) {
  return function (selection, env) {
    if (env.options.disableLabels) return

    // todo move to generics
    selection.selectAll('.label-bottom').remove()

    if (env.data.primaryAxis && env.data.primaryAxis.caption) {
      selection.selectAppend('div.chart-footer')
        .append('div')
        .classed('label-bottom', true)
        .text(env.data.primaryAxis.caption)
    }
  }
}

// determine if grid should be default or to display in a
//   also: all negative values and bar chart (zero on top)
export function shouldDisplayGrid (env) {
  if (env.options.disableGrid) return false
  if (env.data.config.grid_style === 'no-grid') return false
  if (env.data.config.grid_style === 'no-grid-no-domain') return false
  if (env.data.config.grid_style === 'force-grid-force-domain') return true
  if (env.data.config.grid_style === 'force-grid') return true
  if (env.data.annotations && env.data.annotations.length) return false
  if (isMixedDomain(env.data)) return false
  if (isNegativeDomain(env.data)) return false

  // keep terminal themes with gridlines for now
  if (['terminal', 'coth', 'bizweek'].indexOf(env.data.theme) !== -1) {
    return true
  }

  // else if (['opinion'].indexOf(env.data.theme) !== -1) {
  //   return true // WAITING FOR CONFIRMATION
  // }

  if (env.data.chartType === 'line') return true

  return false
}

// for charts that go from positive to negative we want to
// default to hiding the x-axis and showing the zero axis
export function shouldDisplayDomain (env) {
  if (env.options.disableDomain) return false
  if (env.data.config.grid_style === 'no-grid-no-domain') return false
  if (env.data.config.grid_style === 'force-grid-force-domain') return true
  if (isMixedDomain(env.data)) return false
  if (isNegativeDomain(env.data)) return false
  return true
}

// we allow domain ticks to be hidden only when the type is a bar chart
// and also only when the bar chart is not a time series chart
export function shouldDisplayDomainTicks (env) {
  if (isMixedDomain(env.data)) return true
  if (['line', 'area', 'barline'].indexOf(env.data.chartType) !== -1) return true

  // specifically for column types
  const columnWidth = getColumnWidth(env.el, env.data, env.options)
  if (columnWidth < 50) return true
  if (renderChartAsTimeSeries(env.el, env.data, env.options)) return true

  return false
}

// We use the Axis bounds to determine how big an area
// to render into, so we need to draw an invisible line
// spanning the axis
export function makeAxisBounds (selection, axis) {
  // set bounds of range with invisible dom
  if (axis.orientation() === HORIZONTAL) {
    selection.selectAppend('line.bounds')
      .attr('y', 0)
      .attr('x1', axis.scale.range()[0])
      .attr('x2', axis.scale.range()[1])
      .style('stroke', 'transparent')
  } else {
    selection.selectAppend('line.bounds')
      .attr('x', 0)
      .attr('y1', axis.scale.range()[0])
      .attr('y2', axis.scale.range()[1])
      .style('stroke', 'transparent')
  }
}

// defaultScaffold provides titles and footers within the chart
// container - this can be disabled and overriden by the client
export function defaultScaffold (options) {
  function wrap (selection) {
    selection.each(function (data) {
      const el = select(this)
      const variantLogo = getOption('variant_logo', data, options)
      const variantColor = getOption('variant_color', data, options)

      // clear the chart when there is a change of ids
      if (el.attr('toaster-id') &&
          el.attr('toaster-id') !== data.id) {
        el.selectAll('*').remove()
      }

      el.attr('toaster-id', data.id)

      // clear previous color variants
      el.node().className = el.node().className.replace(/variant-color-/g, 'x-')
      el.classed(`variant-color-${variantColor}`, true)
      el.classed('no-titles', options.hideTitle)

      const header = el.selectAppend('div.header')
        .classed('hide', options.hideTitle)

      if (!options.hideTitle) {
        header.selectAppend('h1').html(function (d) { return d.title })
        header.selectAppend('h2').html(function (d) { return d.subtitle })
      }

      if (!options.hideLegend) {
        el.selectAppend('div.legend').call(simpleLegend(options))
      }

      el.selectAppend('div.chart')

      if (options.noPadding || (options.hideTitle && options.hideFooter)) {
        el.classed('no-padding', true)
      }

      const footer = el.selectAppend('div.footer')
        .classed('hide', options.hideFooter)

      if (!options.hideFooter) {
        footer.selectAppend('p.source').html(d => d.source)
        footer.selectAppend('p.footnote').html(d => d.footnote)
        footer.selectAppend('div.logo')

        if (variantLogo) {
          el.classed(`variant-logo-${variantLogo}`, true)
          footer.select('div.logo')
            .classed(`variant-logo-${variantLogo}`, true)
        }
      }
    })
  }

  return wrap
}

// defaultScaffold provides titles and footers within the chart
// container - this can be disabled and overriden by the client
export function widgetScaffold (options) {
  function wrap (selection) {
    selection.each(function (data) {
      var el = select(this)
        .classed('widget-mode', true)
        .classed('no-padding', true)

      // clear the chart when there is a change of ids
      if (el.attr('toaster-id') &&
          el.attr('toaster-id') !== data.id) {
        el.selectAll('*').remove()
      }

      el.attr('toaster-id', data.id)
      el.selectAppend('div.chart')
    })
  }

  return wrap
}

// https://stackoverflow.com/questions/38224875/replacing-d3-transform-in-d3-v4
export function getTranslation (transform) {
  // Create a dummy g for calculation purposes only. This will never
  // be appended to the DOM and will be discarded once this function
  // returns.
  var g = document.createElementNS('http://www.w3.org/2000/svg', 'g')

  // Set the transform attribute to the provided string value.
  g.setAttributeNS(null, 'transform', transform)

  // consolidate the SVGTransformList containing all transformations
  // to a single SVGTransform of type SVG_TRANSFORM_MATRIX and get
  // its SVGMatrix.
  var matrix = g.transform.baseVal.consolidate().matrix

  // As per definition values e and f are the ones for the translation.
  return [matrix.e, matrix.f]
}

// return numeric aspect ratio
export function getAspectRatio (chartData, defaultRatio) {
  var ratio = defaultRatio || '5:2'

  if (chartData.chartOptions && chartData.chartOptions.aspectRatio) {
    ratio = chartData.chartOptions.aspectRatio
  }
  if ((typeof ratio) === 'string') {
    ratio = ratio.split(':')[0] / ratio.split(':')[1]
  }

  return ratio
}

// return css properties from an element as an array
// pass in properties as array of arguments
export function getCSSProperties (node) {
  const properties = Array.prototype.slice.call(arguments, 1)
  const style = window.getComputedStyle(node, null)

  return properties.map(prop => style.getPropertyValue(prop))
}

// return sum of padding-top / padding-bottom
export function getPaddingHeight (node) {
  const heights = getCSSProperties(node, 'padding-top', 'padding-bottom')

  return heights
    .map(val => Number(val.replace('px', '')))
    .reduce((val, sum) => val + sum)
}

// return sum of padding-top / padding-bottom
export function getMarginHeight (node) {
  const heights = getCSSProperties(node, 'margin-top', 'margin-bottom')
  return heights
    .map(val => Number(val.replace('px', '')))
    .reduce((val, sum) => val + sum)
}
