import {
  getDurationYears,
  dateToUTCString,
  isForceCategorical
} from '../../utils/data'
import applyDateAxis from './dateAxis'
import {
  hideDuplicates,
  labelDensity,
  hideIntermediateTicksMobile,
  hideOverlappingTicks,
  abbreviateYears,
  yearStartCapEnabled
} from './utils'
import { VERTICAL, MOBILE_AXIS_WIDTH } from '../../utils/layout'

/**
  Each type of axis formatting will have it's own builder - defining the
  optimal number of ticks, applying to selection and then a postRender
  function to separate multiple lines and clean things up.
*/
export default function decadeAxis (selection, axis, env) {
  let numTicks = Math.max(2, Math.floor(axis.range()[1] / (axis.tickStepHorizontal())))
  const durationYears = getDurationYears(axis.field(), axis.axisScale.domain())
  const formatters = [v => v.format('YYYY')]
  const options = {
    noStartCap: false,
    noEndCap: true,
    endCapPriority: false
  }
  const isMobile = axis.rangeLength() <= MOBILE_AXIS_WIDTH
  const isVertical = axis.orientation() === VERTICAL
  const noAbbreviation = isMobile || isForceCategorical(axis.field())

  numTicks = Math.min(numTicks, durationYears)
  numTicks /= labelDensity(axis)

  // some cases we want the startCap
  options.noStartCap = !yearStartCapEnabled(axis, numTicks)

  // for mobile we need the endcap
  if (isMobile) {
    options.noEndCap = false
    options.noStartCap = false
  }

  axis.model()
    .ticks(numTicks)
    .tickFormat(function (d, i) {
      return dateToUTCString(d, axis.field())
    })

  selection.call(axis.model())
  applyDateAxis(selection, axis, formatters, env, options)

  if (!noAbbreviation && !isVertical) {
    abbreviateYears(selection, axis)
  }

  if (isMobile) {
    hideIntermediateTicksMobile(selection, axis)
  } else {
    hideOverlappingTicks(selection, axis)
    hideDuplicates(selection, axis)
  }

  return selection
}
