import { select } from 'd3'
import line from './line.js'
import {
  buildTaxonomy,
  validator,
  isTimeSeries,
  isMultipleValues,
  getFieldIndexValueParser
} from '../utils/data'
import debugEnv from '../utils/debug'
import _merge from 'lodash-es/merge'
import _clone from 'lodash-es/cloneDeep'
import dial from '../layers/dial'
import { getAspectRatio, shouldDisplayGrid, shouldDisplayDomain } from '../utils/layout'
import { makeLayerPrototype } from '../utils/layers'
import { applyMultiplesOptions } from './multiples'

export default function spread (options) {
  const defaults = {
    // default options here
    defaultBoxType: 'line',
    minimumBoxWidth: 170,
    minimumBoxHeight: 180,
    disableGrid: true,
    disableDomain: true,

    // gutters between charts
    gutterWidth: 25,

    // breakpoint to split to "small" version of
    // chart that uses circles instead of flats
    renderStyleBreakpoint: 250,

    // make sure pie chart has no labels
    disablePieLabels: true,
    pieOuterRadius: 120,
    pieMinRadius: 20,
    automaticRadius: false,
    forceTickAtDomainTop: true,
    minimumTicks: 2,

    // configuration options for layout manager
    layoutManager: {
      minHeight: 300
    }
  }
  options = _merge(defaults, options)

  // we need a way to store env, el for each selection and
  // expose that out to any calling function
  const selections = []

  function spread (selection) {
    selection.each(function (chartData, i) {
      const env = {
        x: null, // simpleAxis({orientation: 'x'}),
        y: null, // simpleAxis({orientation: 'y'}),
        r: null,
        options: Object.assign({}, options)
      }
      const el = select(this)
        .classed('toaster-chart-spread', true)

      // ensure we have a taxonomy specified on the data
      buildTaxonomy(chartData)
      env.data = chartData

      // ensure bar charts are vertical bars
      chartData.config.force_vertical_bars = true

      const chart = el.select('.chart')
      const chartWidth = chart.node().offsetWidth
      const chartTypeClass = line
      let series = chartData.fields.slice(1).filter((f, i) => i % 2 === 0)
      if (env.options.widget) series = [series[0]]
      let boxWidth = Math.ceil(chartWidth / series.length) - 6
      let boxHeight = (chartWidth / getAspectRatio(chartData))
      let numRows = 1

      // when boxes are too small we start wrapping them
      if (boxWidth < env.options.minimumBoxWidth) {
        numRows = Math.ceil(env.options.minimumBoxWidth * series.length / chartWidth)
        boxWidth = chartWidth / Math.ceil(series.length / numRows) - 6
        boxHeight /= numRows
      }

      boxHeight = Math.max(boxHeight, env.options.minimumBoxHeight)
      let numPerRow = Math.floor(chartWidth / boxWidth)
      const gutterPadding = env.options.gutterWidth * (numPerRow - 1) / numPerRow

      env.options.pieOuterRadius = ((boxWidth - 50) / 2) - 20
      if (env.options.widget) env.options.pieOuterRadius = 55

      env.options.layoutManager.minHeight = boxHeight

      // no legend for these types
      el.select('.legend').style('display', 'none')
      el.classed('no-grid', !shouldDisplayGrid(env))
        .classed('no-domain', !shouldDisplayDomain(env))

      // TWO types of renderers -- a single value render with dial or timeline style
      // with axis / spread
      if (chartData.fields.length === 2) {
        chart.call(dial(env.options))
        selections.push({ env, el })
      } else {
        // create postRender layer
        const isSmallChart = (boxWidth < env.options.renderStyleBreakpoint)
        options.layers = [createRenderLayer(chartData, isSmallChart)]

        // el.selectAll('.spread-container').remove()
        const spreadData = splitData(chartData, env.options)
        const spreads = chart.selectAll('.spread').data(spreadData)
        const defaultOptions = {}
        if (!isSmallChart) {
          defaultOptions.curveType = 'stepAfter'
          defaultOptions.layoutManager = {
            yAxisInnerPadding: 22,
            xAxisExtendRight: 50
          }
        } else {
          defaultOptions.forceNoTicks = true
        }
        const renderOptions = _merge(defaultOptions, env.options)

        spreads.exit().remove()
        spreads.enter()
          .append('div')
          .attr('class', function (d, i) {
            return 'spread spread-' + i
          })
          .merge(spreads)
          .style('width', (boxWidth - gutterPadding) + 'px')
          .style('padding-right', (d, i) => {
            // last item on row has no padding-right
            if (i % numPerRow === numPerRow - 1) {
              return 0
            } else {
              return env.options.gutterWidth + 'px'
            }
          })

        chart.selectAll('.spread').each(function (d, i) {
          const container = select(this)
          const layerOptions = Object.assign(renderOptions, {
            tooltipPostRender: tooltipPostRender(chartData, i)
          })
          const drawLayer = chartTypeClass(layerOptions)

          container.selectAppend('div.title')
            .text(d => {
              return spreadData[i] ? spreadData[i].fields[1].name : ''
            })
          container.selectAppend('div.score-title')
            .html(d => {
              const score = getScore(chartData, i)
              return `<span>Score:</span> ${score}`
            })
          container.selectAppend('div.toaster-chart')
            .datum(spreadData[i])
            .call(drawLayer)

          selections.push({
            el: container,
            env: drawLayer.getEnv()
          })
        })
      }

      debugEnv(env)
    })
  }

  return makeLayerPrototype(spread, selections, options)
}

function getScore (chartData, i) {
  const colIndex = (2 * i) + 2
  const rowIndex = chartData.data.length - 1

  return chartData.data[rowIndex][colIndex]
}

function createRenderLayer (chartData, isSmallChart) {
  return (renderTarget, el, env, opts) => {
    const container = el.select('.draw-container')
    const lineDom = container.select('.line-group .line')
    const oldSegments = lineDom.attr('d').split('L')
      .map(v => v.replace('M', ''))
    const { selectIndex } = opts

    // now create the draw region
    container.selectAll('.perf-group').remove()
    container.insert('g', '.annotations') // insert before annotations
      .classed('perf-group', true)
    const renderGroup = container.select('g.perf-group')

    if (isSmallChart) {
      const circles = []
      const xData = getFieldIndexValueParser(chartData, true, env.options.forceCategorical)

      chartData.data.forEach((row, i) => {
        circles.push({
          line: `M${oldSegments[2 * i]}L${oldSegments[(2 * i) + 1]}`,
          x: env.x.scale(xData(i)),
          y: env.y.scale(row[1 + (selectIndex * 2)]),
          value: chartData.data[i][1 + (selectIndex * 2) + 1]
        })
      })

      const points = renderGroup.selectAll('.circle').data(circles)
      points.enter()
        .append('circle')
        .attr('cx', d => d.x)
        .attr('cy', d => d.y)
        .attr('r', 5)
        .attr('class', function (d, i) {
          return 'score-' + (d.value)
        })
    } else {
      const newSegments = []

      for (let i = 0; i < oldSegments.length / 2; i++) {
        if (oldSegments[(2 * i) + 1] && chartData.data[i]) {
          newSegments.push({
            line: `M${oldSegments[2 * i]}L${oldSegments[(2 * i) + 1]}`,
            value: chartData.data[i][1 + (selectIndex * 2) + 1]
          })
        } else if (oldSegments[(2 * i)] && chartData.data[i]) {
          const curSegment = oldSegments[(2 * i)]
          const curPts = curSegment.split(',').map(v => Number(v))
          const lastSegment = newSegments[newSegments.length - 1]
          const lastPts = lastSegment.line
            .replace('M', '')
            .split('L')
            .map(v => v.split(','))
          const segWidth = Number(lastPts[1][0]) - Number(lastPts[0][0])
          const finalSeg = `${curPts[0] + segWidth},${curPts[1]}`
          // create the last segment as extended piece

          newSegments.push({
            line: `M${oldSegments[2 * i]}L${finalSeg}`,
            value: chartData.data[i][1 + (selectIndex * 2) + 1]
          })
        }
      }

      const lines = renderGroup.selectAll('.line').data(newSegments)
      lines.enter()
        .append('path')
        .attr('stroke-width', '10')
        .attr('class', function (d, i) {
          return 'score-' + (d.value)
        })
        .merge(lines)
        .attr('d', d => d.line)
    }
  }
}

// Validation is a static method to check if the class
// supports the chartData before building the chart
spread.validateData = function (chartData, options) {
  const validate = validator(chartData, options)

  validate('Line chart must have more than one value.', isMultipleValues)
  validate('Line should be a time series', isTimeSeries)

  return validate
}

export function splitData (chartData, options) {
  return chartData.fields.slice(1)
    .filter((field, i) => i % 2 === 0)
    .map((field, i) => {
      const clone = _clone(chartData)
      const colIndex = (2 * i) + 1

      // leftover remnant of editor
      field.caption = null

      clone.fields = [chartData.fields[0], clone.fields[colIndex]]
      clone.data = clone.data.map(row => [row[0], row[colIndex]])

      if (clone.annotations) {
        clone.annotations = clone.annotations
          .filter(annotation => annotation.series === (colIndex))
          .map(annotation => {
            annotation.series = 1
            return annotation
          })
      }

      return clone
    })
    .map((clone, index) => {
      return applyMultiplesOptions(clone, options, index)
    })
}

function tooltipPostRender (originalData, seriesIndex) {
  return (env, container, value, index) => {
    const scoreIndex = 1 + (seriesIndex * 2) + 1
    const scoreVal = originalData.data[index][scoreIndex]
    container
      .select('.tooltip-values')
      .append('div')
      .classed('tooltip-value', true)
      .style('padding-top', '3px')
      .style('line-height', '1')
      .text(`Score: ${scoreVal}`)
  }
}
