import {
  scaleBand,
  scaleTime,
  range as d3range
} from 'd3'

import { buildAxis } from './format'
import { objectAccessor } from '../utils/object'
import { getSeries, getField } from '../utils/data'
import * as layout from '../utils/layout'

// allow for a different scale from the axis scale
export default function bandAxisProvider (options) {
  var state = {
    type: 'band',
    isPrimaryAxis: true, // this axis is typically the secondary (x) axis
    orientation: layout.HORIZONTAL,
    position: layout.BOTTOM,
    range: [0, 500],
    data: null,
    model: null,
    series: null,
    field: null,
    domain: [0, 100],
    padding: 0.3,
    outerPadding: 0.0,
    tickStep: 30, // ideal pixels between steps
    tickStepHorizontal: 100,
    tickStepVertical: 60,
    axisTextMarginHorizontal: 16,
    axisTextMarginVertical: 4,
    axisTextLineHeight: 18,
    axisTextLineHeightMobile: 14,
    axisTextLineHeightHorizontal: 15,
    axisTextPadding: 6,
    horizontalBarHeight: 32,
    horizontalBarPadding: 0.2,
    verticalAxisWrapWidth: 300,
    tickLabelAlternate: false,
    stackedBand: false,
    stackedDomain: false,
    isDateAxis: false,
    numTicks: 1 // used to store number of ticks used
  }

  state = Object.assign(state, options || {})

  // bind to the element we want to draw to
  var axis = function (selection, env) {
    selection.selectAll('*').remove() // clear

    // TODO:: better way to match mediaQuery
    if (window.innerWidth < 600) {
      axis.axisTextMarginHorizontal(10)
      axis.axisTextLineHeight(state.axisTextLineHeightMobile)
    }

    if (!env.options.disableAxis) {
      buildAxis(selection, axis, env, { forceBand: !axis.isDateAxis() })
    }

    return selection
  }

  // turn the whole thing into one giant accessor
  objectAccessor(axis, state)

  // scale and seriesScale are all no longer part of the
  // state accessor - this is because they are all callable functions
  axis.scale = scaleBand()
  axis.seriesScale = scaleBand()
  axis.axisScale = null

  // this is needed to initialize scale, range etc.
  axis.data = function (data) {
    if (data) {
      axis.series(getSeries(data))
      axis.field(getField(data, state.isPrimaryAxis))

      const field = axis.field()

      axis.scale
        .domain(field.data)
        .paddingInner(state.padding)
        .paddingOuter(state.outerPadding)
        .range(state.range)

      if (state.orientation === layout.VERTICAL) {
        let barHeight = state.horizontalBarHeight * axis.series().length
        if (axis.stackedBand()) {
          barHeight = state.horizontalBarHeight
        }
        state.range[0] = 0
        state.range[1] = state.field.data.length * barHeight
        axis.scale
          .range(state.range)
          .padding(state.horizontalBarPadding)
          .bandwidth(barHeight)
      }

      axis.seriesScale
        .domain(d3range(axis.series().length))
        .range([0, axis.scale.bandwidth()])

      // support rendering bottom axis as date axis
      if (axis.isDateAxis()) {
        axis.axisScale = scaleTime()
          .domain([
            field.taxonomy.dateStart,
            field.taxonomy.dateEnd
          ])
          .range(axis.range())
      }

      axis.axisScale = axis.axisScale || axis.scale
      axis.model(layout.AXIS_MAP[state.position](axis.axisScale))

      state['data'] = data
      return axis
    } else {
      return state['data']
    }
  }

  // initialize data right away if data provided in options
  if (state.data) {
    axis.data(state.data)
  }

  // return the axis scale value from item index
  axis.scaleIndex = function (index) {
    return axis.scale(axis.field()[index])
  }

  // given a normalized range coordinate return the data at that position
  axis.getDataAtPosition = function (position) {
    var band = getBandFromPosition(axis.scale, position)
    var seriesBand = { index: 0 }
    var data = []

    if (axis.series().length > 1) {
      seriesBand = getBandFromPosition(axis.seriesScale, band.position)
    }

    if (band.index === -1 || axis.data().fields[seriesBand.index + 1] === undefined) {
      return null
    }

    axis.series().forEach(function (field) {
      data.push(field.data[band.index])
    })

    return {
      dataIndex: band.index,
      seriesIndex: seriesBand.index,
      data: data
    }
  }

  // given a data index [bandIndex, seriesIndex] return the normalized
  // position where an anchor would be set (ie center of the column)
  axis.getPositionFromIndex = function (index, ignoreSeries) {
    var bandIndex = index
    var seriesIndex = 0
    var leftPx

    if (axis.series().length === 1) {
      ignoreSeries = true
    }

    if (index.length) {
      bandIndex = index[0]
      seriesIndex = index[1] || 0
    }

    leftPx = axis.scale(axis.field().data[bandIndex])

    // force position evaluation as single series bar
    if (ignoreSeries) {
      leftPx += axis.scale.bandwidth() * 0.5
    } else {
      leftPx += axis.seriesScale(seriesIndex)
      leftPx += axis.seriesScale.bandwidth() * 0.5
    }

    return leftPx / axis.rangeLength()
  }

  axis.rangeLength = function () {
    return Math.abs(axis.scale.range()[1] - axis.scale.range()[0])
  }

  return axis
}

// reverse lookup from a normalized range value to the band
// https://raw.githubusercontent.com/d3/d3-scale/master/img/band.png
function getBandFromPosition (scale, pos) {
  var rangePos = pos * Math.abs(scale.range()[1] - scale.range()[0])
  var outerPadding = scale.step() * scale.paddingOuter()
  var bandIndex
  var bandPos
  var data

  // subtract outerPadding and divide by step to get band index
  rangePos -= outerPadding
  bandIndex = Math.floor(rangePos / scale.step())
  data = scale.domain()[bandIndex]

  // now return the band and the normalized position within the band
  // so that we can determine the series
  bandPos = rangePos - scale(data) + outerPadding

  if (bandPos > scale.bandwidth()) { // we are in the padding
    return {
      index: -1,
      position: 0.0
    }
  } else {
    return {
      index: bandIndex,
      position: bandPos / scale.bandwidth()
    }
  }
}
