/**
  Axis-specific formatting. Separate from the formatter in utils/data

  Beware of date formatting converting to local time:
  ie:  new Date('2004')     -->   Wed Dec 31 2003 19:00:00 GMT-0500 (EST)
*/

import {
  OUTPUT_FORMAT_YEARS,
  OUTPUT_FORMAT_CATEGORICAL,
  DURATION_HOURS,
  DURATION_DAYS,
  DURATION_WEEKS,
  DURATION_MONTHS,
  DURATION_QUARTERS,
  DURATION_MULTIYEAR,
  DURATION_DECADE,
  DURATION_MULTIDECADE,
  DURATION_CENTURY,
  dateFormatter
} from '../../utils/data'

import {
  axisPadding,
  hideOverlappingTicks,
  getVisibleTicks,
  hideIntermediateTicksMobile,
  hideDuplicates,
  hideLine2Duplicates,
  applyLabelDensity,
  labelDensity,
  quarterLabelDensity,
  drawDomain,
  filterYearFormatter
} from './utils'

import { VERTICAL } from '../../utils/layout'

import { default as hourlyAxis } from './hourly'
import { default as dayAxis } from './daily'
import { default as monthPlusAxis } from './monthPlus'
import { default as monthlyAxis } from './monthly'
import { default as decadeAxis } from './decade'
import { default as decadePlusAxis } from './decadePlus'
import { default as numericAxis } from './numeric'
import { default as bandAxis } from './band'
import { default as yearBandAxis } from './yearBand'
import { default as quarterAxis } from './quarter'
import { default as periodicAxis } from './periodic'
import { default as placeVerticalAxisLabel } from './axisLabel'

export {
  placeVerticalAxisLabel,
  axisPadding,
  hideOverlappingTicks,
  getVisibleTicks,
  hideIntermediateTicksMobile,
  hideDuplicates,
  hideLine2Duplicates,
  applyLabelDensity,
  labelDensity,
  quarterLabelDensity,
  drawDomain,
  filterYearFormatter
}

/**
 *  This whole thing is the core of axis display taxonomy.
 */
export function buildAxis (selection, axis, env, options) {
  options = options || {}
  var field = axis.field()

  if (!field) return

  // if defined output format and line chart
  if (axis.isPrimaryAxis() &&
    env.data.chartOptions.dateOutputFormat &&
    env.data.chartType === 'line' &&
    !options.forceBand
  ) {
    return periodicAxis.apply(this, arguments)
  }

  // if defined output format
  if (axis.isPrimaryAxis() && env.data.chartOptions.dateOutputFormat) {
    var outFormat = env.data.chartOptions.dateOutputFormat
    var df = dateFormatter

    if (outFormat !== OUTPUT_FORMAT_CATEGORICAL) {
      if (outFormat === OUTPUT_FORMAT_YEARS) {
        env.options.categoricalFormatter = df('YYYY')
        return yearBandAxis.apply(this, arguments)
      }
      return periodicAxis.apply(this, arguments)
    }
  }

  // band axis can be years / quarters / months etc.
  if (options.forceBand) {
    if (env.layout.orientation() !== VERTICAL &&
      field.taxonomy &&
      field.taxonomy.isDate) {
      if (field.taxonomy.duration === DURATION_QUARTERS) {
        return quarterAxis.apply(this, arguments)
      }

      if (field.taxonomy.isYears) {
        return yearBandAxis.apply(this, arguments)
      }
    }

    return bandAxis.apply(this, arguments)
  }

  if (field.taxonomy && field.taxonomy.isDate) {
    if (field.taxonomy.duration === DURATION_MONTHS) {
      return monthlyAxis.apply(this, arguments)
    } else if (field.taxonomy.duration === DURATION_MULTIYEAR ||
      field.taxonomy.duration === DURATION_DECADE) {
      return decadeAxis.apply(this, arguments)
    } else if (field.taxonomy.duration === DURATION_MULTIDECADE ||
      field.taxonomy.duration === DURATION_CENTURY) {
      return decadePlusAxis.apply(this, arguments)
    } else if (field.taxonomy.duration === DURATION_WEEKS) {
      return monthPlusAxis.apply(this, arguments)
    } else if (field.taxonomy.duration === DURATION_DAYS) {
      return dayAxis.apply(this, arguments)
    } else if (field.taxonomy.duration === DURATION_HOURS) {
      return hourlyAxis.apply(this, arguments)
    } else {
      return decadeAxis.apply(this, arguments)
    }
  } else if (field.taxonomy.isContinuous) {
    // linear numbers
    return numericAxis.apply(this, arguments)
  } else {
    // band ordinal axis
    return bandAxis.apply(this, arguments)
  }
}
