import {
  select as d3select
} from 'd3'

import {drawDomain} from './utils'
import {
  fieldDateParser,
  dateToUTCString
} from '../../utils/data'

// utility functions for all dates:
// - builds a start and end cap
// - applies date formatters to all ticks
// - hides elements that collide with start and end cap
export default function (selection, axis, formatters, env, options) {
  options = options || {}
  const parseDate = fieldDateParser(axis.field())
  const initialTicks = selection.selectAll('g.tick')
  const first = d3select(initialTicks.nodes()[0])
  const last = d3select(initialTicks.nodes()[initialTicks.size() - 1])
  const labelGutter = options.labelGutter || 10
  let start
  let end
  let ticks

  drawDomain(selection, axis, env)

  // add start / end cap manually
  if (!options.noStartCap) {
    start = selection.append('g')
      .classed('start-tick', true)

    buildDefaultTick(start)
  }

  let startVal = ''
  if (axis.axisScale.domain()[0] &&
      axis.axisScale.domain()[0].toUTCString) {
    startVal = dateToUTCString(axis.axisScale.domain()[0], axis.field())
  }
  let endVal = ''
  if (axis.axisScale.domain()[1] &&
      axis.axisScale.domain()[1].toUTCString) {
    endVal = dateToUTCString(axis.axisScale.domain()[1], axis.field())
  }

  if (!options.noStartCap) {
    start.select('text')
      .classed('start-text', true)
      .text(startVal)
  }

  // default is no endcaps, but it can be set to false
  // no more endcaps (July 26)
  if (options.noEndCap == null) options.noEndCap = true
  if (!options.noEndCap) {
    end = selection.append('g')
      .classed('end-tick', true)
      .attr('transform', 'translate(' + axis.axisScale.range()[1] + ',0)')

    buildDefaultTick(end)

    end.select('text')
      .classed('end-text', true)
      .text(endVal)
  }

  ticks = selection.selectAll('g.tick')
    .select('text')
    .each(function (d, i) {
      const textElement = d3select(this)
      const textVal = textElement.text()
      const yText = textElement.attr('y')
      let f = formatters

      if (options.startCapFormatters &&
          textElement.classed('start-text')) {
        if (options.requiresFormatting) {
          if (options.requiresFormatting(textVal)) {
            f = options.startCapFormatters
          }
        } else {
          f = options.startCapFormatters
        }
      }

      // textElement.attr('text-anchor', 'start')
      textElement.text(null)
      textElement.append('tspan')
        .attr('x', 0)
        .attr('y', yText)
        .attr('dy', axis.axisTextMarginHorizontal() + 'px')
        .classed('line1', true)
        .text(f[0](parseDate(textVal)))

      // allow a month display unless data is years only
      if (f[1] && !options.axisTextSingleLine) {
        textElement.append('tspan')
          .attr('x', 0)
          .attr('y', yText)
          .attr('dy', axis.axisTextMarginHorizontal() + axis.axisTextLineHeight() + 'px')
          .classed('line2', true)
          .text(f[1](parseDate(textVal)))
      }
    })

  let visibleCount = 2 + initialTicks.size()

  // hide the first tick text if it overlaps with start item
  initialTicks.each(function () {
    if (!options.noStartCap && (
        start.node().getBoundingClientRect().right >
        this.getBoundingClientRect().left - labelGutter ||
        start.text() === d3select(this).text()
    )) {
      d3select(this).classed('hide-tick', true)
      visibleCount--
    }

    if (!options.noEndCap && (
      this.getBoundingClientRect().right + labelGutter >
          end.node().getBoundingClientRect().left ||
          end.text() === d3select(this).text()
    )) {
      if (options.endCapPriority) {
        d3select(this).classed('hide-tick', true)
      } else {
        end.classed('hide-tick', true)
      }

      visibleCount--
    }
  })

  return {
    initial: initialTicks,
    all: ticks,
    first: first,
    last: last,
    start: start,
    end: end,
    visibleCount: visibleCount
  }
}

function buildDefaultTick (sel) {
  sel.classed('tick', true)
  sel.append('line')
    .attr('x1', 0.5)
    .attr('x2', 0.5)
    .attr('y2', 6)
  sel.append('text')
    .attr('y', 9)
    .attr('x', 0.5)
    .attr('dy', '0.71em')
    .attr('text-anchor', 'middle')
}
