import {
  select as d3select
} from 'd3'

import { VERTICAL } from '../../utils/layout'
import { isForceCategorical } from '../../utils/data'
import { default as bandAxis } from './band'
import {
  applyStartEndTick,
  formatYearDisplay,
  applyLabelDensity,
  hideIntermediateTicksMobile,
  abbreviateYears
} from './utils'

export default function yearBandAxis (selection, axis, env, options) {
  bandAxis(selection, axis, env, options)

  const isVertical = axis.orientation() === VERTICAL
  const noAbbreviation = isForceCategorical(axis.field())
  if (!noAbbreviation && !isVertical) {
    abbreviateYears(selection, axis)
  }

  applyStartEndTick(selection, axis)
  hideIntermediateTicksMobile(selection, axis)
  applyLabelDensity(selection, axis)

  formatYearDisplay(selection, axis, { abbreviations: !noAbbreviation })

  return selection
}
