import {placeMultiLineText} from '../utils/text'
import {
  valueFormatter,
  singleUseFormatter,
  numberOrNull
} from '../utils/data'

export function drawColumnLabel (chart, env) {
}

// science: a value between 3-5
function bandwidthOffset (bandwidth, seriesLength) {
  return Math.min(5,
    Math.max(
      3,
      (3 / ((bandwidth / seriesLength) / 32))
    )
  )
}

// For now this only applies to horizontal bar charts
export function drawColumnLabels (chart, env) {
  const labelAxis = env.x
  const fieldAxis = env.y
  const labelGroup = chart.selectAppend('g.label-container')
  const valFormatter = valueFormatter(env.x, env, {defaultDecimals: 1})
  const valFormatterAlt = valueFormatter(env.x, env, {bareValue: true, defaultDecimals: 1})
  const formatter = singleUseFormatter(valFormatter, valFormatterAlt)

  labelGroup.selectAll('*').remove()

  labelAxis.field().data.forEach(function (d, index) {
    labelAxis.series().forEach(function (s, seriesIndex) {
      const fieldVal = env.data.data[index][0]
      let fieldPos = fieldAxis.scale(fieldVal)
      fieldPos += fieldAxis.seriesScale(seriesIndex)

      const val = env.data.data[index][1 + seriesIndex]
      const valPos = labelAxis.scale(numberOrNull(val))
      const seriesLength = labelAxis.series().length
      const vOffset = bandwidthOffset(fieldAxis.scale.bandwidth(), seriesLength)

      const text = labelGroup.append('text')
        .classed('column-label', true)
        .classed('label-' + index, true)
        .text(formatter(val))
        .attr('x', valPos + env.options.columnLabelOffset)
        .attr('y', fieldPos +
                (fieldAxis.scale.bandwidth() / (seriesLength * 2)) +
                vOffset)

      if (val < 0) {
        text
          .attr('x', valPos - env.options.columnLabelOffset)
          .style('text-anchor', 'end')
      }
    })
  })
}

// These labels are for "table style" horizontal bar charts where the
// value labels are at the end rather than at bar width
export function drawColumnTableLabels (chart, env) {
  const labelAxis = env.x
  const fieldAxis = env.y
  const labelGroup = chart.selectAppend('g.label-container')
  const valFormatter = valueFormatter(env.x, env, {defaultDecimals: 1})
  const valFormatterAlt = valueFormatter(env.x, env, {bareValue: true, defaultDecimals: 1})
  const formatter = singleUseFormatter(valFormatter, valFormatterAlt)

  labelGroup.selectAll('*').remove()

  labelAxis.field().data.forEach(function (d, index) {
    labelAxis.series().forEach(function (s, seriesIndex) {
      const fieldVal = env.data.data[index][0]
      let fieldPos = fieldAxis.scale(fieldVal)
      fieldPos += fieldAxis.seriesScale(seriesIndex)

      const val = env.data.data[index][1 + seriesIndex]
      const valPos = env.layout.width()
      const seriesLength = labelAxis.series().length
      const vOffset = bandwidthOffset(fieldAxis.scale.bandwidth(), seriesLength)

      labelGroup.append('text')
        .classed('column-label', true)
        .classed('label-' + index, true)
        .text(formatter(val))
        .style('text-anchor', 'start')
        .attr('x', valPos + env.options.columnLabelOffset)
        .attr('y', fieldPos +
                (fieldAxis.scale.bandwidth() / (seriesLength * 2)) +
                vOffset)
    })
  })
}

// move into toaster labels
// label has position, orientation, and text
export function placeOffsetLabel (container, label, env) {
  const g = container.append('g.label')
    .attr('index', label.index)
    .attr('orientation', label.orientation)
    .attr('extend', label.extend)
    .classed('highlight', label.highlight)
    .translate(label.position)

  let barStart = [0, 0]
  let barEnd = [0, 0]
  let barOffset = env.options.labelLineOffset

  if (label.extend === 'top' || label.extend === 'bottom') {
    // draw a two-level line
    let barStart0 = [0, 0]
    let barEnd0 = [0, 0]
    let barLength0 = label.extendLength || env.options.labelExtendLength

    if (label.extend === 'top') {
      barStart0[1] -= barOffset
      barEnd0 = barStart0.slice(0)
      barEnd0[1] -= barLength0
    } else if (label.extend === 'bottom') {
      barStart0[1] += barOffset
      barEnd0 = barStart0.slice(0)
      barEnd0[1] += barLength0
    }

    g.append('line')
      .attr('x1', barStart0[0] + 'px')
      .attr('y1', barStart0[1] + 'px')
      .attr('x2', barEnd0[0] + 'px')
      .attr('y2', barEnd0[1] + 'px')

    barStart = barEnd0 // start the next line at the end
    barOffset = 0
  }

  if (label.orientation === 'right') {
    barStart[0] += barOffset
    barEnd = barStart.slice(0)
    barEnd[0] += env.options.labelLineLength
  } else if (label.orientation === 'left') {
    barStart[0] -= barOffset
    barEnd = barStart.slice(0)
    barEnd[0] -= env.options.labelLineLength
  }

  g.append('line')
    .attr('x1', barStart[0] + 'px')
    .attr('y1', barStart[1] + 'px')
    .attr('x2', barEnd[0] + 'px')
    .attr('y2', barEnd[1] + 'px')

  const textObject = {
    orientation: label.orientation,
    position: barEnd,
    line1: label.valueText,
    line2: label.labelText
  }

  placeMultiLineText(g, textObject, env)
}
