import { getDurationYears, dateToUTCString } from '../../utils/data'
import applyDateAxis from './dateAxis'
import {
  hideDuplicates,
  labelDensity,
  hideIntermediateTicksMobile
} from './utils'

export default function decadePlusAxis (selection, axis, env) {
  var numTicks = Math.max(2, Math.floor(axis.rangeLength() / (axis.tickStepHorizontal() * 2.0)))
  var durationYears = getDurationYears(axis.field(), axis.axisScale.domain())
  var formatters = [v => v.format('YYYY')]
  var options = {
    axisTextSingleLine: true,
    labelGutter: 30,
    noEndCap: false,
    endCapPriority: false
  }

  numTicks = Math.min(numTicks, durationYears)
  numTicks /= labelDensity(axis)

  axis.model()
    .ticks(numTicks)
    .tickFormat(function (d, i) {
      if (d.toUTCString) {
        return dateToUTCString(d, axis.field())
      }
    })

  selection.call(axis.model())

  applyDateAxis(selection, axis, formatters, env, options)

  hideIntermediateTicksMobile(selection, axis)
  hideDuplicates(selection, axis)

  return selection
}
