import { select } from '../d3-custom'
import { makeLayerPrototype } from '../utils/layers'
import {
  pie as d3pie,
  arc as d3arc
} from 'd3'
import {
  getSeries,
  valueFormatter
} from '../utils/data'
import { placeOffsetLabel } from './labels'
import {
  MOBILE_BREAKPOINT,
  PHABLET_BREAKPOINT,
  getAspectRatio
} from '../utils/layout'
import { default as simplePie } from '../layers/simplePie'
import { default as makeEnv } from './env'
import debugEnv from '../utils/debug'
import {default as _merge} from 'lodash-es/merge'

var PIE_MAX_SERIES = 6

// There is a positioning bug that only happens on safari, this is
// the only way I could think of to avoid
const isSafari = /^((?!chrome|android).)*safari/i.test(navigator.userAgent)
const safariL2Offset = isSafari ? 5 : 0

export default function pie (options) {
  var defaults = {
    // allow pie chart with no labels
    disablePieLabels: false,

    // allow small multiples to disable series sorting
    sortPieSeries: true,

    // distance of default label line
    labelLineLength: 50,
    // distance of vertical line in |__ label bar
    labelExtendLength: 30,
    // distance line sits from edge of pie
    labelLineOffset: 2,
    labelLine1Height: 26,
    labelLine2Height: 16,
    labelTextWrapWidth: 200,
    verticalMargin: 40,
    pieOuterRadius: 120,
    pieMaxRadius: 150,
    pieMinRadius: 60,
    // set to false to force radius to provided value
    automaticRadius: true,
    phabletOptions: {
      labelLine1Height: 20,
      labelLine2Height: 13,
      labelTextWrapWidth: 130,
      labelLineLength: 35
    },
    mobileOptions: {
      labelLine1Height: 18,
      labelLine2Height: 13,
      labelTextWrapWidth: 130,
      labelLineLength: 35
    }
  }
  options = _merge(defaults, options)

  const selections = []

  function pie (selection) {
    selection.each(function (chartData) {
      const el = select(this)
        .classed('toaster-chart-pie', true)
        .classed('pie-colors', true)

      const env = makeEnv(el, chartData, options)

      if (window.innerWidth <= PHABLET_BREAKPOINT &&
        window.innerWidth > MOBILE_BREAKPOINT) {
        Object.assign(env.options, options.phabletOptions)
      } else if (window.innerWidth <= MOBILE_BREAKPOINT) {
        Object.assign(env.options, options.mobileOptions)
      }

      env.options.labelLine2Height += safariL2Offset

      // apply axis labels etc to the field
      Object.assign(chartData.fields[1], chartData.secondaryAxis)

      if (chartData.data.length > PIE_MAX_SERIES) {
        throw new Error('Pie chart only supports up to 6 values')
      }

      var chart = el.selectAppend('div.chart')
        .classed('toaster-chart-pie', true)
      chart.selectAll('*').remove()

      var width = chart.node().offsetWidth
      var height = 380
      var svg = chart.selectAppend('svg')
        .style('width', width + 'px')
        .style('height', height + 'px')

      // uses aspectRatio to drive pie radius
      if (env.options.automaticRadius) {
        var ratioMultiplier = 6.0 / (3.0 + getAspectRatio(chartData))

        env.options.pieOuterRadius = Math.max(
          Math.min(
            width * (1 / 8),
            env.options.pieMaxRadius
          ),
          env.options.pieMinRadius
        ) * ratioMultiplier
      }
      var pan = svg.selectAppend('g.pie-container')

      pan.selectAll('*').remove()
      pan.call(simplePie(env.options))

      if (!env.options.disablePieLabels) {
        pieLabels(pan, env)
      }

      // after labels drawn, adjust scale and offset
      shiftAndScalePie(pan, svg, width, height, env)

      selections.push({ env, el })
      debugEnv(env)
    })
  }

  return makeLayerPrototype(pie, selections, options)
}

// used to disable legend before scaffold
pie.makeOptions = function (options) {
  return Object.assign({ hideLegend: true }, options)
}

// pie chart isn't using axes, but needs to pass one in
// to the formatter
function axisWrapper (data) {
  return {
    field: () => data.fields[1]
  }
}

function shiftAndScalePie (pan, svg, width, height, env) {
  // resize the view based on the size with labels
  const bbox = pan.node().getBBox()
  const yCenter = Math.abs(bbox.y - (env.options.verticalMargin / 2))
  let xCenter = width / 2
  let transformScale = 'scale(1.0)'
  height = bbox.height
  height += env.options.verticalMargin
  svg.style('width', width + 'px')
    .style('height', height + 'px')

  // on mobile we shift the chart left/right to fit
  // if adjustment isn't possible we scale down to fit box
  if (bbox.width > width) {
    var ratio = width / bbox.width
    transformScale = `scale(${ratio}) `
  }

  pan.attr('transform', `translate(${xCenter}, ${yCenter}) ${transformScale}`)
}

function pieLabels (container, env) {
  var labels = []
  var field = getSeries(env.data)[0]
  var pieData = d3pie()(field.data)
  var highlightedLabels = []

  if (env.data.config && env.data.config['highlight_label']) {
    highlightedLabels = env.data.config['highlight_label']
      .split(',')
      .map(s => Number(s))
  }

  // for pie chart if domain extent between 1000 - 10000 use commas
  var formatOptions = {}
  var domainMax = Math.max(Math.abs(field.max), Math.abs(field.min))
  if (domainMax >= 1000 && domainMax < 100000) {
    formatOptions.forceCommaDisplay = true
  }
  var format = valueFormatter(axisWrapper(env.data), env, formatOptions)

  container.selectAll('.label').remove()

  var labelArc = d3arc()
    .outerRadius(env.options.pieOuterRadius)
    .innerRadius(env.options.pieOuterRadius)

  env.data.data.forEach(function (data, i) {
    var pd = pieData[i]
    var centerAngle = (pd.startAngle + pd.endAngle) / 2
    var diff = Math.PI / 8

    var label = {
      position: labelArc.centroid(pd),
      orientation: 'right',
      extend: null,
      extendLength: env.options.labelExtendLength,
      valueText: format(pd.value), // TODO :: support percentage option
      labelText: data[0],
      centerAngle: centerAngle,
      pieIndex: pd.index,
      highlight: (highlightedLabels.indexOf(pd.index + 1) !== -1)
    }

    if (pd.value <= 0 || isNaN(pd.value)) return

    if (centerAngle < diff) {
      label.orientation = 'right'
      label.extend = 'top'
    } else if (centerAngle > (Math.PI * 2) - diff) {
      label.orientation = 'left'
      label.extend = 'top'
    } else if (centerAngle > (Math.PI - diff) && centerAngle <= Math.PI) {
      label.orientation = 'right'
      label.extend = 'bottom'
    } else if (centerAngle < (Math.PI + diff) && centerAngle > Math.PI) {
      label.orientation = 'left'
      label.extend = 'bottom'
    } else {
      if (centerAngle < Math.PI) {
        label.orientation = 'right'
      } else {
        label.orientation = 'left'
      }
    }

    labels.push(label)
  })

  // before we draw, count the number of labels that stack up
  // at the top (small values) and push up the extend value
  if (env.options.sortPieSeries) {
    labels.sort((a, b) => a.pieIndex - b.pieIndex)
  }
  var topExtenders = labels.filter(label => label.extend === 'top')
  if (topExtenders.length > 1) {
    var labelHeight = env.options.labelLine1Height +
      env.options.labelLine2Height +
      env.options.labelLineOffset * 2
    topExtenders.forEach((label, i) => {
      label.extendLength = env.options.labelExtendLength +
        labelHeight * i
    })
  }

  labels.forEach(label => placeOffsetLabel(container, label, env))
}
