import { select } from 'd3'
import {
  hideOverlappingTicks,
  applyLabelDensity,
  quarterLabelDensity,
  hideLine2Duplicates,
  drawDomain
} from './utils'

import {
  OUTPUT_FORMAT_YEARS,
  OUTPUT_FORMAT_QUARTERS,
  OUTPUT_FORMAT_MONTHS,
  OUTPUT_FORMAT_DAYS,
  OUTPUT_FORMAT_HOURS,
  fieldDateParser,
  formatDateAsQuarter
} from '../../utils/data'

var TICK_YVAL = 6
var TEXT_YVAL = 9
var TEXT_DYVAL = '0.71em'

/**
  The Periodic axis is different from year / month etc. in that it
  doesn't use a timeline but rather places ticks by iterating over
  the data and labelling values based off the rounded value at the
  data position.

  This essentially "rounds" the timeline from the given output format
  rather than using a d3 time-based tick.
*/
export default function periodicAxis (selection, axis, env, options) {
  options = options || {}
  var field = axis.field()
  var parseDate = fieldDateParser(field)
  var outputFormat = env.data.chartOptions.dateOutputFormat
  var formatter = valFormatter(outputFormat, parseDate, env)
  var formattedValues = []
  var tickValues = []
  var lastVal = null

  field.data.forEach((d, i) => {
    if (d) {
      var fVal = formatter(d)
      if (fVal !== lastVal) {
        tickValues.push([d, i])
        formattedValues.push(fVal)
        lastVal = fVal
      }
    }
  })

  axis.model().ticks(0)
  selection.call(axis.model())
  drawDomain(selection, axis, env)
  var ticks = selection.selectAll('.tick').data(tickValues)

  ticks.exit().remove()
  ticks.enter()
    .append('g')
    .attr('class', 'tick')
    .translate(d => {
      return [axis.axisScale(parseDate(d[0])), 0]
    })
    .append('line')
    .attr('stroke', '#000')
    .attr('y2', TICK_YVAL)

  selection.selectAll('.tick').each(function (d, i) {
    var tick = select(this)
    tick.select('text').remove()

    var fVals = formattedValues[i].split('//')
    var text = tick.append('text')
      .attr('fill', '#000')
      .attr('y', TEXT_YVAL)
      .attr('dy', TEXT_DYVAL)

    text.append('tspan')
      .attr('x', 0)
      .attr('y', TEXT_YVAL)
      .attr('dy', axis.axisTextMarginHorizontal() + 'px')
      .classed('line1', true)
      .text(fVals[0])

    if (fVals[1] && !options.axisTextSingleLine) {
      text.append('tspan')
        .attr('x', 0)
        .attr('y', TEXT_YVAL)
        .attr('dy', axis.axisTextMarginHorizontal() + axis.axisTextLineHeight() + 'px')
        .classed('line2', true)
        .text(fVals[1])
    }
  })

  hideOverlappingTicks(selection, axis)
  hideLine2Duplicates(selection, axis)

  if (outputFormat === OUTPUT_FORMAT_QUARTERS) {
    quarterLabelDensity(selection, axis)
  } else {
    applyLabelDensity(selection, axis)
  }

  return selection
}

// now supporting a config option to hide_year_display
function valFormatter (format, parseDate, env) {
  if (format === OUTPUT_FORMAT_YEARS) {
    return val => parseDate(val).format('YYYY')
  } else if (format === OUTPUT_FORMAT_MONTHS) {
    if (env.data.config && env.data.config.hide_year_display) {
      return val => parseDate(val).format('MMM')
    }
    return function (val) {
      const d = parseDate(val)
      return `${d.format('MMM')}//${d.format('YYYY')}`
    }
  } else if (format === OUTPUT_FORMAT_HOURS) {
    return function (val) {
      const d = parseDate(val)
      return `${d.format('H:mm')}//${d.format('MMM D')}`
    }
  } else if (format === OUTPUT_FORMAT_DAYS) {
    return function (val) {
      const d = parseDate(val)
      return `${d.format('ddd')}//${d.format('MMM D')}`
    }
  } else if (format === OUTPUT_FORMAT_QUARTERS) {
    if (env.data.config && env.data.config.hide_year_display) {
      return val => formatDateAsQuarter(parseDate(val))
    }
    return function (val) {
      const pVal = parseDate(val)
      const q = formatDateAsQuarter(pVal)
      return `${q}//${pVal.format('YYYY')}`
    }
  } else {
    // no formatting
    return val => val
  }
}
