import {
  select as d3select
} from 'd3'

import { parseSvg } from 'd3-interpolate/src/transform/parse'

import { makeAxisBounds, VERTICAL, shouldDisplayDomain, shouldDisplayDomainTicks } from '../../utils/layout'
import { renderMultiLineText } from '../../utils/text'
import {
  labelDensity,
  applyLabelDensity,
  hideDuplicates,
  hideLine2Duplicates,
  drawDomain
} from './utils'

export default function bandAxis (selection, axis, env, options) {
  options = options || {}
  var isVertical = axis.orientation() === VERTICAL
  var pad = axis.axisTextPadding()

  axis.model()
    .tickFormat(function (d) {
      if (env.options.categoricalFormatter) {
        return env.options.categoricalFormatter(d)
      } else {
        return d
      }
    })

  selection
    .call(axis.model())
    .classed('no-ticks', false)

  selection.selectAll('g.tick text').each(function (p, i) {
    const t = d3select(this)
    const textVal = t.text()
    t.parent().attr('tick-value', textVal)
  })

  // draw invisible bounds on the axis
  makeAxisBounds(selection, axis, isVertical)
  drawDomain(selection, axis, env)

  if (isVertical) {
    selection.selectAll('g.tick line')
      .attr('display', 'none')

    // multiline becomes single line in HBar mode
    if (env.options.multilineFormatter) {
      selection.selectAll('g.tick text')
        .each(renderMultiLineText({
          margin: axis.axisTextMarginHorizontal(),
          lineHeight: axis.axisTextLineHeight(),
          isHorizontalText: true
        }))
    }

    if (env.options.mobileColumnChart) {
      var offsetLabel = (axis.axisScale.bandwidth() / 2) * -1
      offsetLabel -= env.options.mobileColumnOffset || 0
      selection.selectAll('g.tick text')
        .classed('left-align', true)
        .attr('dy', offsetLabel + 'px')
    } else if (axis.verticalAxisWrapWidth) {
      selection.selectAll('g.tick text')
        .each(wrapText(axis.verticalAxisWrapWidth(), axis))
    }
  } else {
    if (env.options.multilineFormatter) {
      selection.selectAll('g.tick text')
        .each(renderMultiLineText({
          margin: axis.axisTextMarginHorizontal(),
          lineHeight: axis.axisTextLineHeight()
        }))
    } else if (axis.axisScale.step) {
      selection.selectAll('g.tick text')
        .each(wrapText(axis.axisScale.step() - pad, axis))
    }

    // no tick style for column / bar charts
    // only apply when domain is displayed
    if (shouldDisplayDomain(env) && !shouldDisplayDomainTicks(env)) {
      selection.classed('no-ticks', true)
      selection.selectAll('g.tick')
        .each(function () {
          const tick = d3select(this)
          const { translateX } = parseSvg(tick.attr('transform'))
          tick.translate([translateX, -6])
        })
    }
  }

  if (!isVertical) {
    hideDuplicates(selection, axis)
    hideLine2Duplicates(selection, axis)

    // apparently some people want to apply labelDensity to categorical
    if (labelDensity(axis) === 1) {
      // DISABLE OVERLAP for discrete bands
      // hideOverlappingTicks(selection, axis, { discrete: true })
    } else {
      applyLabelDensity(selection, axis)
    }
  }

  return selection
}

// if a label is longer than the width of an axis band, wrap it over
// multiple lines.
function wrapText (width, axis) {
  return function (d) {
    var newLineToken = '<N>'
    var newWidth = Number(width)
    var text = d3select(this)
    var textVal = text.text().replace(/\n|<br>/g, ' ' + newLineToken + ' ')
    var words = textVal.trim().split(/\s+/).reverse()
    var word
    var line = []
    var lineNumber = 0
    var isVertical = axis.orientation() === VERTICAL
    var lineHeight = axis.axisTextLineHeight() // px
    var y = text.attr('y')
    var dy = axis.axisTextMarginHorizontal()
    if (axis.orientation() === VERTICAL) {
      dy = axis.axisTextMarginVertical()
    }
    var tspan = text.text(null).append('tspan').attr('x', 0).attr('y', y).attr('dy', dy + 'px')
    var hasWrapped = false

    // vertical axis === horizontal bars
    if (isVertical) {
      lineHeight = axis.axisTextLineHeightHorizontal()
    }

    if (words.length === 1) {
      newWidth = 9999 // basically disable wrap
    }

    var pushCount = 0
    while (word = words.pop()) { // eslint-disable-line
      line.push(word)
      pushCount++
      tspan.text(line.join(' '))
      tspan.classed('line-' + (lineNumber + 1))
      tspan.attr('yval', lineNumber * lineHeight + dy)

      if ((tspan.node().getComputedTextLength() > newWidth && pushCount > 1) ||
          word === newLineToken) {
        if (word === newLineToken) word = ''
        hasWrapped = true
        line.pop()
        tspan.text(line.join(' ').trim())
        line = [word]
        tspan = text.append('tspan')
          .attr('x', 0)
          .attr('y', y)
          .attr('dy', ++lineNumber * lineHeight + dy + 'px')
          .attr('yval', lineNumber * lineHeight + dy)
          .text(word)
      }
    }

    // middle alignment happens in vertical layouts
    if (hasWrapped && axis.orientation() === VERTICAL) {
      var textHeight = text.node().getBoundingClientRect().height
      text.selectAll('tspan')
        .attr('dy', function () {
          var val = d3select(this).attr('yval')
          return val - (textHeight / 4) + 4 + 'px'
        })
    }
  }
}
