import {select} from '../d3-custom'
import {scaleBand} from 'd3-scale'
import {range} from 'd3-array'

// TODO:: move layout types to utils/layout
import * as layout from '../utils/layout'
import {
  numberOrNull,
  valueHighlighter,
  getSeries,
  getFieldIndexValueParser
} from '../utils/data'

export default function simpleColumn (options) {
  const defaults = {
    verticalColumnMinLength: 1,
    horizontalColumnMinLength: 2
  }
  options = Object.assign(defaults, options)
  let xScale = options.xScale
  let yScale = options.yScale
  const seriesOffset = options.seriesOffset || 0

  function simpleColumn (selection) {
    selection.each(function (chartData) {
      const el = select(this)
        .classed('simple-column', true)

      // create a child element that encapsulates the entire range in order
      // to force bounding box to match the "nice()" domain.
      el.selectAll('.bounding-rect').remove()
      el.append('rect')
        .classed('bounding-rect', true)
        .attr('x', xScale.range()[0])
        .attr('width', xScale.range()[1])
        .attr('y', yScale.range()[1])
        .attr('height', yScale.range()[0])
        .attr('fill', 'transparent')

      const categoricalData = getFieldIndexValueParser(chartData, true, true)
      const series = options.seriesData || getSeries(chartData)
      const seriesScale = scaleBand()
        .domain(range(series.length))

      if (options.orientation === layout.HORIZONTAL) {
        seriesScale.rangeRound([0, xScale.bandwidth()])
      } else {
        seriesScale.rangeRound([0, yScale.bandwidth()])
      }

      window.seriesScale = seriesScale

      // function to highlight a value by index
      const isHighlighted = valueHighlighter(chartData)

      if (seriesScale.bandwidth() < 1) {
        console.warn(chartData.id, 'Column width is less than 1px', chartData.data.length + ' columns')
      }

      let cseries = el.selectAll('.series')
        .data(series)

      cseries.exit().remove()

      cseries = cseries.enter()
        .append('g')
        .attr('class', function (d, i) {
          return 'series f series-' + (i + seriesOffset)
        })
        .merge(cseries)

      if (options.orientation === layout.HORIZONTAL) {
        cseries.attr('transform', function (d, i) {
          return 'translate(' + seriesScale(i) + ',0)'
        })
      } else {
        cseries.attr('transform', function (d, i) {
          return 'translate(0, ' + seriesScale(i) + ')'
        })
      }

      const columns = cseries.selectAll('.column')
        .data(function (d) {
          return d.data
        })

      columns.exit().remove()

      // HORIZONTAL equals vertical bars
      if (options.orientation === layout.HORIZONTAL) {
        columns.enter()
          .append('rect')
          .attr('class', 'column')
          .merge(columns)
          .classed('highlight', isHighlighted)
          .attr('x', (d, i) => {
            return xScale(categoricalData(i))
          })
          .attr('y', function (d) {
            return yScale(Math.max(0, numberOrNull(d)))
          })
          .attr('width', Math.max(1, seriesScale.bandwidth()))
          .attr('height', function (d) {
            if (d == null) return 0
            return Math.max(
              Math.abs(yScale(numberOrNull(d)) - yScale(0)),
              options.verticalColumnMinLength)
          })
      } else {
        // these are horizontal bars
        columns.enter()
          .append('rect')
          .attr('class', 'column')
          .merge(columns)
          .classed('highlight', isHighlighted)
          .attr('y', function (d, i) {
            return yScale(categoricalData(i))
          })
          .attr('x', function (d, i) {
            return xScale(Math.min(0, numberOrNull(d)))
          })
          .attr('height', seriesScale.bandwidth())
          .attr('width', function (d) {
            if (d == null) return 0
            return Math.max(
              Math.abs(xScale(numberOrNull(d)) - xScale(0)),
              options.horizontalColumnMinLength)
          })
      }
    })
  }

  simpleColumn.xScale = function (_) {
    return arguments.length ? (xScale = _, simpleColumn) : xScale // eslint-disable-line
  }

  simpleColumn.yScale = function (_) {
    return arguments.length ? (yScale = _.copy(), simpleColumn) : yScale // eslint-disable-line
  }

  return simpleColumn
}
