export const seriesThemes = {
  business,
  bizweek,
  green,
  'green-dark': greenDark,
  terminalX: terminal
}

export function seriesColors (theme) {
  // treat line and area types differently in color options etc.
  const columnTypes = ['bar', 'hbar', 'stackedVbar', 'stackedHbar', 'area']
  const lineTypes = ['line', 'barline']
  const values = {}

  lineTypes.forEach(type => {
    values[type] = filterLineColorSeries(theme, seriesThemes[theme])
  })
  columnTypes.forEach(type => {
    values[type] = seriesThemes[theme]
  })

  return values
}

export function toRGB (colorString) {
  // parses "rgb(0,0,0)"
  if (colorString && colorString.startsWith('rgb(')) {
    return colorString.replace(/^(rgb|rgba)\(/, '').replace(/\)$/, '').replace(/\s/g, '').split(',')
  // parses "#000000"
  } else if (colorString && colorString.startsWith('#')) {
    return /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec(colorString).slice(1).map(c => parseInt(c, 16))
  }
}

// allows us to determine to render black / white text on top of a color
export function luminance (r, g, b) {
  return (0.299 * r + 0.587 * g + 0.114 * b) / 255
}

// strip out color series options that don't apply to lines
function filterLineColorSeries (theme, values) {
  if (!values) return null
  let newValues = {}
  Object.keys(values).forEach(key => {
    newValues[key] = values[key].slice()
  }) // deep copy

  if (theme === 'bizweek') {
    delete newValues[1][1]
    delete newValues[2][0]
    delete newValues['all'][1]
    delete newValues['all'][2]
  } else if (theme === 'terminal') {
    newValues = terminalLineColors
  } else {
    delete newValues[1][1]
    delete newValues[2][1]
    delete newValues[2][2]
    delete newValues[5][1]
    delete newValues[5][2]
    delete newValues[6][1]
    delete newValues[6][2]
    delete newValues['all'][1]
    delete newValues['all'][2]
  }

  newValues[1] = newValues[1].filter(v => v)
  newValues[2] = newValues[2].filter(v => v)
  newValues[5] = newValues[5].filter(v => v)
  newValues[6] = newValues[6].filter(v => v)
  newValues['all'] = newValues['all'].filter(v => v)

  return newValues
}
