import { select } from '../d3-custom'
import { format as d3format } from 'd3'
import pie from './pie'
import _merge from 'lodash-es/merge'
import { getSeries, valueFormatter } from '../utils/data'
import { makeLayerPrototype } from '../utils/layers'

export default function customPie (options) {
  const defaults = {
    // default options here
    legendOffsetTop: -15,
    legendLineHeight: 16,
    customPieDisableLegend: false,
    customPieLegendPercentage: false,
    sortPieSeries: false
  }
  options = _merge(defaults, options)

  const pieRenderer = pie(options)

  // we need a way to store env, el for each selection and
  // expose that out to any calling function
  const selections = []

  function customPie (selection) {
    pieRenderer(selection)
    selection.each(function (chartData, i) {
      const el = select(this)
      const pieField = getSeries(chartData)[0]
      const env = {
        data: chartData,
        options: Object.assign({}, options)
      }

      if (!env.options.customPieDisableLegend) {
        const formatPrimary = valueFormatter(axisWrapper(env.data), env)
        const formatPercent = val => d3format('3d')(val * 100.0).replace(/ /g, '')
        const legendOuter = selection
          .selectAppend('div.legend-container-outer')
        const legend = legendOuter
          .selectAppend('div.legend-container')

        legendOuter
          .style('height', (pieField.data.length * env.options.legendLineHeight) + 'px')

        const values = legend.selectAll('.legend-value').data(pieField.data)
        const totalVal = pieField.data.reduce((a, b) => a + b)
        values.exit().remove()
        values.enter()
          .append('div')
          .classed('legend-value', true)
          .merge(values)
          .html(function (d, i) {
            if (d === 0 || d === '0') {
              return ''
            }
            let percentageContent = ''
            if (env.options.customPieLegendPercentage) {
              percentageContent = `(${formatPercent(d / totalVal)}%)`
            }
            return `
                <span class="swatch bg series-${i}"></span>
                <span class="val">
                 ${formatPrimary(d)} ${percentageContent}
                </span>
              `
          })
      }

      selections.push({ el, env })
    })
  }

  return makeLayerPrototype(customPie, selections, options)
}

// pie chart isn't using axes, but needs to pass one in
// to the formatter
function axisWrapper (data) {
  return {
    field: () => data.fields[1]
  }
}
