import { select } from '../../d3-custom'
import { parseSvg } from 'd3-interpolate/src/transform/parse'
import {
  ticks as d3ticks,
  range as d3range
} from 'd3'
import {
  prepareFormatterOptions,
  hasCustomDomainStep,
  valueFormatter,
  hasCustomDomain,
  greatestCommonDivisor,
  validator,
  isMixedDomain,
  DISPLAY_AS_MULTIPLIERS as multipliers
} from '../../utils/data'
import {
  shouldDisplayDomain,
  VERTICAL
} from '../../utils/layout'
import { getVisibleTicks } from './utils'

const MAX_VERTICAL_TICKS = 8
const MAX_VERTICAL_TICKS_CUSTOM = 14
const MIN_TICK_HEIGHT = 25

// caps out at 10 but increases until we get a whole number divisor
const getNumTicksFromDomain = (numTicks, domain) => {
  let foundValue = false
  while (!foundValue && numTicks < MAX_VERTICAL_TICKS) {
    let tickValues = d3ticks(domain[0], domain[1], numTicks)
    if (tickValues[tickValues.length - 1] === domain[1]) {
      foundValue = true
    } else {
      numTicks++
    }
  }

  return numTicks
}

const applyCustomStep = (step, domain) => {
  const numTicks = Math.floor(
    Math.min(MAX_VERTICAL_TICKS_CUSTOM,
      Math.max(2, Math.abs(domain[1] - domain[0]) / step)))
  const customSteps = d3range(numTicks + 1).map(i => domain[0] + (i * step))
  const customDomain = [customSteps[0], customSteps[customSteps.length - 1]]
  return [customSteps, customDomain]
}

const displayAsMultiplier = (axis) => {
  let multiplier = 1
  if (axis.field().display_as && multipliers[axis.field().display_as]) {
    multiplier = multipliers[axis.field().display_as]
  }
  return v => v * multiplier
}

/**
  Right now this is both vertical and horizontal
*/
export default function numericAxis (selection, axis, env, options) {
  const valueMultiplier = displayAsMultiplier(axis)
  const isVertical = (axis.orientation() === VERTICAL)
  const rangeMax = isVertical ? axis.range()[0] : axis.range()[1]
  const tickStep = isVertical ? axis.tickStepVertical() : axis.tickStepHorizontal()
  const customDomain = hasCustomDomain(env.data)
  const tickAtDomainTop = customDomain || env.options.forceTickAtDomainTop
  const minTicks = env.options.minimumTicks == null ? 3 : env.options.minimumTicks
  const forceNoTicks = env.options.forceNoTicks
  let formatOptions = {
    bareValue: true,
    voidDecimals: true,
    forceCommaDisplay: env.options.forceCommaDisplay || env.data.config.forceCommaDisplay
  }
  let formatter = valueFormatter(axis, env, formatOptions)
  let domain = axis.axisScale.domain()
  let numTicks = Math.min(MAX_VERTICAL_TICKS,
    Math.max(minTicks, Math.floor(rangeMax / tickStep)))
  let axisNode
  let ticks
  let tickValues

  // apply nice() with numTicks only if not a customDomain
  if (!customDomain) {
    axis.axisScale.nice(Math.max(numTicks, minTicks))
    domain = axis.axisScale.domain()
  }

  axis.numTicks(numTicks)
  axis.model()
    .ticks(numTicks)
    .tickFormat(formatter)

  // custom domain step
  if (tickAtDomainTop) {
    numTicks = getNumTicksFromDomain(minTicks, domain)
    tickValues = d3ticks(domain[0], domain[1], numTicks)
    if (tickValues.length > numTicks && isMixedDomain(env.data)) {
      const customStep = greatestCommonDivisor(domain[0], domain[1])
      const [customSteps] = applyCustomStep(customStep, domain)
      if (customSteps.length < tickValues.length) {
        numTicks = customSteps.length
        tickValues = customSteps
      }
    }
    axis.numTicks(numTicks)
    axis.model().tickValues(tickValues)
  }

  if (hasCustomDomainStep(env.data)) {
    const step = env.data.chartOptions.scaleStep
    domain = axis.axisScale.domain()
    const [customSteps, customDomain] = applyCustomStep(step, domain)
    numTicks = customSteps.length
    axis.numTicks(numTicks)
    axis.model().tickValues(customSteps)
    axis.axisScale.domain(customDomain)
  }

  // this apparatus is to determine the best way to display the
  // axis values based on the values themselves
  const mDomain = domain.map(valueMultiplier)
  tickValues = d3ticks(mDomain[0], mDomain[1], numTicks)
  if (forceNoTicks) {
    tickValues = mDomain
    axis.model().tickValues(tickValues)
  }

  formatOptions = prepareFormatterOptions(
    tickValues,
    formatOptions,
    axis,
    env
  )
  formatter = valueFormatter(axis, env, formatOptions)
  axis.model().tickFormat(formatter)

  axisNode = selection.call(axis.model())
    .classed(axis.position(), true) // TODO :: move to layout
    .classed('vcenter', !!axis.val('axisTextCenterVertical'))

  const enoughSpaceForTickUnderneath = (axis.rangeLength() / tickValues.length > MIN_TICK_HEIGHT)

  if (isVertical && !enoughSpaceForTickUnderneath) {
    const validate = validator(env.data, options)
    validate('Too many ticks for proper grid display. Please set custom step.', () => true)
  }

  // IF NEW STYLE AXIS
  if (isVertical) {
    axisNode.classed('no-domain', true)
  }
  if (isVertical && enoughSpaceForTickUnderneath) {
    let cWidth = 0  // TODO :: calc from longest number
    let cOffset = 8
    axisNode.classed('no-line', axis.noDomainLine())
    axisNode.classed('variant-above', true)

    const texts = axisNode.selectAll('.tick text')
    texts.each(function (d, i) {
      const el = select(this)
      const text = el.text()
      const numeric = text.replace(/[^\d,.-]/g, '')
      const rest = text.split(numeric) // keep pre

      // zero line
      if (Number(numeric) === 0) {
        select(el.node().parentElement).classed('zero-line', true)
      }
      if (i === texts.size() - 1) {
        el.html(`<tspan class="pre">${rest[0]}</tspan><tspan class="val">${numeric}</tspan><tspan class="post">${rest[1]}</tspan>`)
      } else {
        el.html(`<tspan class="val">${numeric}</tspan>`)
      }
    })

    // calculate width of widest tick
    axisNode.selectAll('.tick tspan.val').each(function (d, i) {
      if (this.getComputedTextLength) {
        cWidth = Math.max(cWidth, this.getComputedTextLength())
      }
    })

    axisNode.select('.tick').classed('base', true)
    axisNode.selectAll('.tick').each(function (d, i) {
      // without the weirdOffset the lines don't perfectly match
      const weirdOffset = i === 0 ? -0.5 : 0
      const tick = select(this)
      const { translateY } = parseSvg(tick.attr('transform'))
      tick.translate([(cWidth - cOffset), translateY + weirdOffset])
    })

    axisNode.selectAll('.tick text')
      .attr('dy', '-9px')

    axisNode.selectAll('.tick line')
      .style('visibility', 'visible')
      .style('display', 'block')
      .attr('x1', ((cWidth * -1) + cOffset))
      .attr('x2', 8)

    extendTick(axisNode.select('.tick.base'), cWidth, env)
  }

  selection.node().getBoundingClientRect()

  ticks = selection.selectAll('g.tick')

  const negativeAxis = axis.axisScale.domain()[0] < 0

  // hide every other label and the first (at bottom)
  if (axis.tickLabelAlternate()) {
    const modHide = ticks.size() % 2 === 0 ? 0 : 1
    const hideFirstTick = ticks.size() > 5
    ticks.each(function (d, i) {
      if (negativeAxis) {
        if (i % 2 === modHide) {
          select(this).classed('hide-text', true)
        }
      } else {
        if ((hideFirstTick && i === 0) || i % 2 === modHide) {
          select(this).classed('hide-text', true)
        }
      }
    })
  }

  // if only one tick is visible we must show the bottom tick
  // to build a range
  if (getVisibleTicks(selection).length === 1) {
    select(ticks.nodes()[0]).classed('hide-text', false)
  }

  return selection
}

// This is a tricky one -- in order to get the domain to match
// up 100% with the bottom tick it's better to extend the domain
// out to the edge rather than try to line it up. So we do this:
function extendTick (baseTick, tickWidth, env) {
  if (shouldDisplayDomain(env)) {
    env.layout.addPostLayoutHandler((el, env, layout) => {
      const domain = env.g.select('.axis-x .domain')
      const dTokens = domain.attr('d').split('L') // MX,0,LX2,0   --  M-22,0V0H512V0
      let width = layout.outerWidth() - layout.yAxisInnerPadding()
      width += tickWidth

      domain.attr('d', `${dTokens[0]}L${width},0`)
      domain.attr('tick-extend-has-executed', true)
      baseTick.select('line').style('display', 'none')
    })
  }
}
