import {
  select as d3select,
  ticks as d3ticks
} from 'd3'

import {
  DURATION_HOURS,
  DURATION_DAYS,
  DURATION_WEEKS,
  DURATION_QUARTERS,
  isForceCategorical,
  parseDate
} from '../../utils/data'
import { VERTICAL, MOBILE_AXIS_WIDTH } from '../../utils/layout'

const MIN_WIDTH_ABBREVIATE = 70
const MIN_WIDTH_START_CAP = 80

/**
* Each duration type will have a specific padding associated with it in order
* for labels to be properly aligned to the end caps
*/
export function axisPadding (chartData) {
  if (chartData.fields[0] && chartData.fields[0].taxonomy) {
    if (chartData.fields[0].taxonomy.duration === DURATION_HOURS ||
      chartData.fields[0].taxonomy.duration === DURATION_DAYS ||
      chartData.fields[0].taxonomy.duration === DURATION_WEEKS) {
      return 25
    } else if (chartData.fields[0].taxonomy.duration === DURATION_QUARTERS) {
      return 0 // 0 because band axis
    }

    if (isForceCategorical(chartData.fields[0])) {
      return 0 // forced to band axis
    }
  }
  return 18
}

/**
* A utility function to run through and hide ticks when they are too
* densely packed and have gone through the formatter already
*/
export function hideOverlappingTicks (selection, axis, tickOptions) {
  const options = tickOptions || {}
  const checkOnly = options.checkOnly
  const ignoreStartEnd = options.ignoreStartEnd
  const padding = 4
  const ticks = getVisibleTicks(selection)
  if (ticks.length < 3) return

  let hasOverlaps = false
  let lastTick = null
  let lastTickBounds = null // store because it's an expensive call

  ticks.forEach((t, i) => {
    if (!lastTick) {
      lastTick = t
      lastTickBounds = t.getBoundingClientRect()
      return
    }

    if (checkOnly && hasOverlaps) return
    const bounds = t.getBoundingClientRect()
    if (lastTickBounds.right + padding > bounds.left) {
      hasOverlaps = true
      if (checkOnly) return
      const currentTick = d3select(t)

      if (ignoreStartEnd) {
        currentTick.classed('hide-tick', true)
      } else {
        // special case for axes that define start/end caps
        if (!currentTick.classed('start-tick') &&
            !currentTick.classed('end-tick')) {
          currentTick.classed('hide-tick', true)
        } else if (lastTick) {
          d3select(lastTick).classed('hide-tick', true)
        }

        if (currentTick.classed('end-tick')) {
          currentTick.classed('hide-tick', false)
        }
      }
    } else {
      lastTick = t
      lastTickBounds = bounds
    }
  })

  return hasOverlaps
}

// format year display
export function formatYearDisplay (selection, axis, options) {
  options = options || {}
  options.spacing = options.spacing || 40
  if (options.forceFormat ||
      hideOverlappingTicks(selection, axis, { checkOnly: true })) {
    const ticks = getVisibleTicks(selection)
    const toYear = (v) => Number(parseDate(v).format('YYYY'))
    const domain = axis.scale.domain()
    const domainYear = [toYear(domain[0]), toYear(domain[domain.length - 1])]
    const numTicks = axis.rangeLength() / options.spacing // TODO :: env.option for 40
    const tickValues = d3ticks(domainYear[0], domainYear[1], numTicks)
    const yearVisible = {}
    ticks.forEach((t, i) => {
      const tick = d3select(t)
      const yearVal = Number(tick.attr('tick-value'))
      if (tickValues.indexOf(yearVal) !== -1 && !yearVisible[yearVal]) {
        yearVisible[yearVal] = true
        tick.classed('hide-tick', false)
      } else {
        tick.classed('hide-tick', true)
      }

      // if start / end make sure it's unnabreviated
      if (options.abbreviations && yearVal === tickValues[0]) {
        tick
          .classed('start-tick', true)
          .select('tspan')
          .text(yearVal)
      }
      if (options.abbreviations && yearVal === tickValues[tickValues.length - 1]) {
        tick
          .classed('end-tick', true)
          .select('tspan')
          .text(yearVal)
      }
    })

    // now actually hide the overlaps
    hideOverlappingTicks(selection, axis)
  }
}

export function abbreviateYears (selection, axis) {
  const ticks = getVisibleTicks(selection)
  const tickWidth = axis.rangeLength() / ticks.length

  if (tickWidth < MIN_WIDTH_ABBREVIATE) {
    ticks.forEach((t, i) => {
      if (i !== 0 && i !== ticks.length - 1) {
        d3select(t)
          .select('tspan')
          .text(function () {
            return `'${d3select(this).text().slice(2)}`
          })
      }
      if (i === 0) {
        d3select(t).classed('tick-start', true)
      } else if (i === ticks.length - 1) {
        d3select(t).classed('tick-end', true)
      }
    })
  }
}

// return ticks that are visible as nodes
// if start-tick and end-tick nodes exist position them
// in the proper order
export function getVisibleTicks (selection) {
  const ticks = selection.selectAll('.tick').nodes()
  const filtered = ticks.filter((tickNode, i) => {
    var t = d3select(tickNode)
    return !(t.classed('hide-text') ||
      t.classed('hide-tick') ||
      t.classed('start-tick') ||
      t.classed('end-tick'))
  })

  const startTick = selection.select('.start-tick')
  const endTick = selection.select('.end-tick')

  if (startTick.size()) filtered.unshift(startTick.node())
  if (endTick.size()) filtered.push(endTick.node())

  return filtered
}

// apply start-tick and end-tick to ticks
export function applyStartEndTick (selection) {
  const ticks = selection.selectAll('.tick').nodes()
  if (ticks[0]) {
    d3select(ticks[0]).classed('start-tick', true)
  }
  if (ticks[ticks.length - 1]) {
    d3select(ticks[ticks.length - 1]).classed('end-tick', true)
  }
}

/**
* Check if the chart falls under mobile width constraints and hide
* all ticks between first and last
* When start-tick / end-tick present just use those
*/
export function hideIntermediateTicksMobile (selection, axis) {
  if (axis.rangeLength() <= MOBILE_AXIS_WIDTH) {
    if (selection.select('.start-tick').size()) {
      selection.selectAll('.tick').classed('hide-tick', true)
      selection.select('.start-tick').classed('hide-tick', false)
      selection.select('.end-tick').classed('hide-tick', false)
    } else {
      const ticks = selection.selectAll('.tick')

      ticks.filter((t, i) => (i !== 0 && i !== ticks.size() - 1))
        .classed('hide-tick', true)
    }
  }
}

/**
* A utility function to run through and hide any repeated ticks
*/
export function hideDuplicates (selection, axis) {
  var ticks = getVisibleTicks(selection)

  var lastTickText = null
  ticks.forEach(tick => {
    tick = d3select(tick)
    if (!lastTickText) {
      lastTickText = tick.text()
      return
    }

    if (tick.text() === lastTickText) {
      tick.classed('hide-tick', true)
    } else {
      lastTickText = tick.text()
    }
  })
}

/**
* A utility function to run through and hide line2 text that is
* repeated. (ie Jan 2018 will only show year on first appearance)
*/
export function hideLine2Duplicates (selection, axis) {
  const ticks = getVisibleTicks(selection)
  const field = axis.field()

  // hide all years on yearless sets (months)
  if (field.taxonomy && field.taxonomy.noYears) {
    selection.selectAll('.tick')
      .classed('hide-line2', true)
    return
  }

  var lastTickText = null
  ticks.forEach(tick => {
    tick = d3select(tick)
    if (!tick.select('.line2').size()) return

    if (!lastTickText) {
      lastTickText = tick.select('.line2').text()
      return
    }

    if (tick.select('.line2').text() === lastTickText) {
      tick.classed('hide-line2', true)
    } else {
      lastTickText = tick.select('.line2').text()
    }
  })
}

/**
* When a custom density is applied we run through and hide ticks as
* necessary.
*/
export function applyLabelDensity (selection, axis) {
  const density = labelDensity(axis)
  if (density === 1) return

  let ticks = getVisibleTicks(selection)

  // get rid of the first and last
  ticks = ticks.slice(1, ticks.length - 1)

  ticks.forEach((tick, i) => {
    if ((i + 1) % density !== 0) {
      tick = d3select(tick)
      if (!tick.classed('do-not-hide')) {
        tick.classed('hide-tick', true)
      }
    }
  })
}

export function labelDensity (axis) {
  var field = axis.field()
  if (!field['auto_density'] && field['label_density']) {
    return Math.max(field['label_density'], 1)
  }
  return 1
}

export function quarterLabelDensity (selection, axis) {
  // we need custom label density hiding for quarters
  if (labelDensity(axis) > 1) {
    var density = labelDensity
    var visibleTicks = getVisibleTicks(selection)

    visibleTicks.forEach(tick => {
      tick = d3select(tick)
      if (Number(tick.attr('quarter')) !== 1) {
        tick.classed('hide-tick', true)
      }
    })

    d3select(visibleTicks[visibleTicks.length - 1])
      .classed('do-not-hide', true)

    if (density > 2) {
      applyLabelDensity(selection, axis)
    }
  }
}

