export function drawDomain (selection, axis, env) {
  var isVertical = axis.orientation() === VERTICAL

  // get rid of the hanging chads on the domain line
  if (isVertical) {
    selection.select('.domain')
      .attr('d', 'M' + (env.layout.xAxisInnerPadding() * -1) +
        ',0V0H' + (axis.axisScale.range()[0] +
        env.layout.xAxisInnerPadding()) + 'V0')
      .classed('vertical-baseline', true)
  } else {
    selection.select('.domain')
      .attr('d', 'M' + (env.layout.yAxisInnerPadding() * -1) +
        ',0L' + (axis.axisScale.range()[1] +
        env.layout.yAxisInnerPadding()) + ',0')
      .classed('horizontal-baseline', true)
  }
}

/**
* Simple utility function to check if hide_year_display is enabled
* and remove the second (YEAR) display formatter
*/
export function filterYearFormatter (formatters, env) {
  if (env.data.config && env.data.config.hide_year_display) {
    return [formatters[0]]
  }
  return formatters
}

// The start cap for year display is tricky
// if in first half of year and enough room between years we
// include one
export function yearStartCapEnabled (axis, numTicks, env) {
  const domain = axis.scale.domain()
  const tickWidth = axis.rangeLength() / numTicks
  const startMonth = Number(parseDate(domain[0]).format('M'))
  if (startMonth < 7 && tickWidth > MIN_WIDTH_START_CAP) {
    return true
  }
  return false
}
