import { select } from 'd3'
import line from './line'
import area from './area'
import column from './column'
import multiplesPie from './multiples-pie'
import {
  buildTaxonomy,
  validator,
  isTimeSeries,
  isMultipleValues
} from '../utils/data'
import { applyOptionsToData } from '../utils/options'
import debugEnv from '../utils/debug'
import _merge from 'lodash-es/merge'
import _clone from 'lodash-es/cloneDeep'
import { getAspectRatio, shouldDisplayGrid, shouldDisplayDomain } from '../utils/layout'
import { makeLayerPrototype } from '../utils/layers'

const TYPE_MAP = {
  'line': line,
  'area': area,
  'bar': column,
  'pie': multiplesPie
}

export default function spread (options) {
  const defaults = {
    // default options here
    defaultBoxType: 'line',
    minimumBoxWidth: 200,
    minimumBoxHeight: 200,
    gutterWidth: 25,

    // make sure pie chart has no labels
    disableGrid: true,
    disablePieLabels: true,
    pieOuterRadius: 120,
    pieMinRadius: 20,
    automaticRadius: false,
    forceTickAtDomainTop: true,

    // configuration options for layout manager
    layoutManager: {
      minHeight: 300
    }
  }
  options = _merge(defaults, options)

  // we need a way to store env, el for each selection and
  // expose that out to any calling function
  const selections = []

  function spread (selection) {
    selection.each(function (chartData, i) {
      const env = {
        x: null, // simpleAxis({orientation: 'x'}),
        y: null, // simpleAxis({orientation: 'y'}),
        r: null,
        layout: null, // simpleLayout(),
        layers: [],
        options: Object.assign({}, options)
      }
      const el = select(this)
        .classed('toaster-chart-spread', true)

      // ensure we have a taxonomy specified on the data
      buildTaxonomy(chartData)
      env.data = chartData

      const chartType = chartData.config.multiples_type || env.options.defaultBoxType

      if (chartType === 'area') {
        el.classed('area-colors', true)
      }

      // custom legend for pie, otherwise we hide it
      if (chartType === 'pie') {
        var legendVals = chartData.data.map(d => {
          return {name: d[0]}
        })
        el.select('.legend')
          .call(customLegend(legendVals))
      } else {
        el.select('.legend')
          .style('display', 'none')
      }

      el.classed('no-grid', !shouldDisplayGrid(env))
        .classed('no-domain', !shouldDisplayDomain(env))

      // ensure bar charts are vertical bars
      chartData.config.force_vertical_bars = true

      const spreadData = splitData(chartData, env.options)
      const chart = el.select('.chart')
      const chartWidth = chart.node().offsetWidth
      const chartTypeClass = TYPE_MAP[chartType]
      // let series = chartData.fields.slice(1)
      let series = spreadData
      let boxWidth = Math.ceil(chart.node().offsetWidth / series.length) - 6
      let boxHeight = (chart.node().offsetWidth / getAspectRatio(chartData))

      // when boxes are too small we start wrapping them
      if (boxWidth < env.options.minimumBoxWidth) {
        let numRows = Math.ceil(env.options.minimumBoxWidth * series.length / chart.node().offsetWidth)
        boxWidth = chart.node().offsetWidth / Math.ceil(series.length / numRows) - 6
        boxHeight /= numRows
      }

      boxHeight = Math.max(boxHeight, env.options.minimumBoxHeight)
      const numPerRow = Math.floor(chartWidth / boxWidth)
      const gutterPadding = env.options.gutterWidth * (numPerRow - 1) / numPerRow

      env.options.pieOuterRadius = Math.max(
        env.options.pieMinRadius || 0,
        Math.min(
          env.options.pieMaxRadius || 1000,
          ((boxWidth - 50) / 2) - 20))

      env.options.layoutManager.minHeight = boxHeight

      // el.selectAll('.spread-container').remove()
      const spreads = chart.selectAll('.spread').data(spreadData)

      spreads.exit().remove()
      spreads.enter()
        .append('div')
        .attr('class', function (d, i) {
          return 'spread spread-' + i
        })
        .merge(spreads)
        .style('width', (boxWidth - gutterPadding) + 'px')
        .style('padding-right', (d, i) => {
          // last item on row has no padding-right
          if (i % numPerRow === numPerRow - 1) {
            return 0
          } else {
            return env.options.gutterWidth + 'px'
          }
        })
        // .style('height', boxHeight + 'px')

      chart.selectAll('.spread').each(function (d, i) {
        const container = select(this)
        const drawLayer = chartTypeClass(env.options)

        container.selectAppend('div.title')
          .text(spreadData[i].fields[1].name)
        container.selectAppend('div.toaster-chart')
          .datum(spreadData[i])
          .call(drawLayer)

        selections.push({
          el: container,
          env: drawLayer.getEnv()
        })
      })

      debugEnv(env)
    })
  }

  return makeLayerPrototype(spread, selections, options)
}

// Validation is a static method to check if the class
// supports the chartData before building the chart
spread.validateData = function (chartData, options) {
  const validate = validator(chartData, options)

  validate('Line chart must have more than one value.', isMultipleValues)
  validate('Line should be a time series', isTimeSeries)

  return validate
}

function customLegend (series) {
  function customLegend (selection) {
    selection.each(function (chartData) {
      var items = select(this)
        .selectAppend('ul.simple-legend')
        .selectAll('li')
        .data(series)

      items.exit().remove()

      items = items.merge(items.enter()
        .append('li'))

      items.selectAppend('span')
        .attr('class', function (d, i) {
          return 'swatch bg series-' + i
        })
        .classed('highlight-legend', d => d.isHighlight)

      items.selectAppend('label')
        .text(function (d) {
          return d.name
        })

      if (series.length === 0) {
        select(this).classed('hidden', true)
      }
    })
  }

  return customLegend
}

/**
* The purpose of this function is to take any given chartData and
* duplicate it on a per-field level so each small multiple chart
* gets a one-series copy of the data (categorical field + numerical)

* splitMultiples can be defined as config/options like: 1-2, 3-4 etc.
*/
export function splitData (chartData, options) {
  let indices = chartData.fields.slice(1).map((v, i) => [i + 1])
  if (chartData.config.splitMultiples || options.splitMultiples) {
    const splitString = options.splitMultiples || chartData.config.splitMultiples
    indices = splitString.split(',').map(s => {
      return s.trim().split('-').map(v => Number(v))
    })
  }

  return indices.map((group) => {
    let [start, end] = group
    end = end || start
    const clone = _clone(chartData)
    const field = chartData.fields[start]

    // leftover remnant of editor
    field.caption = null

    clone.fields = [chartData.fields[0]]
    for (let i = start; i <= end; i++) {
      clone.fields.push(chartData.fields[i])

      if (chartData.annotations) {
        clone.annotations = chartData.annotations
          .filter(annotation => annotation.series === (i + 1))
          .map(annotation => {
            annotation.series = 1 + (i - start)
            return annotation
          })
      }
    }

    // pluck the data
    clone.data = clone.data.map(row => {
      const newRow = [row[0]]
      for (let i = start; i <= end; i++) {
        newRow.push(row[i])
      }
      return newRow
    })

    return clone
  })
  .map((clone, index) => {
    return applyMultiplesOptions(clone, options, index)
  })
}

/**
 * Allow custom properties to be set via options that override
 * units etc. for each small multiple
 * options.multiples = { 1: {}, 2: {}, 3: {}}
 */
export function applyMultiplesOptions (chartData, options, index) {
  if (options.multiples &&
    (options.multiples[index + 1] || options.multiples[(index + 1) + ''])) {
    const mOptions = options.multiples[index + 1] || options.multiples[(index + 1) + '']
    const newOptions = _merge(_clone(options), mOptions)

    return applyOptionsToData(chartData, newOptions)
  }
  return chartData
}
