import {
  scalePoint,
  scaleUtc
} from 'd3'

import { buildAxis } from './format'
import { objectAccessor } from '../utils/object'

import * as dataUtils from '../utils/data'
import * as layout from '../utils/layout'

export default function dateAxisProvider (options) {
  var state = {
    type: 'date',
    isPrimaryAxis: true, // this axis is typically the primary (x) axis
    orientation: layout.HORIZONTAL,
    position: layout.BOTTOM,
    data: null,
    model: null,
    field: null,
    series: null,
    range: [0, 500],
    domain: [0, 100],
    padding: 0,
    tickStep: 30, // ideal pixels between steps
    tickStepHorizontal: 80,
    axisTextMarginHorizontal: 16,
    axisTextLineHeight: 18,
    axisTextPadding: 6,
    tickLabelAlternate: false,
    numTicks: 1 // used to store number of ticks used
  }

  state = Object.assign(state, options || {})

  // bind to the element we want to draw to
  var axis = function (selection, env) {
    selection.selectAll('*').remove() // clear

    // TODO:: better way of matching mediaQuery
    if (window.innerWidth < 600) {
      axis.axisTextMarginHorizontal(10)
      axis.axisTextLineHeight(14)
    }

    if (!env.options.disableAxis) {
      buildAxis(selection, axis, env)
    }

    return selection
  }

  // turn the whole thing into one giant accessor
  objectAccessor(axis, state)

  // scale and seriesScale are all no longer part of the
  // state accessor - this is because they are all callable functions
  axis.scale = scalePoint()
  axis.axisScale = null

  // this is needed to initialize scale, range etc.
  axis.data = function (data) {
    if (data) {
      axis.series(dataUtils.getSeries(data))
      axis.field(dataUtils.getField(data, state.isPrimaryAxis))

      const field = axis.field()

      axis.scale
        .domain(field.data)
        .range(axis.range())
      // .padding(0)

      if (field.taxonomy && field.taxonomy.isDate && field.taxonomy.isContinuous) {
        var timeRange = [
          field.taxonomy.dateStart,
          field.taxonomy.dateEnd
        ]
        var dateAxis = scaleUtc()
          .domain(timeRange)
          .range(axis.range())

        // TODO :: this should be default
        if (data.config.forceDateScale) axis.scale.domain(timeRange)
        axis.scale = dateAxis
      }

      // set model to d3.axisBottom / axisLeft etc.
      axis.axisScale = axis.scale
      axis.model(layout.AXIS_MAP[state.position](axis.axisScale))

      state['data'] = data
      return axis
    } else {
      return state['data']
    }
  }

  // return the axis scale value from item index
  axis.scaleIndex = function (index) {
    return axis.scale(axis.field()[index])
  }

  // given an x,y coordinate get the data at that position for
  // tooltips etc.
  axis.getDataAtPosition = function (position) {
    const data = []
    const targetPos = position * axis.rangeLength()
    const getVal = dataUtils.fieldValueParser(axis.field(), options.forceCategorical)
    let closestIndex = 0
    let closestDistance = 9999999

    // now loop through data and for index closest to targetVal
    axis.field().data.forEach((val, i) => {
      const dVal = getVal(val)
      if (Math.abs(axis.scale(dVal) - targetPos) < closestDistance) {
        closestDistance = Math.abs(axis.scale(dVal) - targetPos)
        closestIndex = i
      }
    })

    axis.series().forEach(function (field) {
      data.push(field.data[closestIndex])
    })
    return {
      dataIndex: closestIndex,
      data: data
    }
  }

  // parse the value at index, and return that as percentage of range
  axis.getPositionFromIndex = function (index) {
    const parser = dataUtils.getFieldIndexValueParser(state.data, state.isPrimaryAxis)
    const pos = axis.scale(parser(index))
    return pos / axis.rangeLength()
  }

  axis.rangeLength = function () {
    return Math.abs(axis.scale.range()[1] - axis.scale.range()[0])
  }

  return axis
}
