import { select } from '../d3-custom'

import { default as simpleColumn } from '../layers/simpleColumn'
import { default as simpleLine } from '../layers/simpleLine'
import { columnAnnotations } from './annotations'
import { renderChartAsTimeSeries, columnTaxonomy } from './column'
import { bottomLabel, HORIZONTAL, shouldDisplayGrid, shouldDisplayDomain } from '../utils/layout'
import { makeLayerPrototype } from '../utils/layers'
import { columnTooltip } from './tooltip'
import { default as makeEnv } from './env'
import { layoutManager, bandAxis, linearAxis, grid, format } from '../axis'
import debugEnv from '../utils/debug'
import {default as _merge} from 'lodash-es/merge'

import {
  validator,
  isDoubleSeries,
  isQuadSeries,
  isTimeSeries,
  isMultipleValues
} from '../utils/data'

// this isn't working for some reason
// import { default as _clone } from 'lodash-es/cloneDeep'

/**

  A barline chart combines a line chart and a column chart by splitting
  the series data between two charts.

*/
export default function barline (options) {
  var defaults = {
    barline: true,
    // distance tooltip box is away from point
    tooltipOffsetX: 7,
    // distance tooltip box is away from point
    tooltipOffsetY: 7,
    // tooltip box line for height estimation
    tooltipLineHeight: 30,
    // tooltip max width
    tooltipMaxWidth: 160,
    // radius of tooltip anchor
    tooltipAnchorSize: 4,
    // replaces axis inner padding for date axes
    dateAxisInnerPadding: 8,

    // configuration options for layout manager
    layoutManager: {
      yAxisInnerPadding: 0,
      xAxisExtendRight: 7
    },

    // configuration for the chart axes
    dateAxis: {},
    bandAxis: {},
    linearAxis: {}
  }
  options = _merge(defaults, options)

  options = options || {}

  const selections = []

  function barline (selection) {
    selection.each(function (chartData, i) {
      const el = select(this)
        .classed('toaster-chart-barline', true)
        .classed('bar-colors', true)
        .classed('quad-colors', isQuadSeries(chartData))

      // ensure we have a taxonomy specified on the data
      columnTaxonomy(chartData)
      const env = makeEnv(el, chartData, options)

      const isDateAxis = renderChartAsTimeSeries(el, chartData, env.options)

      const layoutConfig = {
        orientation: HORIZONTAL
      }
      Object.assign(layoutConfig, env.options.layoutManager)

      const linearAxisConfig = {
        isPrimaryAxis: false,
        domainFromZero: true
      }
      Object.assign(linearAxisConfig, env.options.linearAxis)

      const bandAxisConfig = { isPrimaryAxis: true }
      if (isDateAxis) {
        layoutConfig.yAxisInnerPadding = env.options.dateAxisInnerPadding
        bandAxisConfig.isDateAxis = true
      }
      Object.assign(bandAxisConfig, env.options.bandAxis)

      layoutConfig.secondaryAxis = linearAxis(linearAxisConfig)
      layoutConfig.primaryAxis = bandAxis(bandAxisConfig)

      const chartLayout = layoutManager(layoutConfig)
      const chart = el.selectAppend('div.chart')
      chart.selectAll('*').remove()
      chart.call(chartLayout, env)
      const line = chartLayout.draw().classed('line-container', true)
      const gridLength = env.x.rangeLength() +
        (2 * chartLayout.yAxisInnerPadding())

      // grid is now essentially a render layer
      el.classed('no-grid', !shouldDisplayGrid(env))
        .classed('no-domain', !shouldDisplayDomain(env))
        .classed('no-axis', env.options.disableGrid)
      line.selectAppend('g.grid')
        .call(grid.gridlines({ length: gridLength }), env)

      const columnOptions = {
        xScale: env.x.scale,
        yScale: env.y.scale,
        seriesData: [chartData.fields[1]],
        orientation: HORIZONTAL
      }

      const lineOptions = {
        xScale: env.x.scale,
        yScale: env.y.scale,
        seriesData: chartData.fields.slice(2),
        seriesOffset: 1,
        xOffset: env.x.scale.bandwidth() / 2,
        curve: chartData.config.curve_type,
        forceCategorical: true // drive
      }

      // [DH] an option to switch between using lines or
      // columns as the primary rendering type
      if (chartData.config && chartData.config.barline_bar_series) {
        lineOptions.seriesOffset = 0
        lineOptions.seriesData = [chartData.fields[1]]
        columnOptions.seriesOffset = 1
        columnOptions.seriesData = chartData.fields.slice(2)

        el.selectAll('.simple-legend .swatch').each(function (d, i) {
          select(this).classed('line', i === 0)
        })
      } else {
        el.selectAll('.simple-legend .swatch').each(function (d, i) {
          select(this).classed('line', i > 0)
        })
      }

      line.call(simpleColumn(columnOptions), env)
      line.call(simpleLine(lineOptions), env)

      // after drawing we place the y axis label
      // on top of everything
      format.placeVerticalAxisLabel(env)

      columnAnnotations(line, chartData.annotations, env)

      // create bottom label if needed
      chart.call(bottomLabel(), env)

      // overflow layout call
      chartLayout.overflowScale()

      // build out the default tooltip renderer
      if (!env.options.disableTooltip) {
        columnTooltip({
          chart: chart,
          area: line,
          env: env,
          barline: true
        })
      }

      selections.push({ env, el })
      debugEnv(env)
    })
  }

  return makeLayerPrototype(barline, selections, options)
}

// Validation is a static method to check if the class
// supports the chartData before building the chart
barline.validateData = function (chartData) {
  var validate = validator(chartData)

  validate('Barline chart must have more than one value.', isMultipleValues)
  validate('Barline chart requires a series of 2 fields of data.', isDoubleSeries)
  validate('Barline chart must be time series data', isTimeSeries)

  return validate
}
