import {select} from '../d3-custom'

export default function simpleLegend () {
  function simpleLegend (selection) {
    selection.each(function (chartData) {
      // TODO give this more thought and make an accessor
      var series = chartData.fields.length > 1 ? chartData.fields.slice(1) : chartData.fields

      // filter out any series item with no field names
      series = series.filter(function (s) { return (s.name && s.name !== '') })

      // allow highlight label added as a legend item
      if (chartData.config && chartData.config.highlight_label) {
        series.push({
          name: chartData.config.highlight_label,
          isHighlight: true
        })
      }

      var items = select(this)
        .selectAppend('ul.simple-legend')
        .selectAll('li')
        .data(series)

      items.exit().remove()

      items = items.merge(items.enter()
        .append('li'))

      items.selectAppend('span')
        .attr('class', function (d, i) {
          return 'swatch bg series-' + i
        })
        .classed('highlight-legend', d => d.isHighlight)

      items.selectAppend('label')
        .html(function (d) {
          return d.name
        })

      if (series.length === 0) {
        select(this).classed('hidden', true)
      }
    })
  }

  return simpleLegend
}
