const version = '0.1.0'
export { version }

import chartFactory from './factory'
import column from './column'
import columnStacked from './columnStacked'
import line from './line'
import area from './area'
import barline from './barline'
import tooltip from './tooltip'
import table from './table'
import pie from './pie'
import performanceChart from './performanceChart'
import multiples from './multiples'
import { typeChecks, editorHooks } from '../utils/reporting'

// for export
import * as d3 from '../d3-export'
import * as utils from '../utils/data'

const {
  buildTaxonomy,
  determineType,
  makeData
} = utils

export {
  chartFactory,
  column,
  columnStacked,
  line,
  area,
  barline,
  tooltip,
  table,
  performanceChart,
  pie,
  d3,
  utils
}

// map Toaster type string to constructor
const CHART_TYPES = {
  'bar': column,
  'hbar': column,
  'stackedVbar': columnStacked,
  'stackedHbar': columnStacked,
  line,
  area,
  table,
  barline,
  pie,
  performanceChart,
  multiples
}

export function factory (el, data, options) {
  options = options || {}

  // pass data through function-formerly-know-as-makegreat
  makeData(data)
  buildTaxonomy(data)

  // if no chart type
  var chartType = CHART_TYPES[data.chartType]
  if (!chartType) {
    chartType = determineType(CHART_TYPES, data, options)
  }

  // if isEditor is enabled we perform a type check on the
  // data to return messages back to the editor
  // also register editor hooks
  if (options.isEditor) {
    editorHooks(chartType, data, options)
    if (!options.suppressTypeCheck) {
      typeChecks(chartType, CHART_TYPES, data, options)
    }
  }

  const chart = chartFactory(el, chartType, data, options)

  return chart
}
